<template>
  <div class="page">
    <!-- :pagination="pagination" -->
    <div class="form-box">
      <a-table
        rowKey="num"
        :pagination="pagination"
        :columns="keywords_columns"
        :data-source="keywords_listdata"
        :scroll="{ x: gettablewidth(), y: gettableheight() }"
      >
      </a-table>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      keywords_columns: [
        {
          title: "序号",
          dataIndex: "num",
          width: 50
        },
        {
          title: "关键词",
          dataIndex: "key_word",
          width: 250
        }
      ],
      keywords_listdata: [],
      alldata: [],
      searchList: {},
      //分页
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          // this.pagination.pageSize = size;
          // this.keywords_listdata = this.alldata.slice(
          //   (this.pagination.pagenum - 1) * this.pagination.pageSize,
          //   this.pagination.pagenum * this.pagination.pageSize
          // );
          // this.pagination.current = current;
          // this.pagination.pageSize = pageSize;
          // this.searchList.pageNum = current;
          // this.searchList.pageSize = pageSize;
          // var params = JSON.parse(JSON.stringify(this.searchList));
          // params.pageNum = 1;
          // params.pageSize = pageSize;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变页数
        onChange: (current, size) => {
          //console.log(current);
          this.pagination.current = current;
          // this.keywords_listdata = this.alldata.slice(
          //   (this.pagination.current - 1) * this.pagination.pageSize,
          //   this.pagination.current * this.pagination.pageSize
          // );
          // this.pagination.current = current;
          // this.pagination.pageSize = size;
          // this.searchList.pageNum = current;
          // this.searchList.pageSize = size;
          // var params = JSON.parse(JSON.stringify(this.searchList));
          // params.pageNum = current;
          // params.pageSize = size;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          //   this.getList()
        } // 点击页码事件
      }
    };
  },
  methods: {
    // onShowSizeChange(current) {
    //   this.pagination.current = current;
    //   this.keywords_listdata = this.keywords_listdata.slice(
    //     (this.pagination.current - 1) * this.pagination.pageSize,
    //     this.pagination.current * this.pagination.pageSize
    //   );
    // },
    // 表格宽度
    gettablewidth() {
      let w = 0;
      for (let i of this.keywords_columns) {
        w = w + i.width + 20;
      }
      return w;
    },
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 300;
    },
    getcommonlist() {
      this.$keywords_http
        .post("/javaTalkKeyWord/newKeyWord/getIndustryFilterKeyWord")
        .then(res => {
          console.log(res);
          if (res.data.code == 101) {
            this.keywords_listdata = JSON.parse(res.data.result);
            this.keywords_listdata.forEach((item, index) => {
              item.num = index + 1;
            });
            this.alldata = this.keywords_listdata;
          }
        });
    }
  },
  mounted() {}
};
</script>

<style scoped lang="scss">
/deep/ .form-box .form-box .ant-table-body::-webkit-scrollbar {
  width: 15px;
}
</style>



