<template>
  <div class="Industrylist clear">
    <div class="Industryclassify_list">
      <div
        class="onelevel_industrybox clear"
        v-for="onelevel in onelevel_industrylist"
        :key="onelevel.type_id"
      >
        <div class="onelevel_industryitem clear" @click="dropdown_twolevelindustry(onelevel)">
          <div
            v-show="onelevel.childrenList.length > 0 && onelevel.dropdown == true"
            class="onelevel_icon down"
          ></div>
          <div
            v-show="onelevel.childrenList.length > 0 && onelevel.dropdown == false"
            class="onelevel_icon right"
          ></div>
          <div v-show="onelevel.childrenList.length == 0" class="onelevelprefer_icon"></div>
          <div class="onelevel_title" :title="onelevel.type_name">{{onelevel.type_name}}</div>
          <div class="onelevel_actionicon" @click.stop="open_editindustry('onelevel',onelevel)">
            <img src="../../../assets/qapool/edit_icon.svg" style="width:16px;height:16px" alt />
          </div>
          <div class="onelevel_actionicon" @click.stop="open_tipmodal(onelevel,'oneindustry')">
            <img
              src="../../../assets/qapool/delete_icon_blue.svg"
              style="width:16px;height:16px"
              alt
            />
          </div>
        </div>
        <div
          class="twolevel_industryitem clear"
          :class="currtwolevel_industryid == twolevel.type_id ? 'active' :''"
          v-show="onelevel.dropdown == true"
          v-for="twolevel in onelevel.childrenList"
          :key="twolevel.type_id"
          @click="getquestionclassify_list(twolevel)"
        >
          <div v-show="twolevel.status == 1" class="prefer_icon"></div>
          <div v-show="twolevel.status == 2" class="shelves_icon"></div>
          <div class="twolevel_title" :title="twolevel.type_name">{{twolevel.type_name}}</div>
          <div
            class="twolevel_prefer"
            @click.stop="modal_debounce(preferorshelves,1000,twolevel)"
          >{{twolevel.status == 1 ? '下架' : '上架'}}</div>
          <div class="twolevel_actionicon" @click.stop="open_editindustry('twolevel',twolevel)">
            <img src="../../../assets/qapool/edit_icon.svg" style="width:16px;height:16px" alt />
          </div>
          <div class="twolevel_actionicon" @click.stop="open_tipmodal(twolevel,'twoindustry')">
            <img
              src="../../../assets/qapool/delete_icon_blue.svg"
              style="width:16px;height:16px"
              alt
            />
          </div>
        </div>
      </div>
    </div>
    <div class="qalist">
      <QAlist
        ref="qalist_ref"
        :type="'edit'"
        :curtwolevel_industry="curtwolevel_industry"
        :question_classifylist_edit="curquestionclassify_list"
        @editclassify="open_editindustry"
        @refreshclassify="getquestionclassify_list"
        @gettransferdata="gettransferdata"
      ></QAlist>
    </div>

    <!-- 修改行业 -->
    <a-modal
      v-model="editindustrymodal.show"
      :title="editindustrymodal.title"
      :footer="null"
      @cancel="close_editindustrymodal"
      :width="380"
    >
      <div class="modal_content">
        <div class="modal_body_item clear positionrelative">
          <div
            class="item_title"
          >{{editindustrymodal.type == 'onelevel' ? '一级行业：': editindustrymodal.type == 'twolevel' ? '二级行业：' : '分类名称：'}}</div>
          <a-input
            v-model="editindustrymodal.industry_name"
            :placeholder="editindustrymodal.type == 'onelevel' ? '一级行业': editindustrymodal.type == 'twolevel' ? '二级行业' : '分类名称：'"
            style="width:270px;"
            @focus="editindustrymodal.industry_name_nullerr = false;"
            :class="editindustrymodal.industry_name_nullerr ? 'null_err' :''"
          />
          <div
            class="qapoolTip"
            :class="[{'qapoolTip-show':editindustrymodal.industry_name_nullerr}]"
            style="margin-left: 65px;"
          >
            <p class="qapoolTip-center" v-html="qapoolTip"></p>
          </div>
        </div>
        <div class="modal_footer">
          <a-button type="primary" @click="modal_debounce(editindustry,1000)">保持更改</a-button>
          <a-button style="margin-left:15px;" @click="close_editindustrymodal">取消</a-button>
        </div>
      </div>
    </a-modal>

    <!-- 删除行业提示 -->
    <Tipmodal
      :show="tipmodal.show"
      :title="tipmodal.title"
      :type="tipmodal.type"
      :content_tip="tipmodal.content_tip"
      @getshow="close_tipmodal"
      @ok="modal_debounce(delete_industry,1000,tipmodal.info)"
    ></Tipmodal>
  </div>
</template>

<script>
import Tipmodal from "@/components/common/tip_type.vue";
import QAlist from "./QA_list";
export default {
  components: {
    QAlist,
    Tipmodal
  },
  props: ["industrylist"],
  data() {
    return {
      onelevel_industrylist: [],
      currtwolevel_industryid: "",
      curtwolevel_industry: {},
      curquestionclassify_list: [],
      qapoolTip: "",
      // 修改行业
      editindustrymodal: {
        industry_name: "",
        title: "修改行业",
        industry_name_nullerr: false,
        type: "onelevel",
        show: false,
        info: {}
      },
      tipmodal: {
        show: false,
        ok: "questionclassify",
        info: {},
        title: "温馨提示",
        type: "warning",
        content_tip: "删除后不可恢复，你确定要删除吗？"
      }
    };
  },
  methods: {
    // 打开提示对话框
    open_tipmodal(item, ok) {
      this.tipmodal.show = true;
      this.tipmodal.info = item;
      this.tipmodal.ok = ok;
      if (ok == "oneindustry") {
        this.tipmodal.content_tip =
          "删除一级行业后，下面的二级行业及分类都将被删除且不可恢复，你确定要删除吗？";
      } else {
        this.tipmodal.content_tip =
          "删除二级行业后，下面的分类及问题都将被删除且不可恢复，你确定要删除吗？";
      }
    },
    // 关闭提示对话框
    close_tipmodal(value) {
      this.tipmodal.show = value;
    },
    // 下拉二级行业
    dropdown_twolevelindustry(onelevelitem) {
      for (let i = 0; i < this.onelevel_industrylist.length; i++) {
        if (this.onelevel_industrylist[i].type_id == onelevelitem.type_id) {
          if (onelevelitem.dropdown == false) {
            onelevelitem.dropdown = !onelevelitem.dropdown;
            this.$set(this.onelevel_industrylist, i, onelevelitem);
          } else {
            onelevelitem.dropdown = !onelevelitem.dropdown;
            this.$set(this.onelevel_industrylist, i, onelevelitem);
          }
        }
      }
    },
    // 点击二级行业获取分类列表
    getquestionclassify_list(twolevel) {
      // if(twolevel.type_id == this.curtwolevel_industry.type_id){
      //   return;
      // }
      this.curtwolevel_industry = twolevel;
      this.currtwolevel_industryid = twolevel.type_id;
      this.$emit("getcur_industry", twolevel);
      let data = {
        type_parent: this.currtwolevel_industryid
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/selectClassification", data)
        .then(res => {
          if (res.data.code == 101) {
            this.curquestionclassify_list = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          this.$emit("getclassifylist", this.curquestionclassify_list);
        });
    },
    // 打开修改行业,分类对话框
    open_editindustry(type, info) {
      this.editindustrymodal = {
        industry_name: info.type_name,
        industry_name_nullerr: false,
        type: type,
        title: type == "classify" ? "修改问题类别名称" : "修改行业",
        show: true,
        info: info
      };
    },
    // 关闭修改行业,分类对话框
    close_editindustrymodal() {
      this.editindustrymodal = {
        industry_name: "",
        title: "",
        industry_name_nullerr: false,
        show: false
      };
    },
    // 修改行业,分类名称
    editindustry() {
      if (this.replace_null(this.editindustrymodal.industry_name) == "") {
        this.editindustrymodal.industry_name_nullerr = true;
        if (this.editindustrymodal.type == "classify") {
          this.qapoolTip = "请输入分类名称";
        } else {
          this.qapoolTip = "请输入行业名称";
        }
        return;
      } else {
        if (
          this.replace_null(this.editindustrymodal.industry_name) ==
          this.replace_null(this.editindustrymodal.info.type_name)
        ) {
          this.close_editindustrymodal();
          return;
        }
        let data = {
          type_id: this.editindustrymodal.info.type_id,
          type_name: this.editindustrymodal.industry_name
        };
        this.$qapool_http
          .post("/newknowledgebase/tempBase/updateIndustryName", data)
          .then(res => {
            if (res.data.code == 101) {
              this.$message.success("修改成功");
              if (this.editindustrymodal.type == "classify") {
                this.getquestionclassify_list(this.curtwolevel_industry);
              } else {
                this.editindustrymodal.info.type_name = this.editindustrymodal.industry_name;
                // this.$emit("refreshindustry");
              }
              this.close_editindustrymodal();
            } else if (res.data.code == 230) {
              this.editindustrymodal.industry_name_nullerr = true;
              this.qapoolTip = "名称已存在";
            }
          })
          .catch(err => {
            this.$message.error("系统错误");
            console.log(err);
          })
          .finally(() => {});
      }
    },
    // 删除一二级行业
    delete_industry(info) {
      let data = {
        type_id: info.type_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/deleteIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("删除成功");
            this.$emit("refreshindustry");
            // 删除的二级行业为当前选中二级行业则清除分类和问题
            if (info.type_id == this.currtwolevel_industryid) {
              this.curtwolevel_industry = {};
              this.curquestionclassify_list = [];
            }
            // 删除的一级行业为当前选中二级行业的父级则清除分类和问题
            for (let key of this.onelevel_industrylist) {
              if (key.type_id == info.type_id) {
                for (let item of key.childrenList) {
                  if (item.type_id == this.currtwolevel_industryid) {
                    this.curtwolevel_industry = {};
                    this.curquestionclassify_list = [];
                  }
                }
              }
            }
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          this.close_tipmodal(false);
        });
    },
    // 上架 下架
    preferorshelves(twolevel) {
      let data = {};
      if (twolevel.status == 1) {
        data = {
          type_id: twolevel.type_id,
          cmd: "lower"
        };
      } else if (twolevel.status == 2) {
        data = {
          type_id: twolevel.type_id,
          cmd: "upper"
        };
      }
      this.$qapool_http
        .post("/newknowledgebase/tempBase/upperAndLowerShelves", data)
        .then(res => {
          if (res.data.code == 101) {
            if (twolevel.status == 1) {
              this.$message.success("已下架");
              twolevel.status = 2;
            } else if (twolevel.status == 2) {
              this.$message.success("上架成功");
              twolevel.status = 1;
            }
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          //   this.$emit("refreshindustry");
        });
    },
    // 批量转移数据
    gettransferdata(data) {
      this.$emit("gettransferdata", data);
    },
    // 清空问题答案列表勾选
    clearquestionchecked() {
      this.$refs.qalist_ref.questionselectedKeys = [];
    }
  },
  watch: {
    industrylist(data) {
      this.onelevel_industrylist = JSON.parse(JSON.stringify(data));
      for (let item of this.onelevel_industrylist) {
        item.dropdown = false;
      }
      this.dropdown_twolevelindustry(this.onelevel_industrylist[0]);
      if (
        this.onelevel_industrylist[0].childrenList &&
        this.onelevel_industrylist[0].childrenList.length !== 0
      ) {
        this.getquestionclassify_list(
          this.onelevel_industrylist[0].childrenList[0]
        );
      }
    }
  }
};
</script>
<style lang="scss">
.Industrylist {
  width: 100%;
  height: 100%;
  .Industryclassify_list {
    float: left;
    width: 240px;
    height: 100%;
    // max-height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    border-top: 1px solid #d8dfea;
    border-left: 1px solid #d8dfea;
    border-bottom: 1px solid #d8dfea;
    .onelevel_industrybox {
      width: 100%;
      font-size: 12px;
      .onelevel_industryitem {
        width: 100%;
        height: 40px;
        line-height: 40px;
        &:hover {
          background-color: #e7f4fd;
          .onelevel_actionicon {
            display: block;
          }
        }
        .onelevel_icon {
          float: left;
          &.down {
            width: 0;
            height: 0;
            margin: 15px 5px 0 8px;
            border-width: 10px 6px 0;
            border-style: solid;
            border-color: #28334b transparent;
          }
          &.right {
            width: 0;
            height: 0;
            margin: 14px 5px 0 10px;
            border-width: 6px 0 6px 10px;
            border-style: solid;
            border-color: transparent #28334b;
          }
        }
        .onelevel_title {
          float: left;
          width: 130px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .onelevel_actionicon {
          display: none;
          width: 30px;
          height: 100%;
          padding: 0 7px;
          cursor: pointer;
          float: left;
        }
      }
      .twolevel_industryitem {
        float: left;
        width: 100%;
        height: 40px;
        padding: 0 0 0 20px;
        line-height: 40px;
        &:hover {
          background-color: #e7f4fd;
          .twolevel_actionicon {
            display: block;
          }
        }
        &.active {
          background-color: #eaebed;
        }
        .twolevel_title {
          float: left;
          width: 75px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .twolevel_prefer {
          float: left;
          width: 30px;
          height: 100%;
          color: #218bfc;
          cursor: pointer;
        }
        .twolevel_actionicon {
          display: none;
          width: 30px;
          height: 100%;
          padding: 0 7px;
          cursor: pointer;
          float: left;
        }
      }
      .onelevelprefer_icon {
        float: left;
        width: 8px;
        height: 8px;
        margin: 16px 7px 16px 10px;
        border-radius: 50%;
        background: #23ad3e;
      }
      .prefer_icon {
        float: left;
        width: 8px;
        height: 8px;
        margin: 16px 10px 16px 10px;
        border-radius: 50%;
        background: #23ad3e;
      }
      .shelves_icon {
        float: left;
        width: 8px;
        height: 8px;
        margin: 16px 10px 16px 10px;
        border-radius: 50%;
        background: #466080;
      }
    }
  }
  .qalist {
    float: left;
    width: calc(100% - 240px);
    height: 100%;
  }
}
</style>


