<template>
  <div class="keywords">
    <div class="tab_pagebox clear">
      <div class="filiter_box">
        <!-- <a-month-picker
          class="filiter_item"
          style="width:200px;"
          :allowClear="false"
          @change="timeChange"
          v-model="select_time_show"
          placeholder="选择日期"
        /> -->
        <a-select
          class="filiter_item"
          style="width: 150px"
          placeholder="一级行业"
          v-model="onelevel_industry"
          @change="onelevel_industryChange"
        >
          <a-select-option
            v-for="item in onelevel_industrylist"
            :key="item.id"
            :value="item.id"
            :title="item.industry_name"
            >{{ item.industry_name }}</a-select-option
          >
        </a-select>
        <a-select
          class="filiter_item"
          style="width: 120px"
          placeholder="二级行业"
          v-model="twolevel_industry"
          :disabled="twolevelindustry_disabled"
          @change="twolevel_industryChange"
        >
          <a-select-option
            v-for="item in twolevel_industrylist"
            :key="item.id"
            :value="item.id"
            :title="item.industry_name"
            >{{ item.industry_name }}</a-select-option
          >
        </a-select>

        <!-- <a-button
          class="filiter_item"
          @click="modal_debounce(addtoindustrypool, 1000)"
          :disabled="selectedRowKeys.length == 0"
          >添加到行业关键词</a-button
        > -->
        <a-button
          class="filiter_item"
          @click="modal_debounce(addtocommonpool, 1000)"
          :disabled="selectedRowKeys.length == 0"
          >添加到行业过滤词</a-button
        >
        <span style="margin-left:10px" v-show="selectedRowKeys.length > 0">
          已选择
          <span style="color:#F44024">{{ selectedRowKeys.length }}</span>
        </span>
      </div>

      <!-- 列表展示 -->
      <!-- :pagination="pagination" -->
      <div class="content">
        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              rowKey="id"
              :row-selection="{
                selectedRowKeys: selectedRowKeys,
                onChange: onSelectChange
              }"
              :pagination="pagination"
              :columns="keywords_columns"
              :data-source="keywords_listdata"
              :scroll="{ x: gettablewidth(), y: gettableheight() }"
            >
              <div
                slot="one_industry"
                slot-scope="text"
                :title="text"
                class="texthidden"
                style="width:99px"
              >
                {{ text || "-" }}
              </div>
              <div
                slot="two_industry"
                slot-scope="text"
                :title="text"
                class="texthidden"
                style="width:99px"
              >
                {{ text || "-" }}
              </div>
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  props: {
    alloneindustrylist: {
      type: Array
    },
    alltwoindustrylist: {
      type: Array
    }
  },
  components: {},
  data() {
    return {
      table_spinning: false, //loadding图显示
      select_time_show: "", //日期双向绑定
      onelevel_industry: [], //一级行业
      onelevel_industrylist: [], //一级行业列表
      twolevel_industry: [], //二级行业双向绑定
      twolevelindustry_disabled: true,
      twolevel_industrylist: [], //二级行业列表
      selectedRowKeys: [],
      keywords_columns: [
        { title: "序号", dataIndex: "num", width: 50 },
        { title: "关键词", dataIndex: "key_word", width: 150 },
        {
          title: "一级行业",
          dataIndex: "one_industry",
          width: 120,
          scopedSlots: { customRender: "one_industry" }
        },
        {
          title: "二级行业",
          dataIndex: "two_industry",
          width: 120,
          scopedSlots: { customRender: "two_industry" }
        },
        {
          title: "有线索中的总量",
          dataIndex: "clew_num",
          width: 120
        },
        {
          title: "总量",
          dataIndex: "total_num",
          width: 120
        },
        {
          title: "占比",
          dataIndex: "proportion",
          width: 120
        }
      ],
      keywords_listdata: [],
      searchList: {},
      //分页
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          // this.pagination.current = current;
          // this.pagination.pageSize = pageSize;
          // this.searchList.pageNum = current;
          // this.searchList.pageSize = pageSize;
          // var params = JSON.parse(JSON.stringify(this.searchList));
          // params.pageNum = 1;
          // params.pageSize = pageSize;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          this.pagination.current = current;
          // this.pagination.pageSize = size;
          // this.searchList.pageNum = current;
          // this.searchList.pageSize = size;
          // var params = JSON.parse(JSON.stringify(this.searchList));
          // params.pageNum = current;
          // params.pageSize = size;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          //   this.getList()
        } // 点击页码事件
      }
    };
  },
  methods: {
    // 表格宽度
    gettablewidth() {
      let w = 0;
      for (let i of this.keywords_columns) {
        w = w + i.width + 20;
      }
      return w;
    },
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 300;
    },
    //改变日期
    timeChange(e) {
      this.select_time_show = this.timechangestart(e._d);
      console.log(this.select_time_show);
      this.getkeywordslist();
    },
    //改变一级行业列表
    onelevel_industryChange(value) {
      this.twolevelindustry_disabled = true;
      this.twolevel_industry = undefined;
      this.getindustry_two(value);
    },
    //获取数据
    getkeywordslist() {
      var data = {
        industry:
          this.twolevel_industry !== undefined
            ? this.onelevel_industry + "," + this.twolevel_industry
            : undefined,
        //add_time: this.select_time_show.replace("-", ""),
        word_type: 1
      };
      console.log(data);
      this.table_spinning = true;
      this.$keywords_http
        .post("/javaTalkKeyWord/newKeyWord/getIndustryKeyWord", data)
        .then(res => {
          console.log(res);
          if (res.data.code == 101) {
            let response = JSON.parse(res.data.result);
            this.keywords_listdata = response;
            this.table_spinning = false;
            this.keywords_listdata.forEach((item, index) => {
              item.num = index + 1;
              let industry = [];
              if (item.industry) {
                industry = item.industry.split(",");
                for (let i of this.onelevel_industrylist) {
                  if (i.id == industry[0]) {
                    item.one_industry = i.industry_name;
                  }
                }
                for (let i of this.alltwolevel_industrylist) {
                  if (i.id == industry[1]) {
                    item.two_industry = i.industry_name;
                  }
                }
                if (!item.one_industry) {
                  item.one_industry = "-";
                }
                if (!item.two_industry) {
                  item.two_industry = "-";
                }
              }
            });
          } else {
            this.keywords_listdata = [];
            this.table_spinning = false;
          }
        });
      // .catch(err => {
      //   this.table_spinning = true;
      //   this.$message.error("系统错误");
      //   console.log(err);
      // });
    },
    //改变二级行业列表
    twolevel_industryChange() {
      this.getkeywordslist();
    },
    //添加到行业关键词
    addtoindustrypool() {
      console.log(this.selectedRowKeys.join(","));
      var data = {
        ids: this.selectedRowKeys.join(",")
      };
      this.$keywords_http
        .post("/javaTalkKeyWord/newKeyWord/addToIndustryKeyWord", data)
        .then(res => {
          console.log(res);
          if (res.data.code == 101) {
            this.$message.success("操作成功");
            this.getkeywordslist();
          } else {
            this.$message.error("系统错误");
          }
        });
    },
    //添加到行业过滤词
    addtocommonpool() {
      var data = {
        ids: this.selectedRowKeys.join(",")
      };
      this.$keywords_http
        .post("/javaTalkKeyWord/newKeyWord/addToIndustryFilterKeyWord", data)
        .then(res => {
          console.log(res);
          if (res.data.code == 101) {
            this.$message.success("操作成功");
            this.getkeywordslist();
          } else {
            this.$message.error("系统错误");
          }
        });
    },
    //表格勾选
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
      console.log(this.selectedRowKeys);
    },

    // 获取一级行业
    getallindustry_one() {
      this.$keywords_http
        .get("/javaTalkKeyWord/keyWord/selectPrimaryIndustry")
        .then(res => {
          if (res.data.code == 101) {
            this.onelevel_industrylist = JSON.parse(res.data.result);
            //this.onelevel_industry = this.onelevel_industrylist[0].id;
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 获取一级行业下二级行业
    getindustry_two(id) {
      this.table_spinning = true;
      let data = {
        type: "one",
        id: id
      };
      this.$keywords_http
        .post("/javaTalkKeyWord/keyWord/selectSecondaryIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.twolevelindustry_disabled = false;
            this.twolevel_industrylist = JSON.parse(res.data.result);
            this.twolevel_industry = this.twolevel_industrylist[0].id;
            this.table_spinning = false;
            // console.log(this.onelevel_industry);
            // console.log(this.twolevel_industry);
          }
        })
        .catch(err => {
          this.table_spinning = false;
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          //this.getkeywordslist();
        });
    },
    //补零操作
    test(str) {
      if (str < 10) {
        return "0" + str;
      } else {
        return str;
      }
    },
    //格式转化
    timechangestart(str) {
      return str.getFullYear() + "-" + this.test(str.getMonth() + 1);
    }
  },
  watch: {
    alloneindustrylist: {
      handler(newVal, oldVal) {
        this.onelevel_industrylist = newVal;
        this.onelevel_industry = newVal[0].id;
        this.getindustry_two(newVal[0].id);
      }
    },
    alltwoindustrylist: {
      handler(newVal, oldVal) {
        this.alltwolevel_industrylist = newVal;
      }
    }
  },
  mounted() {
    this.select_time_show = this.timechangestart(new Date());
    //this.getallindustry_one();
  }
};
</script>

<style scoped lang="scss">
.keywords {
  width: 100%;
  height: 100%;
  .filiter_box {
    .filiter_item {
      margin-right: 10px;
      margin-bottom: 10px;
    }
  }
  .content {
    width: 100%;
    .table_pagination {
      margin: 15px 0;
      text-align: right;
    }
  }
}
.page_item {
  margin-right: 10px;
  float: left;
  cursor: pointer;
  width: 80px;
  height: 100%;
  border: 1px solid #ced5e0;
  line-height: 32px;
  text-align: center;
  border-radius: 2px 0px 0px 2px;
  &:hover {
    border: 1px solid #3b99fc;
    color: #3b99fc;
  }
  &.active {
    border: 1px solid #3b99fc;
    color: #3b99fc;
  }
}
.tab_pagebox {
  margin-bottom: 10px;
}
/deep/ .form-box .ant-table-body::-webkit-scrollbar {
  width: 15px;
}
/deep/
  .ant-table-fixed-header
  .ant-table-scroll
  .ant-table-header::-webkit-scrollbar {
  width: 15px;
}
</style>



