<template>
  <div class="interfaceList">
    <div class="interface-head">
      <div
        class="interface-tabs_head" 
        @click="$router.push({ path: '/packageMonthReport' })"
      >套餐分类业绩月报表</div>
      <div class="interface-tabs_head curactive">套餐分类业绩年报表</div>
    </div>
    <div v-if="hasPermissions == true" style="width:100%;height:calc(100% - 50px)">
      <div class="page-box">
        <div class="clear search-condition-box">
          <div class="search-condition-box-item">
            <a-date-picker
              @panelChange="timeChange"
              @openChange="timeopen"
              :open="datepickeropen"
              v-model="select_time_show"
              mode="year"
              format="YYYY"
              placeholder="选择年份"
            />
            
          </div>
          <div class="search-condition-box-item" style="width:270px">
            <!-- <a-select v-model="searchList.saleBuName" style="width: 120px;display: inline-block;" allowClear placeholder="销售 BU">
            <a-select-option
              v-for="(item,index)  in  salebulist"
              :key="index"
              :value="item.ascription_BU"
              :title="item.ascription_BU"
              >{{item.ascription_BU}}</a-select-option>
            </a-select>-->
            <a-select
              v-model="searchList.productKey"
              style="width: 120px;display: inline-block;margin-left: 6px;"
              allowClear
              placeholder="产品"
              @change="mealChange"
            >
              <a-select-option
                v-for="(item,index)  in  mealList"
                :key="index"
                :value="item.productKey"
                :title="item.productName"
              >{{item.productName}}</a-select-option>
            </a-select>
            <a-select
              v-model="searchList.mealSort"
              style="width: 120px;display: inline-block;margin-left: 6px;"
              allowClear
              placeholder="套餐分类"
            >
              <a-select-option
                v-for="(item,index)  in  mealesList"
                :key="index"
                :value="item.mealSort"
                :title="item.mealSortName"
              >{{item.mealSortName}}</a-select-option>
            </a-select>
          </div>
          <div class="search-condition-box-item" style="width: auto">
            <a-button @click="screenClear">取消筛选</a-button>
            <a-button type="primary" @click="screenBtn">筛选</a-button>
          </div>
        </div>
        <div class="package-price">
          <div class="package-price-line clear">
            【总销售金额】资金：
            <span>{{this.all_money_status.payMoneyAll}}</span>
            元；新优惠券：
            <span>{{this.all_money_status.payNewCouponsAll}}</span>
            元；老优惠券：
            <span>{{this.all_money_status.payOldCouponsAll}}</span>
            元；
          </div>
          <div class="package-price-line clear">
            【总退款金额】资金：
            <span>{{this.all_money_status.backMoneyAll}}</span>
            元；新优惠券：
            <span>{{this.all_money_status.backNewCouponsAll}}</span>
            元；老优惠券：
            <span>{{this.all_money_status.backOldCouponsAll}}</span>
            元；
          </div>
          <div class="package-price-line clear">
            【总实际金额】资金：
            <span>{{this.all_money_status.realMoneyAll}}</span>
            元；新优惠券：
            <span>{{this.all_money_status.realNewCouponsAll}}</span>
            元；老优惠券：
            <span>{{this.all_money_status.realOldCouponsAll}}</span>
            元；
          </div>
        </div>
        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              :columns="columns"
              :locale="locale"
              :dataSource="data"
              bordered
              :pagination="pagination"
              :scroll="{ x:gettablewidth(), y: gettableheight() }"
            >
              <a slot="operation" slot-scope="text, record">
                <router-link
                  tag="a"
                  target="_blank"
                  :to="{name:'orderInformation',params:{catId:0},query:{mealSort:record.mealSort,productKey:record.productKey,selectTime:record.statisticsTime}}"
                >订单明细</router-link>
              </a>
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>
    <div v-else class="no-permission" style="height:calc(100% - 50px)">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  name: "packageMonthReport",
  data() {
    return {
      hasPermissions: false,
      searchList: {
        statisticsTime: null,
        productKey: undefined,
        mealSort: undefined,
        saleBuName: undefined,
        pageNum: 1,
        pageSize: 20
      },
      meal: undefined,
      all_money_status: {},
      mealList: [],
      all_mealslist: [],
      mealesList: [],
      salebulist: [],
      mealType: true,
      columns: [
        { title: "序号", dataIndex: "num", key: "num", show: true, width: 50 },
        // {
        //   title: "月份",
        //   dataIndex: "statisticsTime",
        //   key: "statisticsTime",
        //   width: 120
        // },
        {
          title: "产品",
          dataIndex: "productName",
          key: "productName",
          width: 150
        },
        {
          title: "套餐分类",
          dataIndex: "mealSortName",
          key: "mealSortName",
          width: 150
        },
        {
          title: "客户量",
          dataIndex: "customerNum",
          key: "customerNum",
          width: 120
        },
        {
          title: "销售金额",
          width: 480,
          children: [
            {
              title: "资金",
              dataIndex: "payMoney",
              key: "payMoney",
              width: 120
            },
            {
              title: "新优惠券",
              dataIndex: "payNewCoupons",
              key: "payNewCoupons",
              width: 120
            },
            {
              title: "老优惠券",
              dataIndex: "payOldCoupons",
              key: "payOldCoupons",
              width: 120
            },
            {
              title: "订单数量",
              dataIndex: "payOrderNum",
              key: "payOrderNum",
              width: 120
            }
          ]
        },
        {
          title: "退款金额",
          width: 480,
          children: [
            {
              title: "资金",
              dataIndex: "backMoney",
              key: "backMoney",
              width: 120
            },
            {
              title: "新优惠券",
              dataIndex: "backNewCoupons",
              key: "backNewCoupons",
              width: 120
            },
            {
              title: "老优惠券",
              dataIndex: "backOldCoupons",
              key: "backOldCoupons",
              width: 120
            },
            {
              title: "订单数量",
              dataIndex: "backOrderNum",
              key: "backOrderNum",
              width: 120
            }
          ]
        },
        {
          title: "实际金额",
          children: [
            {
              title: "资金",
              dataIndex: "realMoney",
              key: "realMoney",
              width: 120
            },
            {
              title: "新优惠券",
              dataIndex: "realNewCoupons",
              key: "realNewCoupons",
              width: 120
            },
            {
              title: "老优惠券",
              dataIndex: "realOldCoupons",
              key: "realOldCoupons",
              width: 120
            },
            {
              title: "订单数量",
              dataIndex: "realOrderNum",
              key: "realOrderNum",
              width: 120
            }
          ],
          width: 480
        },
        {
          title: "平均购买数量",
          align: "center",
          dataIndex: "averagePurchaseNum",
          key: "averagePurchaseNum",
          width: 120
        },
        {
          title: "客单价",
          dataIndex: "averagePurchasePrice",
          key: "averagePurchasePrice",
          width: 120
        },
        {
          title: "进货成本",
          dataIndex: "purchaseCost",
          key: "purchaseCost",
          width: 150
        },
        {
          title: "毛利润",
          dataIndex: "grossProfit",
          key: "grossProfit",
          width: 150
        },
        {
          title: "结算金额",
          dataIndex: "settlementAmount",
          key: "settlementAmount",
          width: 120
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          scopedSlots: { customRender: "operation" },
          width: 120
        }
      ],
      data: [],
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1,
        defaultPageSize: 20,
        total: "",
        showTotal: total => `共 ${total} 条数据`,
        showSizeChanger: true,
        pageSizeOptions: ["20", "40", "100"],
        onShowSizeChange: (current, pageSize) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          this.paramsCheck(params);
          this.getmonthReport(params);
          // this.getList()  //显示列表的接口名称
        },
        onChange: (current, size) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          this.pagination.current = current;
          this.pagination.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.paramsCheck(params);
          this.getmonthReport(params);
          // this.getList()
        } // 点击页码事件
      },
      hejingo(){
        var myChart = echarts.init(document.getElementById('main'));
        myChart.setOption({
            title: {
                text: '异步数据加载示例'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            xAxis: {
                data: []
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: []
            }]
        });

        // 异步加载数据
        $.get('data.json').done(function (data) {
            // 填入数据
            myChart.setOption({
                xAxis: {
                    data: data.categories
                },
                series: [{
                    // 根据名字对应到相应的系列
                    name: '销量',
                    data: data.data
                }]
            });
        });
      },
      
      hejing(params){
        this.table_spinning = true;
        getData.getchannel(params).then(res =>{
          if(res.code == 200){
            res.data.list.forEach(item =>{
              item.opentimes = (item.opentimes * 100).toFixed(2) + '%'
            })
            
          }
        })
      },
      hejingotwo(){
        var myChart = echarts.init(document.getElementById('main'));
        myChart.setOption({
          title: {
              text: '异步数据加载'
          },
          tooltip: {},
          legend: {
            data: ['销量']
          },
          xAxis: {
              data: []
          },
          yAxis: {},
          series: [{
            name: '销量',
            type: 'bar',
            data: []
          }]
        });

        //异步加载数据
        $.get('data.json').done(function (data){
          myChart.setOption({
            xAxis: {
              data: data.categories
            },
            series: [{
              name: '销量',
              data: data.data
            }]
          })
        })
      },
      hejingothree(){
        var myChart = echarts .init(document.getElementById('main'));
        myChart.setOption({
          title: {
            text: '异步数据加载'
          }
        })
      },
      locale: {
        // emptyText:'暂无数据'
      },
      defaultValMonthOpera: undefined,
      select_time_show: undefined,
      datepickeropen: false,
      table_spinning: false
    };
  },
  created() {},
  mounted() {
    document.title = "套餐分类业绩年报表";
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
      getData.getproductmealInfo().then(res => {
        this.mealList = res.data.mealSortList;
        let all_mealslist = [];
        for (let item of this.mealList) {
          all_mealslist = all_mealslist.concat(item.mealList);
        }

        this.all_mealslist = all_mealslist;
        this.mealesList = this.all_mealslist;
        this.mealesList = this.splice(this.mealesList);
        this.salebulist = res.data.saleBuMap;
      });
      this.addDate();
      this.getmonthReport(this.searchList);
    }
  },
  watch: {
    "searchList.productKey": function(data) {
      if (data == undefined) {
        this.mealesList = this.all_mealslist;
      }
    }
  },
  methods: {
    //筛选数组
    splice(List){
      return List.filter(item => item.mealSortName != '')
    },
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 330;
    },
    getmonthReport(params) {
      this.table_spinning = true;
      getData.getyearReport(params).then(res => {
        this.table_spinning = false;
        this.pagination.total = res.data.total;
        this.data = res.data.list;
        this.all_money_status = res.data;
        for (var i = 0; i < this.data.length; i++) {
          this.data[i].num = i + 1;
        }
      });
    },
    addDate() {
      var nowDate = new Date();
      let date = {
        year: nowDate.getFullYear(),
        month: nowDate.getMonth() + 1,
        date: nowDate.getDate()
      };
      // var systemDate = date.year + '-' + 0 + date.month
      // if(date.month<10){
      //   systemDate = date.year + '-' + 0 + date.month
      // }else{
      //   systemDate = date.year + '-' + date.month
      // }
      let systemDate = date.year;
      this.searchList.statisticsTime = systemDate + "";
      this.defaultValMonthOpera = systemDate + "";
      this.select_time_show = moment(systemDate, "YYYY");
    },
    timeChange(date) {
      this.select_time_show = date;
      this.datepickeropen = false;
      let dateString = date._d.getFullYear();
      this.searchList.statisticsTime = dateString + "";
    },
    timeopen(status) {
      if (status) {
        this.datepickeropen = true;
      } else {
        this.datepickeropen = false;
      }
    },
    mealChange(v) {
      this.searchList.mealSort = undefined;
      if (v == undefined) {
        // this.mealType = true;
        this.mealesList = this.all_mealslist;
        this.mealesList = this.splice(this.mealesList);
      }
      for (let item of this.mealList) {
        if (v == item.productKey) {
          this.mealesList = item.mealList;
          this.mealesList = this.splice(this.mealesList);
          this.mealType = false;
          
        }
      }
    },
    screenBtn() {
      // if (
      //   this.searchList.productKey !== undefined &&
      //   this.searchList.mealSort == null
      // ) {
      //   this.$message.error("请选择套餐分类");
      //   return;
      // }
      var params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      this.getmonthReport(params);
    },
    paramsCheck(params) {
      if (params.statisticsTime == undefined) {
        params.statisticsTime = null;
      }
      if (params.mealSort == undefined) {
        params.mealSort = null;
      }
      if (params.productKey == undefined) {
        params.productKey = null;
      }
    },
    screenClear() {
      this.cityType = true;
      this.mealType = true;
      this.accountBType = true;
      this.industryType = true;
      (this.select_time_show = moment(this.defaultValMonthOpera, "YYYY-MM")),
        (this.searchList = {
          statisticsTime: this.defaultValMonthOpera,
          productKey: undefined,
          mealSort: undefined,
          saleBuName: undefined,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        });
      this.getmonthReport(this.searchList);
    },
    screenBtn(){
      this.cityType = true;
      this.mealType = true;
      this.accountBType = true;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



