<template>
  <div class="interfaceList">
    <!-- <div class="interface-head curactive">套餐业绩月报表</div> -->
    <div class="interface-head">
      <div class="interface-tabs_head curactive">套餐业绩月报表</div>
      <div
        class="interface-tabs_head"
        @click="$router.push({ path: '/mealYearreport' })"
      >
        套餐业绩年报表
      </div>
      <div
        class="interface-tabs_head"
        @click="$router.push({ path: '/meal_TrendReport' })"
      >
        套餐趋势报表
      </div>
    </div>
    <div v-if="hasPermissions == true" style="width: 100%; height: 100%">
      <!-- <div class="interface-head curactive">套餐业绩月报表</div> -->
      <div class="page-box">
        <div class="clear search-condition-box">
          <div class="search-condition-box-item">
            <a-month-picker
              @change="timeChange"
              v-model="select_time_show"
              placeholder="选择日期"
            />
          </div>
          <div
            class="search-condition-box-item"
            style="width: 330px; height: auto"
          >
            <a-select
              v-model="searchList.saleBu"
              style="width: 120px; display: inline-block"
              allowClear
              placeholder="销售 BU"
            >
              <a-select-option
                v-for="(item, index) in salebulist"
                :key="index"
                :value="item.ascription_BU"
                :title="item.ascription_BU"
                >{{ item.ascription_BU }}</a-select-option
              >
            </a-select>
            <a-select
              v-model="searchList.productKeyList"
              style="width: 200px; display: inline-block; margin-left: 6px"
              allowClear
              placeholder="产品"
              @change="mealChange"
            >
              <a-select-option
                v-for="(item, index) in mealList"
                :key="index"
                :value="item.productKey"
                :title="item.productName"
                >{{ item.productName }}</a-select-option
              >
            </a-select>
          </div>
          <div class="search-condition-box-item" style="width: 220px">
            <a-select
              v-model="searchList.mealSort"
              style="width: 120px; display: inline-block"
              allowClear
              placeholder="套餐分类"
            >
              <a-select-option
                v-for="(item, index) in mealesList"
                :key="index"
                :value="item.mealSort"
                :title="item.mealSortName"
              >
                <span>{{ item.mealSortName }}</span>
              </a-select-option>
            </a-select>
            <a-switch
              style="margin-left: 20px"
              v-model="searchList.isFacilitator"
              checked-children="服务商"
              un-checked-children="服务商"
            />
          </div>
          <div class="search-condition-box-item" style="width: auto">
            <a-button @click="screenClear">取消筛选</a-button>
            <a-button type="primary" @click="screenBtn">筛选</a-button>
          </div>
        </div>
        <div class="package-price">
          <div class="package-price-line clear">
            【总销售金额】资金：
            <span>{{ this.all_money_status.payMoneyAll }}</span>
            元；新优惠券：
            <span>{{ this.all_money_status.payNewCouponsAll }}</span>
            元；老优惠券：
            <span>{{ this.all_money_status.payOldCouponsAll }}</span>
            元；
          </div>
          <div class="package-price-line clear">
            【总退款金额】资金：
            <span>{{ this.all_money_status.backMoneyAll }}</span>
            元；新优惠券：
            <span>{{ this.all_money_status.backNewCouponsAll }}</span>
            元；老优惠券：
            <span>{{ this.all_money_status.backOldCouponsAll }}</span>
            元；
          </div>
          <div class="package-price-line clear">
            【总实际金额】资金：
            <span>{{ this.all_money_status.realMoneyAll }}</span>
            元；新优惠券：
            <span>{{ this.all_money_status.realNewCouponsAll }}</span>
            元；老优惠券：
            <span>{{ this.all_money_status.realOldCouponsAll }}</span>
            元；
          </div>
          <!-- <div class="package-price-line clear">
            【总实际收入】资金：
            <span>{{this.all_money_status.realIncomeMoney}}</span>
            元；
          </div>-->
        </div>
        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              :columns="columns"
              :locale="locale"
              :dataSource="data"
              bordered
              :pagination="false"
              :scroll="{ x: gettablewidth(), y: gettableheight() }"
            >
              <template slot="num" slot-scope="row, text, index">
                <strong>{{ index + 1 }}</strong>
              </template>
              <a slot="operation" slot-scope="text, record">
                <router-link
                  tag="a"
                  target="_blank"
                  :to="{
                    name: 'orderInformation',
                    params: { catId: 0 },
                    query: {
                      mealKey: record.mealKey,
                      productKey: record.productKey,
                      selectTime: searchList.statisticsTime,
                      token: saas_ykf_token,
                    },
                  }"
                  >订单明细</router-link
                >
              </a>
            </a-table>
            <div style="float: right; margin: 30px; margin-right: 150px">
              共 {{ totalshu }}条数据
            </div>
          </a-spin>
        </div>
      </div>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  name: "packageMonthReport",
  data() {
    return {
      saas_ykf_token: "",
      totalshu: "",

      hasPermissions: false,
      searchList: {
        statisticsTime: null,
        productKeyList: [],
        mealSort: undefined,
        saleBu: undefined,
        pageNum: 1,
        pageSize: 10000,
        isFacilitator: false,
      },
      meal: undefined,
      all_money_status: {},
      mealList: [],
      all_mealslist: [],
      mealesList: [],
      salebulist: [],
      mealType: true,
      columns: [
        { title: "序号", scopedSlots: { customRender: "num" }, width: 50 },
        // { title: '产品', dataIndex: 'productName',key:'productName', width: 120, },
        { title: "套餐", dataIndex: "mealName", key: "mealKey", width: 250 },
        {
          title: "套餐分类",
          dataIndex: "mealSortName",
          key: "mealSortName",
          width: 250,
          defaultSortOrder: "",
        },
        {
          title: "客户量",
          dataIndex: "customerNum",
          key: "customerNum",
          width: 120,
          defaultSortOrder: "customerNum",
          sorter: (a, b) => a.customerNum - b.customerNum,
        },
        {
          title: "销售金额",
          width: 480,
          children: [
            {
              title: "资金",
              dataIndex: "payMoney",
              key: "payMoney",
              width: 120,
              defaultSortOrder: "payMoney",
              sorter: (a, b) => a.payMoney - b.payMoney,
            },
            {
              title: "新优惠券",
              dataIndex: "payNewCoupons",
              key: "payNewCoupons",
              width: 120,
              defaultSortOrder: "payNewCoupons",
              sorter: (a, b) => a.payNewCoupons - b.payNewCoupons,
            },
            {
              title: "老优惠券",
              dataIndex: "payOldCoupons",
              key: "payOldCoupons",
              width: 120,
              defaultSortOrder: "payOldCoupons",
              sorter: (a, b) => a.payOldCoupons - b.payOldCoupons,
            },
            {
              title: "订单数量",
              dataIndex: "payOrderNum",
              key: "payOrderNum",
              width: 120,
              defaultSortOrder: "payOrderNum",
              sorter: (a, b) => a.payOrderNum - b.payOrderNum,
            },
          ],
        },
        {
          title: "退款金额",
          width: 480,
          children: [
            {
              title: "资金",
              dataIndex: "backMoney",
              key: "backMoney",
              width: 120,
              defaultSortOrder: "backMoney",
              sorter: (a, b) => a.backMoney - b.backMoney,
            },
            {
              title: "新优惠券",
              dataIndex: "backNewCoupons",
              key: "backNewCoupons",
              width: 120,
              defaultSortOrder: "backNewCoupons",
              sorter: (a, b) => a.backNewCoupons - b.backNewCoupons,
            },
            {
              title: "老优惠券",
              dataIndex: "backOldCoupons",
              key: "backOldCoupons",
              width: 120,
              defaultSortOrder: "backOldCoupons",
              sorter: (a, b) => a.backOldCoupons - b.backOldCoupons,
            },
            {
              title: "订单数量",
              dataIndex: "backOrderNum",
              key: "backOrderNum",
              width: 120,
              defaultSortOrder: "backOrderNum",
              sorter: (a, b) => a.backOrderNum - b.backOrderNum,
            },
          ],
        },
        {
          title: "实际金额",
          width: 480,
          children: [
            {
              title: "资金",
              dataIndex: "realMoney",
              key: "realMoney",
              width: 120,
              defaultSortOrder: "realMoney",
              sorter: (a, b) => a.realMoney - b.realMoney,
            },
            {
              title: "新优惠券",
              dataIndex: "realNewCoupons",
              key: "realNewCoupons",
              width: 120,
              defaultSortOrder: "realNewCoupons",
              sorter: (a, b) => a.realNewCoupons - b.realNewCoupons,
            },
            {
              title: "老优惠券",
              dataIndex: "realOldCoupons",
              key: "realOldCoupons",
              width: 120,
              defaultSortOrder: "realOldCoupons",
              sorter: (a, b) => a.realOldCoupons - b.realOldCoupons,
            },
            {
              title: "订单数量",
              dataIndex: "realOrderNum",
              key: "realOrderNum",
              width: 120,
              defaultSortOrder: "realOrderNum",
              sorter: (a, b) => a.realOrderNum - b.realOrderNum,
            },
          ],
        },
        {
          title: "平均购买数量",
          align: "center",
          dataIndex: "averagePurchaseNum",
          key: "averagePurchaseNum",
          width: 120,
          defaultSortOrder: "averagePurchaseNum",
          sorter: (a, b) => a.averagePurchaseNum - b.averagePurchaseNum,
        },
        {
          title: "客单价",
          dataIndex: "averagePurchasePrice",
          key: "averagePurchasePrice",
          width: 120,
          defaultSortOrder: "averagePurchasePrice",
          sorter: (a, b) => a.averagePurchasePrice - b.averagePurchasePrice,
        },
        {
          title: "进货成本",
          dataIndex: "purchaseCost",
          key: "purchaseCost",
          width: 150,
          defaultSortOrder: "purchaseCost",
          sorter: (a, b) => a.purchaseCost - b.purchaseCost,
        },
        {
          title: "毛利润",
          dataIndex: "grossProfit",
          key: "grossProfit",
          width: 150,
          defaultSortOrder: "grossProfit",
          sorter: (a, b) => a.grossProfit - b.grossProfit,
        },
        {
          title: "结算金额",
          dataIndex: "settlementAmount",
          key: "settlementAmount",
          width: 120,
          defaultSortOrder: "settlementAmount",
          sorter: (a, b) => a.settlementAmount - b.settlementAmount,
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: 120,
          scopedSlots: { customRender: "operation" },
        },
      ],
      data: [],
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1,
        defaultPageSize: 20,
        total: "",
        showTotal: (total) => `共 ${total} 条数据`,
        showSizeChanger: true,
        pageSizeOptions: ["20", "40", "100"],
        onShowSizeChange: (current, pageSize) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          this.paramsCheck(params);
          //this.getmonthReport(params);
          // this.getList()  //显示列表的接口名称
        },
        onChange: (current, size) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          this.pagination.current = current;
          this.pagination.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.paramsCheck(params);
          //this.getmonthReport(params);
          // this.getList()
        }, // 点击页码事件
      },
      locale: {
        // emptyText:'暂无数据'
      },
      defaultValMonthOpera: undefined,
      select_time_show: undefined,
      table_spinning: false,
    };
  },
  watch: {
    "searchList.productKeyList": function (data) {
      if (data == []) {
        this.mealesList = this.all_mealslist;
      }
    },
  },
  created() {},
  mounted() {
    this.hasPermissions = this.$store.state.pagepermission;
    this.saas_ykf_token = sessionStorage.getItem("saas_ykf_token");
    document.title = "套餐业绩月报表";
    if (this.hasPermissions == true) {
      getData.getproductmealInfo().then((res) => {
        this.mealList = res.data.mealSortList;
        let all_mealslist = [];
        for (let item of this.mealList) {
          all_mealslist = all_mealslist.concat(item.mealList);
        }
        this.all_mealslist = all_mealslist;
        this.mealesList = this.all_mealslist;
        this.mealesList = this.splice(this.mealesList);
        this.salebulist = res.data.saleBuMap;
      });
      this.addDate();
      let params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      this.getmonthReport(params);
    }
  },
  methods: {
    //筛选数组
    splice(List) {
      return List.filter((item) => item.mealSortName != "");
    },
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 350;
    },
    getmonthReport(params) {
      this.table_spinning = true;
      getData.getmonthReport(params).then((res) => {
        this.table_spinning = false;
        this.pagination.total = res.data.total;
        this.totalshu = res.data.total;
        this.data = res.data.list;
        this.all_money_status = res.data;
        for (var i = 0; i < this.data.length; i++) {
          this.data[i].num = i + 1;
        }
      });
    },
    addDate() {
      var nowDate = new Date();
      let date = {
        year: nowDate.getFullYear(),
        month: nowDate.getMonth() + 1,
        // month: nowDate.getMonth(),
        date: nowDate.getDate(),
      };
      var systemDate = date.year + "-" + 0 + date.month;
      if (date.month < 10) {
        systemDate = date.year + "-" + 0 + date.month;
      } else {
        systemDate = date.year + "-" + date.month;
      }
      this.searchList.statisticsTime = systemDate;
      this.defaultValMonthOpera = systemDate;
      this.select_time_show = moment(systemDate, "YYYY-MM");
    },
    timeChange(date, dateString) {
      this.select_time_show = date;
      this.searchList.statisticsTime = dateString;
    },
    mealChange(v) {
      this.searchList.mealSort = undefined;
      if (v == undefined || v == []) {
        this.mealesList = this.all_mealslist;
        this.mealesList = this.splice(this.mealesList);
      }
      for (let item of this.mealList) {
        if (v == item.productKey) {
          this.mealesList = item.mealList;
          this.mealesList = this.splice(this.mealesList);
          this.mealType = false;
        }
      }
    },
    screenBtn() {
      // if (
      //   this.searchList.productKey !== undefined &&
      //   this.searchList.mealSort == null
      // ) {
      //   this.$message.error("请选择套餐分类");
      //   return;
      // }
      let params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      this.getmonthReport(params);
      //console.log(this.mealesList)
    },
    paramsCheck(params) {
      if (params.statisticsTime == undefined) {
        params.statisticsTime = null;
      }
      if (params.mealSort == undefined) {
        params.mealSort = null;
      }
      if (params.productKeyList.length == 0) {
        params.productKeyList = null;
      }
    },
    screenClear() {
      this.cityType = true;
      this.mealType = true;
      this.accountBType = true;
      this.industryType = true;
      this.select_time_show = moment(this.defaultValMonthOpera, "YYYY-MM");
      this.searchList = {
        statisticsTime: this.defaultValMonthOpera,
        productKeyList: [],
        isFacilitator: false,
        mealSort: undefined,
        saleBu: undefined,
        pageNum: 1,
        pageSize: this.pagination.pageSize,
      };
      let params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      this.getmonthReport(params);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.form-box {
  height: 100%;
}
</style>



