<template>
    <div>
        <div class="interfaceList"   v-if="hasPermissions == true" style="width:100%;height:100%">
            <div class="interface-head">行业指标统计</div>
            <div class="page-box">
                <div class="clear search-condition-box">
                    <div class="search-condition-box-item">
                        <a-month-picker @change="timeChange" v-model="select_time_show" placeholder="选择日期" />
                    </div>
                    <div class="search-condition-box-item" style="width:200px;height:auto;">
                        <a-select
                            v-model="searchList.saleBu"
                            style="width: 150px;display: inline-block;"
                            allowClear
                            placeholder="一级行业"
                            @change="changehang"
                        >
                        <a-select-option
                            v-for="(item,index)  in  oneindustrylist"
                            :key="index"
                            :value="item.industry_name"
                            :title="item.industry_name"
                        >{{item.industry_name}}</a-select-option>
                        </a-select>
                    </div>
                    <div class="search-condition-box-item" style="width: auto">
                        <a-button @click="screenClear">取消筛选</a-button>
                        <a-button type="primary" @click="screenBtn">筛选</a-button>
                    </div>
                </div>


                <div class="form-box">
                    <a-spin :spinning="table_spinning" tip="数据加载中...">
                        <a-table
                            :columns="columns"
                            :locale="locale"
                            :data-source="data"
                            :loading="loading"
                            :scroll="{ x:gettablewidth(), y: gettableheight() }"
                            :pagination="false"
                        >
                            
                        </a-table>
                    </a-spin>
                </div>
            </div>
        </div>

        <div v-else class="no-permission">
            <span>无权限，请联系管理员</span>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            hasPermissions:false,
            select_time_show:'',
            loading:false,
            pagination: {},
            searchList:{
                saleBu:''
            },
            hangyeid:'',
            riqi:'',
            oneindustrylist:[
                
            ],

            columns: [
                { title: "序号", dataIndex: "num", key: "num", show: true, width: 50 },
                // { title: '产品', dataIndex: 'productName',key:'productName', width: 120, },
                { title: "行业（二级）", dataIndex: "industry_two_name", key: "identity_name", width: 150 },
                {
                    title: "访问量",
                    dataIndex: "uv",
                    key: "uv",
                    width: 120
                },
                {
                    title: "咨询量",
                    dataIndex: "consult",
                    key: "consult",
                    width: 120
                },
                {
                    title: "排队量",
                    dataIndex: "count",
                    key: "count",
                    width: 120
                },
                {
                    title: "对话量",
                    dataIndex: "talk_valid",
                    key: "talk_valid",
                    width: 120
                },
                {
                    title: "留言量",
                    dataIndex: "chat_msg",
                    key: "chat_msg",
                    width: 120
                },
                {
                    title: "机器人的线索量",
                    dataIndex: "robot_clew",
                    key: "robot_clew",
                    width: 120
                },
                {
                    title: "总线索量",
                    dataIndex: "all_clew",
                    key: "all_clew",
                    width: 120
                },
                {
                    title: "客服响应时间",
                    dataIndex: "arg_responsetime",
                    key: "arg_responsetime",
                    width: 120
                },
                {
                    title: "客服接待量",
                    dataIndex: "talk_num",
                    key: "talk_num",
                    width: 120
                },
                {
                    title: "咨询转化率",
                    dataIndex: "operation1",
                    key: "operation1",
                    width: 120
                },
                {
                    title: "线索转化率",
                    dataIndex: "operation2",
                    key: "operation2",
                    width: 120
                },
                {
                    title: "开通客服坐席量",
                    dataIndex: "seats_amount",
                    key: "seats_amount",
                    width: 120
                },
                {
                    title: "月消化金额量",
                    dataIndex: "pay_money",
                    key: "pay_money",
                    width: 120
                }
            ],
            data: [],

            locale:{

            },
            table_spinning: false
        }
    },
    methods:{
        //改变一级菜单
        changehang(e){
            //console.log(e)
            this.searchList.saleBu=e
            this.oneindustrylist.forEach(item=>{
                if(item.industry_name == this.searchList.saleBu){
                    this.hangyeid = item.id
                }
            })

            this.getdata({
                id:this.hangyeid,
                date:this.riqi
            })
        },
        handleTableChange(pagination, filters, sorter){
            const pager = { ...this.pagination };
            pager.current = pagination.current;
            this.pagination = pager;
        },
        
        screenClear(){
            this.getyidata()
            this.fuzhidata()
        },
        //点击筛选
        screenBtn(){
            
            this.oneindustrylist.forEach(item=>{
                if(item.industry_name == this.searchList.saleBu){
                    this.hangyeid = item.id
                }
            })

            this.getdata({
                id:this.hangyeid,
                date:this.riqi
            })

        },
        gettablewidth() {
            let w = 0;
            for (let i of this.columns) {
                w += i.width;
            }
                return w;
            },
            gettableheight() {
                let height =
                    window.innerHeight ||
                    document.documentElement.clientHeight ||
                    document.body.clientHeight;
                return height - 250;
        },

        //获取列表数据
        getdata(data){
            this.loading = true
            this.$keywords_http
            .post("/javaTalkKeyWord/industryIndex/selectIndustryIndex",data).then(res=>{
                //console.log(res)
                if(res.data.code == 101){
                var list = res.data.data
                for(var i=0;i<list.length;i++){
                    list[i].num=i+1
                }
                list.forEach(item=>{
                    if(item.talk_valid == 0 || item.talk_valid == 'null' || item.uv == 0 || item.uv == 'null' || !item.talk_valid || !item.uv){
                        item.operation1 = 0
                    }else{
                        item.operation1=((parseFloat(item.talk_valid)/parseFloat(item.uv))*100).toFixed(1) + '%'
                    }
                    if(item.all_clew == 0 || item.all_clew == 'null' || item.talk_valid == 0 || item.talk_valid == 'null' || !item.all_clew || !item.talk_valid){
                        item.operation2 = 0
                    }else{
                        item.operation2 = ((parseFloat(item.all_clew)/parseFloat(item.talk_valid))*100).toFixed(1) + '%'
                        // console.log((parseInt(item.all_clew)/parseInt(item.talk_valid)).toFixed(3)*100)
                        // console.log(item.operation2)
                    }
                    if( !item.uv ){
                        item.uv=0
                    }
                    if(!item.consult){
                        item.consult = 0
                    }
                    if(!item.count){
                        item.count = 0
                    }
                    if(!item.talk_valid){
                        item.talk_valid = 0
                    }
                    if(!item.chat_msg){
                        item.chat_msg = 0
                    }
                    if(!item.robot_clew){
                        item.robot_clew = 0
                    }
                    if(!item.all_clew){
                        item.all_clew = 0
                    }
                    if(!item.arg_responsetime){
                        item.arg_responsetime = 0
                    }
                    if(!item.talk_num){
                        item.talk_num = 0
                    }
                    if(!item.seats_amount){
                        item.seats_amount =0
                    }
                    if(!item.pay_money){
                        item.pay_money = 0
                    }
                })
                //console.log(list)
                this.data = list
                this.loading = false
                }else{
                    this.loading = false
                    this.data = []
                }
            })
        },

        getyidata(){
            this.riqi=new Date().getFullYear() + (this.test(new Date().getMonth() + 1))
            this.$keywords_http
            .post("/javaTalkKeyWord/industryIndex/selectPrimaryIndustry").then(res=>{
                //console.log(res)
                if(res.data.code == 101){
                    this.oneindustrylist=res.data.data
                    this.searchList.saleBu=res.data.data[0].industry_name
                    this.getdata({
                        id:res.data.data[0].id,
                        date:this.riqi
                    })
                }
            })
        },
        //补零操作
        test(str){
            if(str<10){
                return '0'+str
            }else{
                return str
            }
        },
        fuzhidata(){
            this.select_time_show=new Date().getFullYear() + '-' + (this.test(new Date().getMonth() + 1))
            
        },
        //改变时间
        timeChange(e){
            this.oneindustrylist.forEach(item=>{
                if(item.industry_name == this.searchList.saleBu){
                    this.hangyeid = item.id
                }
            })
            //console.log(e._d)
            this.riqi=e._d.getFullYear()+(this.test(e._d.getMonth() + 1))
            //console.log(this.riqi)
            //console.log(this.hangyeid)
            this.getdata({
                id:this.hangyeid,
                date:this.riqi
            })
        },
    },
    created(){

    },
    mounted(){
        this.getyidata()
        this.fuzhidata()
        this.hasPermissions = this.$store.state.pagepermission;
    }
}
</script>


