<template>
  <div class="interfaceList reguser">
    <div v-if="hasPermissions == true" style="width:100%;height:100%">
      <div class="interface-head">行业管理</div>
      <div class="page-box">
        <div class="clear search-condition-box" style="margin-bottom: 10px;">
          <a-button @click="addNewOneshowModal" type="primary" class="ant-btn"
            >新增一级行业</a-button
          >
          <a-button @click="addNewTwoshowModal" type="primary" class="ant-btn"
            >新增二级行业</a-button
          >
        </div>
        <div style="background: rgba(255, 255, 255, 1);">
          <div
            class="maintype"
            v-for="(item, i) in lists"
            :key="i"
            ref="maintype"
          >
            <div class="main-one">
              <img
                v-if="item.open"
                class="main_img"
                src="../../assets/industry_jian.png"
                @click="closeList(i)"
              />
              <img
                v-else
                class="main_img"
                src="../../assets/industry_jia.png"
                @click="openList(i)"
              />
              <span class="main-left-span">{{ item.industry_name }}</span>
              <span
                class="main-right-span-delete"
                @click="deleteIndustry(item, 1)"
                >删除</span
              >
              <span class="main-right-span-add" @click="editOne(item)"
                >编辑</span
              >
            </div>
            <div
              class="maintyp-two"
              v-for="(item1, i1) in item.second_level"
              :key="i1"
              ref="childType"
            >
              <span class="main-left-span">{{ item1.industry_name }}</span>
              <span
                class="main-right-span-delete"
                @click="deleteIndustry(item1, 2)"
                >删除</span
              >
              <span class="main-right-span-add" @click="editTwo(item.id, item1)"
                >编辑</span
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
    <!--新增一级行业-->
    <a-modal
      title="新增一级行业"
      :visible="addNewOnevisible"
      :confirm-loading="addNewOneconfirmLoading"
      @ok="addNewOneOk"
      @cancel="addNewOneCancel"
    >
      <div class="modal-content">
        <div class="clear modal-content-line">
          <span>行业名称：</span>
          <a-input
            style="float: left;width: 240px;"
            v-model="addNewOne_industry_name"
            placeholder="请输入行业名称"
          />
        </div>
      </div>
    </a-modal>
    <!--编辑一级行业-->
    <a-modal
      title="编辑一级行业"
      :visible="editNewOnevisible"
      :confirm-loading="editNewOneconfirmLoading"
      @ok="editNewOneOk"
      @cancel="editNewOneCancel"
    >
      <div class="modal-content">
        <div class="clear modal-content-line">
          <span>行业名称：</span>
          <a-input
            style="float: left;width: 240px;"
            v-model="editNewOne_industry_name"
            placeholder="请输入行业名称"
          />
        </div>
      </div>
    </a-modal>
    <!--新增二级行业-->
    <a-modal
      title="新增二级行业"
      :visible="addNewTwovisible"
      :confirm-loading="addNewTwoconfirmLoading"
      @ok="addNewTwoOk"
      @cancel="addNewTwoCancel"
    >
      <div class="modal-content">
        <div class="clear modal-content-line" style="margin-bottom: 24px;">
          <span>一级行业：</span>
          <a-select
            v-model="add_industry_parent_id"
            style="width: 240px !important;float: left"
            @change="handleChange"
          >
            <a-select-option
              v-for="(item, index) in wei_lists"
              :key="item.id"
              :value="item.id"
              :title="item.industry_name"
            >
              {{ item.industry_name }}
            </a-select-option>
          </a-select>
        </div>
        <div class="clear modal-content-line">
          <span>行业名称：</span>
          <a-input
            style="float: left;width: 240px;"
            v-model="addNewTwo_industry_name"
            placeholder="请输入行业名称"
          />
        </div>
      </div>
    </a-modal>
    <!--编辑二级行业-->
    <a-modal
      title="编辑二级行业"
      :visible="editNewTwovisible"
      :confirm-loading="editNewTwoconfirmLoading"
      @ok="editNewTwoOk"
      @cancel="editNewTwoCancel"
    >
      <div class="modal-content">
        <!-- <div class="clear modal-content-line" style="margin-bottom: 24px;">
          <span>一级行业：</span>
          <a-select
            v-model="edit_industry_parent_id"
            style="width: 240px !important;float: left"
            @change="handleChange"
          >
            <a-select-option
              v-for="(item, index) in lists"
              :key="item.id"
              :value="item.id"
              :title="item.industry_name"
            >
              {{ item.industry_name }}
            </a-select-option>
          </a-select>
        </div> -->
        <div class="clear modal-content-line">
          <span>行业名称：</span>
          <a-input
            style="float: left;width: 240px;"
            v-model="editNewTwo_industry_name"
            placeholder="请输入行业名称"
          />
        </div>
      </div>
    </a-modal>
  </div>
</template>

<script>
import * as getData from "../../api/api";
import moment from "moment";
import city from "../../assets/js/cities";
import axios from "axios";
export default {
  name: "industryManage",
  created() {
    this.cities = city;
    // axios.defaults.headers['token'] = '1'
  },
  inject: ["reload"],
  data() {
    return {
      industry_parent: "",
      hasPermissions: false,
      lists: [{}],
      addNewOnevisible: false,
      addNewOneconfirmLoading: false,
      addNewOne_industry_name: "",
      editNewOnevisible: false,
      editNewOneconfirmLoading: false,
      editNewOne_industry_name: "",
      editNewOne_id: "",
      addNewTwovisible: false,
      addNewTwoconfirmLoading: false,
      addNewTwo_industry_name: "",
      add_industry_parent_id: "",
      editNewTwovisible: false,
      editNewTwoconfirmLoading: false,
      editNewTwo_industry_name: "",
      editNewTwo_id: "",
      edit_industry_parent_id: ""
    };
  },
  mounted() {
    document.title = "行业管理";
    this.hasPermissions = this.$store.state.pagepermission;
    //this.hasPermissions = true;
    if (this.hasPermissions == true) {
      //          获取行业list
      var data = {};
      this.$industry_http
        .post("/portrayal/IndustryManage/getIndustryList", data)
        .then(res => {
          var lists = res.data.result;
          this.add_industry_parent_id = lists[0].id;
          console.log(this.add_industry_parent_id);
          lists.forEach((item, i) => {
            item.open = false;
            item.second_level.forEach(items => {
              items.open = false;
            });
          });
          this.lists = lists;
          this.wei_lists = JSON.parse(JSON.stringify(lists));
          this.wei_lists.forEach((item, index) => {
            if (item.id == 321) {
              this.wei_lists.splice(index, 1);
            }
          });
          console.log(this.lists);
          console.log(this.wei_lists);
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    }
  },
  methods: {
    // 替换字符串中的空格和回车
    replace_null(str) {
      return str
        .replace(/\ +/g, "")
        .replace(/[ ]/g, "")
        .replace(/[\r\n]/g, "");
    },
    closeList(i) {
      this.lists[i].open = false;
      this.$refs.maintype[i].style.height = 54 + "px";
    },
    openList(i) {
      this.lists[i].open = true;
      this.$refs.maintype[i].style.height =
        54 + this.lists[i].second_level.length * 37 + "px";
    },
    addNewOneshowModal() {
      this.addNewOnevisible = true;
    },
    addNewOneOk(e) {
      if (this.addNewOne_industry_name == "") {
        this.$message.error("请输入行业名称");
        return;
      }
      this.addNewOneconfirmLoading = true;
      var data = {
        industry_name: this.addNewOne_industry_name,
        industry_parent: null,
        industry_level: 1
      };
      this.$industry_http
        .post("/portrayal/IndustryManage/addIndustry", data)
        .then(res => {
          console.log(res.data);
          if (res.data.code == 101) {
            this.$message.success("添加成功");
            this.addNewOnevisible = false;
            this.addNewOneconfirmLoading = false;
            this.reload();
          } else {
            this.addNewOneconfirmLoading = false;
            //this.$message.error(res.data.errorMsg);
          }
        })
        .catch(err => {
          this.addNewOneconfirmLoading = false;
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    addNewOneCancel(e) {
      this.addNewOnevisible = false;
      this.addNewOne_industry_name = "";
    },
    addNewTwoshowModal() {
      this.addNewTwovisible = true;
    },
    addNewTwoOk(e) {
      if (this.addNewTwo_industry_name == "") {
        this.$message.error("请输入行业名称");
        return;
      }
      this.addNewTwoconfirmLoading = true;
      var data = {
        industry_name: this.addNewTwo_industry_name,
        industry_parent: this.add_industry_parent_id,
        industry_level: 2
      };
      this.$industry_http
        .post("/portrayal/IndustryManage/addIndustry", data)
        .then(res => {
          console.log(res.data);
          if (res.data.code == 101) {
            this.$message.success("添加成功");
            this.addNewTwovisible = false;
            this.addNewTwoconfirmLoading = false;
            this.reload();
          } else {
            this.addNewTwoconfirmLoading = false;
            //this.$message.error(res.data.errorMsg);
          }
        })
        .catch(err => {
          this.addNewTwoconfirmLoading = false;
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    addNewTwoCancel(e) {
      this.addNewTwovisible = false;
      this.addNewTwo_industry_name = "";
    },
    handleChange(value) {
      console.log(`selected ${value}`);
    },
    editOne(item) {
      console.log(item);
      this.editNewOnevisible = true;
      this.editNewOne_industry_name = item.industry_name;
      this.editNewOne_id = item.id;
    },
    editNewOneOk(e) {
      if (this.editNewOne_industry_name == "") {
        this.$message.error("请输入行业名称");
        return;
      }
      this.editNewOneconfirmLoading = true;
      var data = {
        industry_name: this.editNewOne_industry_name,
        id: this.editNewOne_id,
        industry_level: 1,
        industry_parent: null
      };
      this.$industry_http
        .post("/portrayal/IndustryManage/editIndustry", data)
        .then(res => {
          console.log(res.data);
          if (res.data.code == 101) {
            this.$message.success("编辑成功");
            this.editNewOnevisible = false;
            this.editNewOneconfirmLoading = false;
            this.reload();
          } else {
            this.editNewOneconfirmLoading = false;
            //this.$message.error(res.data.errorMsg);
          }
        })
        .catch(err => {
          this.editNewOneconfirmLoading = false;
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    editNewOneCancel(e) {
      this.editNewOnevisible = false;
      this.editNewOne_industry_name = "";
    },
    editTwo(id, item) {
      console.log(item);
      this.editNewTwovisible = true;
      this.editNewTwo_industry_name = item.industry_name;
      this.editNewTwo_id = item.id;
      this.edit_industry_parent_id = id;
      this.industry_parent_can = item.industry_parent;
    },
    editNewTwoOk(e) {
      if (this.editNewTwo_industry_name == "") {
        this.$message.error("请输入行业名称");
        return;
      }
      this.editNewTwoconfirmLoading = true;
      var data = {
        industry_name: this.editNewTwo_industry_name,
        id: this.editNewTwo_id,
        industry_level: 2,
        industry_parent: this.industry_parent_can
      };
      this.$industry_http
        .post("/portrayal/IndustryManage/editIndustry", data)
        .then(res => {
          console.log(res.data);
          if (res.data.code == 101) {
            this.$message.success("编辑成功");
            this.editNewTwovisible = false;
            this.editNewTwoconfirmLoading = false;
            this.reload();
          } else {
            this.editNewTwoconfirmLoading = false;
            //this.$message.error(res.data.errorMsg);
          }
        })
        .catch(err => {
          this.editNewTwoconfirmLoading = false;
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    editNewTwoCancel(e) {
      this.editNewTwovisible = false;
      this.editNewTwo_industry_name = "";
    },
    deleteIndustry(item, level) {
      var self = this;
      var data = {
        id: item.id,
        industry_name: item.industry_name,
        industry_level: level
      };
      this.$confirm({
        title: "确认删除行业" + item.industry_name + "吗?",
        content: "删除后无法恢复",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          if (item.id == 321 || item.industry_parent == 321) {
            self.$message.error("该行业无法删除");
          } else {
            self.$industry_http
              .post("/portrayal/IndustryManage/deleteIndustry", data)
              .then(res => {
                console.log(res.data);
                if (res.data.code == 101) {
                  self.$message.success("删除成功");
                  self.reload();
                } else {
                  // self.$message.error(res.data.errorMsg);
                  //(x-1)x+2 = 1
                  //2a = 3 2b = 6 2c = 12
                  //2a = x
                  //2b = 2x
                  //2c = 4x
                  //你说 此生不负良人千里共婵娟 [2] 怎奈人去楼空似云烟
                }
              })
              .catch(err => {
                console.log(err);
              });
          }
        },
        onCancel() {
          console.log("Cancel");
        }
      });
    }
  }
};
</script>

