                        上月之前客户当月的扩展收入+新增扩展收入
                      </template>
                      <div>
                        当月所有扩展
                      </div>
                    </a-popover>
                  </div>
                  <div v-for="item in data" :key="item.id">
                    <div class="box_content">{{ item.totalExtendMrr }}</div>
                  </div>
                </div>
                <div class="title" v-if="cloumsList.mrrIncrease">
                  <div class="box_title">
                    <a-popover>
                      <template slot="content">
                        （当月内降级套餐 MRR + 当月内减少坐席 MRR + 当月内流失
                        MRR）- （当月内升级套餐 MRR + 当月增加坐席 MRR +
                        当月激活 MRR + 当月扩展收入 + 当月新增 MRR）
                      </template>
                      <div>
                        MRR增长
                      </div>
                    </a-popover>
                  </div>
                  <div v-for="item in data" :key="item.id">
                    <div class="box_content">{{ item.mrrIncrease }}</div>
                  </div>
                </div>
                <div class="title" v-if="cloumsList.totalMrr">
                  <div class="box_title">
                    <a-popover>
                      <template slot="content">
                        MRR 增长 + 上月总 MRR
                      </template>
                      <div>
                        当月MRR
                      </div>
                    </a-popover>
                  </div>
                  <div v-for="item in data" :key="item.id">
                    <div class="box_content">{{ item.totalMrr }}</div>
                  </div>
                </div>
                <div class="title" v-if="cloumsList.mrrIncreaseRate">
                  <div class="box_title">
                    <a-popover>
                      <template slot="content">
                        当月 MRR /上月末总 MRR*100%
                      </template>
                      <div>
                        MRR增长率 _
                      </div>
                    </a-popover>
                  </div>
                  <div v-for="item in data" :key="item.id">
                    <div class="box_content">{{ item.mrrIncreaseRate }}</div>
                  </div>
                </div>
              </div>
            </div>
          </a-spin>
        </div>
      </div>
    </div>

    <div v-else class="no-permission" style="height:calc(100% - 50px)">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>
<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  data() {
    return {
      boxflag: false,
      checklist: [],
      spliceList: [],
      modal2Visible: false,
      columns: [
        {
          title: "年月",
          dataIndex: "statisticsTime",
          width: 150
          //scopedSlots: { customRender: 'statisticsTime' },
        },
        {
          title: "客户流失量",
          dataIndex: "leaveNum",
          width: 150
        },
        {
          title: "客户流失率",
          dataIndex: "leaveRate",
          width: 150
        },
        {
          title: "MRR流失",
          dataIndex: "leaveMrrNum",
          width: 150
        },
        {
          title: "MRR流失率",
          dataIndex: "leaveMrrRate",
          width: 150
        },
        {
          title: "降级套餐客户",
          dataIndex: "demotionNum",
          width: 150
        },
        //sortDirections
        {
          title: "降级坐席",
          dataIndex: "demotionSeatNum",
          width: 150
        },
        {
          title: "MRR降级",
          dataIndex: "demotionMrrNum",
          width: 150
        },
        {
          title: "减少坐席客户",
          dataIndex: "subtractNum",
          width: 150
        },
        {
          title: "减少坐席",
          dataIndex: "subtractSeatNum",
          width: 150
        },
        {
          title: "MRR减少",
          dataIndex: "subtractMrrNum",
          width: 150
        },
        {
          title: "总MRR损失",
          dataIndex: "totalMrrLoss",
          width: 150
        },
        {
          title: "总MRR损失率",
          dataIndex: "totalMrrLossRate",
          width: 150
        },
        {
          title: "升级套餐客户",
          dataIndex: "upgradeNum",
          width: 150
        },
        {
          title: "升级坐席",
          dataIndex: "upgradeSeatNum",
          width: 150
        },
        {
          title: "MRR升级",
          dataIndex: "upgradeMrrNum",
          width: 150
        },
        {
          title: "增加坐席客户",
          dataIndex: "addNum",
          width: 150
        },
        {
          title: "增加坐席",
          dataIndex: "addSeatNum",
          width: 150
        },
        {
          title: "MRR增加",
          dataIndex: "addMrrNum",
          width: 150
        },
        {
          title: "激活客户",
          dataIndex: "activateNum",
          width: 150
        },
        {
          title: "激活坐席",
          dataIndex: "activateSeatNum",
          width: 150
        },
        {
          title: "MRR激活",
          dataIndex: "activateMrrNum",
          width: 150
        },
        {
          title: "扩展收入",
          dataIndex: "extendMrr",
          width: 150
        },
        {
          title: "总MRR收益",
          dataIndex: "totalMrrEarnings",
          width: 150
        },
        {
          title: "净MRR流失率",
          dataIndex: "cleanMrrLossRate",
          width: 150
        },
        {
          title: "新增客户",
          dataIndex: "newNum",
          width: 150
        },
        {
          title: "开通坐席",
          dataIndex: "newSeatNum",
          width: 150
        },
        {
          title: "MRR新增",
          dataIndex: "newMrrNum",
          width: 150
        },
        {
          title: "MRR增长",
          dataIndex: "mrrIncrease",
          width: 150
        },
        {
          title: "当月MRR",
          dataIndex: "totalMrr",
          width: 150
        },
        {
          title: "MRR增长率",
          dataIndex: "mrrIncreaseRate",
          width: 150
        },
        {
          title: "新增扩展"
        },
        {
          title: "当月所有扩展"
        }
      ],
      cloumsList: {
        leaveNum: true,
        leaveRate: true,
        leaveMrrNum: true,
        leaveMrrRate: true,
        demotionNum: true,
        demotionSeatNum: true,
        demotionMrrNum: true,
        subtractNum: true,
        subtractSeatNum: true,
        subtractMrrNum: true,
        totalMrrLoss: true,
        totalMrrLossRate: true,
        upgradeNum: true,
        upgradeSeatNum: true,
        upgradeMrrNum: true,
        addNum: true,
        addSeatNum: true,
        addMrrNum: true,
        activateNum: true,
        activateSeatNum: true,
        activateMrrNum: true,
        extendMrr: true,
        totalMrrEarnings: true,
        cleanMrrLossRate: true,
        newNum: true,
        newSeatNum: true,
        newMrrNum: true,
        mrrIncrease: true,
        totalMrr: true,
        mrrIncreaseRate: true,

        newExtendMrr: true,
        totalExtendMrr: true
      },

      hasPermissions: false,
      searchList: {
        groupName: ""
      },
      mealList: [],
      data: [],
      table_spinning: false
    };
  },
  methods: {
    aaa() {
      //console.log(111);
    },

    //获取数据
    getdata(params) {
      this.table_spinning = true;
      getData.getdetailmrr(params).then(res => {
        //console.log(res)
        this.table_spinning = false;
        if (res.code == 200) {
          res.data.forEach(item => {
            item.leaveRate = (item.leaveRate * 100).toFixed(2) + "%";
            item.leaveMrrRate = (item.leaveMrrRate * 100).toFixed(2) + "%";
            item.totalMrrLossRate =
              (item.totalMrrLossRate * 100).toFixed(2) + "%";
            item.cleanMrrLossRate =
              (item.cleanMrrLossRate * 100).toFixed(2) + "%";
            item.mrrIncreaseRate =
              (item.mrrIncreaseRate * 100).toFixed(2) + "%";
          });
          this.data = res.data;
          //console.log(this.data)
          if (this.data.length == 0) {
            this.boxflag = true;
            //console.log(11)
          } else {
            this.boxflag = false;
          }
        } else {
          this.data = [];
          this.boxflag = true;
        }
      });
    },

    //改变流失客户flag
    onChange(e) {
      //空的
      // this.table_spinning = true
      // getData.getdetailmrr(params).then(res => {
      // })
    },
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 330;
    },
    //点击二级菜单
    mealChange() {},
    //格式转化
    timechangestart(str) {
      return str.getFullYear();
    },
    //添加对象
    changemealist() {
      this.mealList = [];
      var str = parseInt(this.timechangestart(new Date()));
      for (var i = 2020; i <= str; i++) {
        this.mealList.unshift({
          productKey: i,
          productName: i
        });
      }
    },
    //筛选
    screenBtn() {
      //console.log(this.searchList.groupName)
      var data = {
        statisticsTime: this.searchList.groupName
      };
      this.getdata(data);
    },
    //显示字段设置
    screenClear() {
      this.modal2Visible = true;
    },

    //点击确定
    clicktrue() {
      this.modal2Visible = false;
      this.spliceList = this.checklist;

      if (this.checklist.indexOf("新增扩展") > -1) {
        this.cloumsList.newExtendMrr = true;
      } else {
        this.cloumsList.newExtendMrr = false;
      }

      if (this.checklist.indexOf("当月所有扩展") > -1) {
        this.cloumsList.totalExtendMrr = true;
      } else {
        this.cloumsList.totalExtendMrr = false;
      }

      //console.log(this.checklist)
      if (this.checklist.indexOf("客户流失量") > -1) {
        this.cloumsList.leaveNum = true;
      } else {
        this.cloumsList.leaveNum = false;
      }

      if (this.checklist.indexOf("客户流失率") > -1) {
        this.cloumsList.leaveRate = true;
      } else {
        this.cloumsList.leaveRate = false;
      }

      if (this.checklist.indexOf("MRR流失") > -1) {
        this.cloumsList.leaveMrrNum = true;
      } else {
        this.cloumsList.leaveMrrNum = false;
      }

      if (this.checklist.indexOf("MRR流失率") > -1) {
        this.cloumsList.leaveMrrRate = true;
      } else {
        this.cloumsList.leaveMrrRate = false;
      }

      if (this.checklist.indexOf("降级套餐客户") > -1) {
        this.cloumsList.demotionNum = true;
      } else {
        this.cloumsList.demotionNum = false;
      }

      if (this.checklist.indexOf("降级坐席") > -1) {
        this.cloumsList.demotionSeatNum = true;
      } else {
        this.cloumsList.demotionSeatNum = false;
      }

      if (this.checklist.indexOf("MRR降级") > -1) {
        this.cloumsList.demotionMrrNum = true;
      } else {
        this.cloumsList.demotionMrrNum = false;
      }

      if (this.checklist.indexOf("减少坐席客户") > -1) {
        this.cloumsList.subtractNum = true;
      } else {
        this.cloumsList.subtractNum = false;
      }

      if (this.checklist.indexOf("减少坐席") > -1) {
        this.cloumsList.subtractSeatNum = true;
      } else {
        this.cloumsList.subtractSeatNum = false;
      }

      if (this.checklist.indexOf("MRR减少") > -1) {
        this.cloumsList.subtractMrrNum = true;
      } else {
        this.cloumsList.subtractMrrNum = false;
      }

      if (this.checklist.indexOf("总MRR损失") > -1) {
        this.cloumsList.totalMrrLoss = true;
      } else {
        this.cloumsList.totalMrrLoss = false;
      }

      if (this.checklist.indexOf("总MRR损失率") > -1) {
        this.cloumsList.totalMrrLossRate = true;
      } else {
        this.cloumsList.totalMrrLossRate = false;
      }

      if (this.checklist.indexOf("升级套餐客户") > -1) {
        this.cloumsList.upgradeNum = true;
      } else {
        this.cloumsList.upgradeNum = false;
      }

      if (this.checklist.indexOf("升级坐席") > -1) {
        this.cloumsList.upgradeSeatNum = true;
      } else {
        this.cloumsList.upgradeSeatNum = false;
      }

      if (this.checklist.indexOf("MRR升级") > -1) {
        this.cloumsList.upgradeMrrNum = true;
      } else {
        this.cloumsList.upgradeMrrNum = false;
      }

      if (this.checklist.indexOf("增加坐席客户") > -1) {
        this.cloumsList.addNum = true;
      } else {
        this.cloumsList.addNum = false;
      }

      if (this.checklist.indexOf("增加坐席") > -1) {
        this.cloumsList.addSeatNum = true;
      } else {
        this.cloumsList.addSeatNum = false;
      }

      if (this.checklist.indexOf("MRR增加") > -1) {
        this.cloumsList.addMrrNum = true;
      } else {
        this.cloumsList.addMrrNum = false;
      }

      if (this.checklist.indexOf("激活客户") > -1) {
        this.cloumsList.activateNum = true;
      } else {
        this.cloumsList.activateNum = false;
      }

      if (this.checklist.indexOf("激活坐席") > -1) {
        this.cloumsList.activateSeatNum = true;
      } else {
        this.cloumsList.activateSeatNum = false;
      }

      if (this.checklist.indexOf("MRR激活") > -1) {
        this.cloumsList.activateMrrNum = true;
      } else {
        this.cloumsList.activateMrrNum = false;
      }

      if (this.checklist.indexOf("扩展收入") > -1) {
        this.cloumsList.extendMrr = true;
      } else {
        this.cloumsList.extendMrr = false;
      }

      if (this.checklist.indexOf("总MRR收益") > -1) {
        this.cloumsList.totalMrrEarnings = true;
      } else {
        this.cloumsList.totalMrrEarnings = false;
      }

      if (this.checklist.indexOf("净MRR流失率") > -1) {
        this.cloumsList.cleanMrrLossRate = true;
      } else {
        this.cloumsList.cleanMrrLossRate = false;
      }

      if (this.checklist.indexOf("新增客户") > -1) {
        this.cloumsList.newNum = true;
      } else {
        this.cloumsList.newNum = false;
      }

      if (this.checklist.indexOf("开通坐席") > -1) {
        this.cloumsList.newSeatNum = true;
      } else {
        this.cloumsList.newSeatNum = false;
      }

      if (this.checklist.indexOf("MRR新增") > -1) {
        this.cloumsList.newMrrNum = true;
      } else {
        this.cloumsList.newMrrNum = false;
      }

      if (this.checklist.indexOf("MRR增长") > -1) {
        this.cloumsList.mrrIncrease = true;
      } else {
        this.cloumsList.mrrIncrease = false;
      }

      if (this.checklist.indexOf("当月MRR") > -1) {
        this.cloumsList.totalMrr = true;
      } else {
        this.cloumsList.totalMrr = false;
      }

      if (this.checklist.indexOf("MRR增长率") > -1) {
        this.cloumsList.mrrIncreaseRate = true;
      } else {
        this.cloumsList.mrrIncreaseRate = false;
      }
    },
    //点击取消
    clickfalse() {
      this.modal2Visible = false;
      this.checklist = this.spliceList;
    }
  },
  mounted() {
    document.title = "详细 MRR";
    this.hasPermissions = this.$store.state.pagepermission;
    this.searchList.groupName = parseInt(this.timechangestart(new Date()));
    this.changemealist();
    var data = {
      statisticsTime: "2020"
    };
    this.getdata(data);

    //this.checklist=[]
    this.columns.forEach(item => {
      this.checklist.push(item.title);
      this.spliceList.push(item.title);
    });
  }
};
</script>
<style lang="less" scoped>
.box {
  width: 100%;
  //height: 36px;
  background: #fff;
  display: flex;
  //justify-content: space-around;
  overflow-x: auto;
  position: relative;
  .box_flag {
    position: absolute;
    top: 50%;
    left: 40%;
  }
  .title {
    flex-shrink: 0;
    float: left;
    min-width: 120px;
    .box_title {
      width: 100%;
      background: #ececec;
      line-height: 36px;
      text-align: right;
      .nianyue {
        width: 100%;
        text-align: center;
      }
    }
    .box_content {
      width: 100%;
      line-height: 50px;
      text-align: right;
      border-bottom: 1px solid #d8dfea;
    }
    .title_hears {
      min-height: 150px;
      .box_content {
        width: 100%;
        line-height: 50px;
        text-align: center;
        border-bottom: 1px solid #d8dfea;
      }
    }
  }
}
</style>



