<template>
  <div class="page">
    <div
      v-if="hasPermissions == true"
      style="width:100%;height:calc(100% - 50px)"
    >
      <div class="interface-head">
        人工质检
      </div>

      <div class="page-box">
        <div
          class="clear search-condition-box"
          style="margin-top:2px;margin-left: 0px"
        >
          <div
            class="clear search-condition-box"
            style="margin-top:10px;margin-left:0px;width:100%;"
          >
            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 100px;line-height:30px;float:left;">
                注册年限：
              </div>
              <div style="width: 140px;float: right;margin-right:30px">
                <a-select
                  v-model="searchList.year"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="选择年限"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in yearList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productkey"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>

            <!-- <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 100px;line-height:30px;float:left">
                行业识别分类：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.industry"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="是否识别"
                  value="small"
                  @change="changestate"
                >
                  <a-select-option
                    v-for="(item, index) in industryList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div> -->
            <!-- v-if="industy_flag" -->
            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 90px;line-height:30px;float:left">
                一级行业：
              </div>
              <a-select
                v-model="value"
                style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                allowClear
                placeholder="选择一级行业"
                value="small"
                @change="changeindustry"
              >
                <a-select-option
                  v-for="(item, index) in industry_list"
                  :key="index"
                  :value="item.id"
                  :title="item.id"
                  >{{ item.industry_name }}
                </a-select-option>
              </a-select>
              <!-- <a-cascader
                :options="industry_list"
                :field-names="{
                  label: 'industry_name',
                  value: 'id',
                  children: 'second_level'
                }"
                :value="value"
                expand-trigger="hover"
                placeholder="选择行业"
                :display-render="displayRender"
                @change="onChange1"
                style="width:140px;float: left;margin-right:30px"
              /> -->
            </div>

            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 90px;line-height:30px;float:left">
                二级行业：
              </div>
              <a-select
                v-model="child_value"
                style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                allowClear
                placeholder="选择二级行业"
                :disabled="industry_child_flag"
                value="small"
              >
                <a-select-option
                  v-for="(item, index) in industry_child_list"
                  :key="index"
                  :value="item.id"
                  :title="item.id"
                  >{{ item.industry_name }}
                </a-select-option>
              </a-select>
              <!-- <a-cascader
                :options="industry_list"
                :field-names="{
                  label: 'industry_name',
                  value: 'id',
                  children: 'second_level'
                }"
                :value="value"
                expand-trigger="hover"
                placeholder="选择行业"
                :display-render="displayRender"
                @change="onChange1"
                style="width:140px;float: left;margin-right:30px"
              /> -->
            </div>

            <div class="search-condition-box-item" style="width: 240px">
              <div style="width: 100px;line-height:30px;float:left;">
                质检状态：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.state"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="选择质检状态"
                  value="small"
                  @change="changecompatibility"
                >
                  <a-select-option
                    v-for="(item, index) in stateList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>
            <div
              class="search-condition-box-item"
              style="width: 330px"
              v-if="quality_time_flag"
            >
              <div
                style="width: 182px;line-height:30px;float:left;margin-left:20px"
              >
                质检时间（该时间及以前）：
              </div>
              <div style="width: 120px;float: right;">
                <a-date-picker
                  @change="change_quality_time"
                  v-model="quality_time"
                  :disabled-date="disabledDate"
                />
              </div>
            </div>
            <div
              class="search-condition-box-item"
              style="width: 240px"
              v-if="compatibilityflag"
            >
              <div
                style="width: 70px;line-height:30px;float:left;margin-left:20px"
              >
                匹配度：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.compatibility"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="选择质检状态"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in compatibilityList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>
          </div>

          <div
            class="clear search-condition-box"
            style="margin-top:10px;margin-left:0px;width:100%;"
          >
            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 100px;line-height:30px;float:left;">
                是否测试：
              </div>
              <div style="width: 140px;float: right;margin-right:30px">
                <a-select
                  v-model="searchList.istext"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="是否测试"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in istextList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>

            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 100px;line-height:30px;float:left">
                坐席开户数量：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.num"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="选择开户数量"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in numList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>

            <div class="search-condition-box-item" style="width: 270px">
              <div style="width: 100px;line-height:30px;float:left">
                去年消费总额：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.lastyeartotal"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="去年消费总额"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in lastYearTotalList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>

            <div class="search-condition-box-item" style="width: 240px">
              <div style="width: 100px;line-height:30px;float:left;">
                导入数量：
              </div>
              <div style="width: 140px;float: right;">
                <a-select
                  v-model="searchList.import"
                  style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                  allowClear
                  placeholder="导入数量"
                  value="small"
                >
                  <a-select-option
                    v-for="(item, index) in importList"
                    :key="index"
                    :value="item.productkey"
                    :title="item.productName"
                    >{{ item.productName }}
                  </a-select-option>
                </a-select>
              </div>
            </div>
            <div
              class="search-condition-box-item"
              style="width: 270px;margin-left:20px"
            >
              <div style="width:80px;float:left">
                <a-button
                  type="primary"
                  @click="confirm"
                  :disabled="noprocessingdata.length > 0"
                >
                  导入
                </a-button>
              </div>
              <div style="width:80px;float:left">
                <a-button type="danger" @click="cleardatamodal">
                  清除
                </a-button>
              </div>
              <div style="width:90px;float:right;margin-top:6px">
                <a style="color:#218BFC;" @click="commit">重置导入条件</a>
              </div>
            </div>
          </div>

          <!-- <div class="search-condition-box-item" style="width: 270px">
            <div
              style="width: 70px;line-height:30px;float:left;margin-left:30px"
            >
              有无对话：
            </div>
            <div style="width: 140px;float: right;">
              <a-select
                v-model="searchList.dialogue"
                style="width: 130px;float:left;display: inline-block;margin-left: -10px;"
                allowClear
                placeholder="有无对话"
              >
                <a-select-option
                  v-for="(item, index) in dialogueList"
                  :key="index"
                  :value="item.productkey"
                  :title="item.productName"
                  >{{ item.productName }}
                </a-select-option>
              </a-select>
            </div>
          </div> -->

          <!-- <div
            class="search-condition-box-item"
            style="width: 140px;margin-top:10px;"
          ></div> -->
        </div>

        <!-- 列表展示 -->

        <div class="form-box" style="height:calc(100vh - 300px)">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              rowKey="num"
              :pagination="pagination"
              :columns="columns"
              :locale="locale"
              :dataSource="data"
              :scroll="{ x: gettablewidth(), y: gettableheight() }"
            >
              <span slot="account_type" slot-scope="text">
                <span v-if="text.account_type == 1">客户账号</span>
                <span v-if="text.account_type == 2">测试账号</span>
              </span>
              <span slot="exit" slot-scope="index, text">
                <a @click="tozhi(text.company_id, text.num)">质检</a>
              </span>
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>
    <div v-else class="no-permission" style="height:calc(100% - 50px)">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import * as getData from "../../api/api";
import moment from "moment";
export default {
  components: {},
  data() {
    return {
      quality_time_flag: false,
      quality_time: undefined,
      child_arr: "",
      industry_child_flag: false,
      industy_flag: false,
      value: 1,
      child_value: 19,
      industry_list: [],
      industry_child_list: [],

      table_spinning: false,
      hasPermissions: true,
      searchList: {
        year: undefined,
        industry: 2,
        num: undefined,
        istext: 1,
        dialogue: 1,
        lastyeartotal: undefined,
        import: 100,
        state: 0,
        compatibility: undefined
      },
      compatibilityList: [
        {
          productkey: 1,
          productName: "0.5以下"
        },
        {
          productkey: 2,
          productName: "0.5 ~ 0.8"
        },
        {
          productkey: 3,
          productName: "0.8以上"
        }
      ],
      compatibilityflag: false,
      lastYearTotalList: [
        {
          productkey: 1,
          productName: "0 ~ 3000"
        },
        {
          productkey: 2,
          productName: "3000 ~ 5000"
        },
        {
          productkey: 3,
          productName: "5000 ~ 10000"
        },
        {
          productkey: 4,
          productName: "10000"
        }
      ],
      yearList: [
        {
          productkey: 1,
          productName: "一年以下"
        },
        {
          productkey: 2,
          productName: "一到三年"
        },
        {
          productkey: 3,
          productName: "三年以上"
        }
      ],
      industryList: [
        {
          productkey: 2,
          productName: "未识别"
        },
        {
          productkey: 1,
          productName: "已识别"
        }
      ],
      numList: [
        {
          productkey: 1,
          productName: "1"
        },
        {
          productkey: 2,
          productName: "2"
        },
        {
          productkey: 3,
          productName: "3"
        },
        {
          productkey: 4,
          productName: "4-9"
        },
        {
          productkey: 5,
          productName: "10-19"
        },
        {
          productkey: 6,
          productName: "20及以上"
        }
      ],
      istextList: [
        {
          productkey: 2,
          productName: "是"
        },
        {
          productkey: 1,
          productName: "否"
        }
      ],
      dialogueList: [
        {
          productkey: 1,
          productName: "有"
        },
        {
          productkey: 2,
          productName: "无"
        }
      ],
      importList: [
        {
          productkey: 100,
          productName: "100"
        },
        {
          productkey: 200,
          productName: "200"
        },
        {
          productkey: 300,
          productName: "300"
        },
        {
          productkey: 400,
          productName: "400"
        }
      ],
      stateList: [
        {
          productkey: 0,
          productName: "未质检"
        },
        {
          productkey: 1,
          productName: "已质检"
        },
        {
          productkey: 2,
          productName: "暂不质检"
        },
        {
          productkey: 3,
          productName: "机器弱质检"
        },
        {
          productkey: 4,
          productName: "机器强质检"
        }
      ],
      data: [],
      noprocessingdata: [],
      locale: {},
      columns: [
        {
          title: "序号",
          dataIndex: "num",
          key: "num",
          align: "left",
          width: 50
        },
        {
          title: "公司名称",
          dataIndex: "company_name",
          key: "company_name",
          align: "left",

          width: 150
        },
        {
          title: "账号类型",
          //dataIndex: "account_type",
          key: "account_type",
          align: "left",
          scopedSlots: { customRender: "account_type" },
          width: 150
        },
        {
          title: "行业类型",
          dataIndex: "industry",
          key: "industry",
          align: "left",
          width: 150
        },
        {
          title: "注册时间",
          dataIndex: "register_time",
          key: "register_time",
          align: "left",
          width: 150,
          sorter: (a, b) => a.changeTime - b.changeTime
        },
        {
          title: "质检时间",
          dataIndex: "check_time",
          key: "check_time",
          align: "left",
          width: 150
        },
        {
          title: "操作",
          dataIndex: "exit",
          key: "exit",
          align: "left",
          width: 150,
          scopedSlots: { customRender: "exit" }
        }
      ],
      //分页
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          //   this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          this.pagination.current = current;
          this.pagination.pageSize = size;
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;

          //   this.paramsCheck(params);
          //   this.regUserList(params);
          //   this.getList()
        } // 点击页码事件
      }
    };
  },
  methods: {
    changeindustry(key) {
      //  console.log(this.value);
      let child_arr = "";
      if (key == undefined || key == 0) {
        this.industry_child_flag = true;
        this.child_value = "";
        //  console.log("111");
      } else {
        this.industry_child_flag = false;
        this.industry_list.forEach(item => {
          if (item.id == key) {
            this.industry_child_list = item.second_level;
            this.child_value = item.second_level[0].id;
            item.second_level.forEach(res => {
              child_arr += res.id + ",";
            });
          }
        });
      }
      this.child_arr = child_arr.substring(0, child_arr.length - 1);
      //  console.log(this.child_arr);
    },
    onChange1(value) {
      this.value = value;
      //console.log(this.value);
      // console.log(this.value[this.value.length - 1]);
    },
    displayRender({ labels }) {
      //this.value = labels[labels.length - 1];
      return labels[labels.length - 1];
      //1.caopi 2.caoxiu 3.guohuai 4.wangping 5.guotu 6.caoren 7.eryuan 8.shenlu
    },
    confirm() {
      var _this = this;
      this.$confirm({
        title: "确定要导入数据吗",
        okText: "确认",
        cancelText: "取消",
        onOk() {
          _this.improtdata(2);
        }
      });
    },
    cleardatamodal() {
      var _this = this;
      this.$confirm({
        title: "确定要清除数据吗",
        okText: "确认",
        cancelText: "取消",
        onOk() {
          _this.cleardata();
        }
      });
    },
    // 表格宽度
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w = w + i.width + 20;
        //console.log(w);
      }

      return w;
    },
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 450;
    },

    //筛选时间
    change_quality_time(date, dateString) {
      //console.log(date, dateString);
      this.quality_time = dateString;
      //console.log(this.quality_time);
    },
    disabledDate(current) {
      return current && current > moment().endOf("day");
    },
    //清除列表
    cleardata() {
      this.table_spinning = true;
      // console.log(this.data);
      var arrid = "";
      this.data.forEach(item => {
        arrid += item.company_id + ",";
      });
      // console.log(arrid);
      var data = {
        user_id: this.$store.state.userinfo[1].user.usermgtUserId,
        company_ids: arrid.substring(0, arrid.length - 1)
      };
      this.$http
        .post(
          "/portrayal/Inspection/clearTempIndustry",
          this.qs.stringify(data)
        )
        .then(res => {
          //.log(res);
          if (res.data.code == 101) {
            this.$message.success("清除数据成功");
            this.noprocessing();
          } else {
            this.$message.error(res.data.errorMsg);
            this.table_spinning = false;
          }
        });
    },
    //改变状态列表
    changestate(e) {
      //console.log(e);
      if (e == 2) {
        this.industy_flag = false;
        this.value = [];
      } else {
        this.industy_flag = true;
      }
    },
    changecompatibility(e) {
      //console.log(e);
      if (e == 3) {
        this.compatibilityflag = true;
        this.quality_time_flag = false;
        this.searchList.compatibility = 1;
      } else if (e == 1) {
        this.compatibilityflag = false;
        this.quality_time_flag = true;
      } else {
        this.compatibilityflag = false;
        this.quality_time_flag = false;
        this.searchList.compatibility = undefined;
      }
      this.quality_time = undefined;
    },
    //未质检完成的数据
    noprocessing() {
      this.table_spinning = true;
      var data = {
        user_id: this.$store.state.userinfo[1].user.usermgtUserId
      };
      this.$http
        .post("/portrayal/Inspection/getTempIndustry", this.qs.stringify(data))
        .then(res => {
          if (res.data.code == 101) {
            res.data.result.forEach((item, index) => {
              item.num = index + 1;
            });
            this.data = res.data.result;
            this.noprocessingdata = res.data.result;
            this.data.forEach(item => {
              if (item.changeTime) {
                item.changeTime = this.changeTime(item.register_time);
              }
              if (item.check_time) {
                item.check_time = this.format(item.check_time);
              }
            });
            this.table_spinning = false;
          } else {
            this.table_spinning = false;
            this.data = [];
          }
        });
    },
    //去质检结果页面
    tozhi(id, index) {
      //console.log(index);
      this.$router.push({
        name: "Inspectionresults",
        params: {
          id: id,
          data: this.data,
          serch: this.searchList,
          index: index
        }
      });
    },

    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    format(shijianchuo) {
      //shijianchuo是整数，否则要parseInt转换
      var time = new Date(shijianchuo);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      var h = time.getHours();
      var mm = time.getMinutes();
      var s = time.getSeconds();
      return (
        y +
        "-" +
        this.add0(m) +
        "-" +
        this.add0(d) +
        " " +
        this.add0(h) +
        ":" +
        this.add0(mm) +
        ":" +
        this.add0(s)
      );
    },

    //日期转数字
    changeTime(option) {
      // currentTime当前时间，假设是 2019-7-2 19:03
      var currentTime = option;
      var reg = new RegExp("-", "g"); //去掉时间里面的-
      var a = currentTime.replace(reg, "");
      var regs = new RegExp(" ", "g"); //去掉时间里面的空格
      var b = a.replace(regs, "");
      var regss = new RegExp(":", "g"); //去掉时间里面的:冒号
      var c = b.replace(regss, "");
      return c;
    },
    //导入数据
    improtdata(index) {
      this.noprocessingdata = ["1"];
      //console.log(this.value, this.child_value, this.child_arr);
      this.table_spinning = true;
      var data = {
        year: this.searchList.year,
        //industryIdentification: this.searchList.industry,
        userNum: this.searchList.num,
        isTest: this.searchList.istext,
        queryNum: this.searchList.import,
        quality_test_state: this.searchList.state,
        user_id: this.$store.state.userinfo[1].user.usermgtUserId,
        industry_id: this.child_value
          ? this.child_value
          : this.value
          ? this.child_arr
          : 0,
        //industry_id: 0,
        parent_industry_id: this.value,
        last_year_total: this.searchList.lastyeartotal,
        matching_degree: this.searchList.compatibility,
        check_time: this.quality_time
      };
      this.$http
        .post("portrayal/Inspection/industry", this.qs.stringify(data))
        .then(res => {
          //console.log(res);
          //console.log(Array.from(res.data));
          //var list = Array.from(res.data);
          if (res.data.code == 101) {
            res.data.result.forEach((item, index) => {
              item.num = index + 1;
              if (this.searchList.istext == 1) {
                item.accounttype = "公司账号";
              } else {
                item.accounttype = "测试账号";
              }
            });
            this.data = res.data.result;
            this.noprocessingdata = res.data.result;
            this.data.forEach(item => {
              if (item.changeTime) {
                item.changeTime = this.changeTime(item.register_time);
              }
              if (item.check_time) {
                item.check_time = this.format(item.check_time);
              }
            });

            if (index == 2) {
              if (this.data.length > 0) {
                this.$message.success("成功导入" + this.data.length + "条数据");
              } else {
                this.$message.success("暂无数据");
              }
            }

            this.table_spinning = false;
          } else {
            this.data = [];
            this.table_spinning = false;
            this.$message.error(res.data.errorMsg);
          }
        });
    },
    //重置导入条件
    commit() {
      //this.data = [];
      this.searchList = {
        year: undefined,
        industry: undefined,
        num: undefined,
        istext: 2,
        dialogue: undefined,
        import: 100,
        state: 0
      };
    }
  },
  mounted() {
    //console.log(this.$store.state.userinfo);
    document.title = "人工质检";
    this.hasPermissions = this.$store.state.pagepermission;
    this.gettablewidth();
    if (this.$route.params.searchcan) {
      this.searchList = this.$route.params.searchcan;
      if (this.$route.params.searchcan.industry == 1) {
        this.industy_flag = true;
      } else {
        this.industy_flag = false;
      }
    }
    //console.log(this.$route);
    //this.improtdata(1);
    this.noprocessing();
    this.$http.post("portrayal/industry/list").then(res => {
      //console.log(res);
      if (res.status == 200) {
        this.industry_list = res.data.result;
        this.industry_list.push({
          industry_name: "其他",
          industry_level: 1,
          id: 0,
          second_level: []
        });
        this.industry_child_list = undefined;
        this.value = undefined;
        this.child_value = undefined;
        //let child_arr = "";
        // this.industry_child_list.forEach(item => {
        //   child_arr += item.id + ",";
        // });
        // this.child_arr = child_arr.substring(0, child_arr.length - 1);
      } else {
        this.industry_list = [];
        this.industry_child_list = [];
      }
    });
  },
  // beforeRouteLeave(to, from, next) {
  //   if (to.path != "/Inspectionresults") {
  //     from.meta.keepAlive = false;
  //     console.log("likai");
  //   }

  //   next();
  // },
  created() {
    //console.log(this.$route.meta.keepAlive);
  }
};
</script>

