  .ant-table-thead
  > tr
  > th
  .ant-table-column-sorter
  .ant-table-column-sorter-inner-full {
  margin-top: -0.6em !important;
}
/deep/ .form-box .form-box .ant-table-body::-webkit-scrollbar {
  width: 15px;
}
