import axios from "axios";
import qs from "qs";
import Vue from "vue";
import { message } from "ant-design-vue";

const clearRequest = {
  source: axios.CancelToken.source()
};

// 普通请求
const qapoolhttp = axios.create({
  baseURL: location.href.includes("53kf.com")
    ? "https://newknowledgebase.53kf.com"
    : "https://newknowledgebase.71baomu.com",
  timeout: 30000,
  headers: {
    // 'Content-Type': 'application/json'
    "Content-Type": "application/x-www-form-urlencoded"
  },
  withCredentials: true //请求头带cookie
});

// 配置发送请求拦截器
qapoolhttp.interceptors.request.use(
  config => {
    config.cancelToken = clearRequest.source.token; // 这句很重要
    if (config.method === "post") {
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 添加响应拦截器(当响应回复回登陆页时跳转)
qapoolhttp.interceptors.response.use(
  function(response) {
    // 是否返回登陆页
    let origin = window.location.origin;
    if (
      response.data.code == 101 ||
      response.data.code == 230 ||
      response.data.code == 231 ||
      response.data.code == 232 ||
      response.data.code == 233
    ) {
    } else if (response.data.code == "201") {
      if (window.location.href.includes("53kf.com")) {
        window.location.href =
          "https://usermanagement.53kf.com/#/?url=" +
          "http://cloudservice.53kf.com/#/Industry_QApool";
      } else {
        window.location.href =
          "https://usermanagement.71baomu.com/#/?url=" +
          "http://cloudservice.71baomu.com/#/Industry_QApool";
      }
      return new Promise(() => {}); // 不进入请求返回层，防止弹出提示
    } else {
      message.error(response.data.message);
    }
    return response;
  },
  function(error) {
    return Promise.reject(error);
  }
);

// 默认请求
const axiosrequest = axios.create({
  timeout: 30000,
  headers: {
    // 'Content-Type': 'application/json'
    "Content-Type": "application/x-www-form-urlencoded"
  },
  withCredentials: true //请求头带cookie
});

// 配置发送请求拦截器
axiosrequest.interceptors.request.use(
  config => {
    config.cancelToken = clearRequest.source.token; // 这句很重要
    if (config.method === "post") {
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 添加响应拦截器(当响应回复回登陆页时跳转)
axiosrequest.interceptors.response.use(
  function(response) {
    // 是否返回登陆页
    let origin = window.location.origin;
    if (response.data.code == 101) {
    } else if (response.data.code == "201") {
      if (window.location.href.includes("53kf.com")) {
        window.location.href =
          "https://usermanagement.53kf.com/#/?url=" +
          "http://cloudservice.53kf.com/#/Industry_QApool";
      } else {
        window.location.href =
          "https://usermanagement.71baomu.com/#/?url=" +
          "http://cloudservice.71baomu.com/#/Industry_QApool";
      }
      return new Promise(() => {}); // 不进入请求返回层，防止弹出提示
    } else {
      message.error(response.data.message);
    }
    return response;
  },
  function(error) {
    return Promise.reject(error);
  }
);

export { qapoolhttp, axiosrequest };



