<template>
  <div>
    <div
      v-if="hasPermissions == true"
      style="width:100%;height:calc(100% - 50px)"
    >
      <div class="interface-head">
        队列分析
      </div>
      <div class="page-box">
        <div class="clear search-condition-box">
          <div class="search-condition-box-item" style="width:240px">
            <a-select
              v-model="searchList.groupName"
              style="width: 230px;display: inline-block;margin-left: 0px;"
              allowClear
              placeholder="产品"
              @change="mealChange"
            >
              <a-select-option
                v-for="(item, index) in mealList"
                :key="index"
                :value="item.productKey"
                :title="item.productName"
                >{{ item.productName }} 组</a-select-option
              >
            </a-select>
          </div>
          <div class="search-condition-box-item" style="width:140px;">
            <a-month-picker
              @change="timeChange"
              v-model="searchList.statisticsTime"
              placeholder="选择日期"
            />
          </div>
          <div
            class="search-condition-box-item"
            style="width: auto;margin-left:10px"
          >
            <a-button @click="screenClear">取消筛选</a-button>
            <a-button type="primary" @click="screenBtn">筛选</a-button>
          </div>
        </div>

        <div class="package-price">
          <div class="package-price-line clear">
            开户量：
            <span>{{ this.datalist.customerTotal }}</span> ； 开通坐席的客户量：
            <span>{{ this.datalist.openSeatCustomer }}</span> ； 开通坐席总数：
            <span>{{ this.datalist.seatNumTotal }}</span>
            个；
          </div>
          <div class="package-price-line clear">
            云客服消费金额：
            <span>{{
              this.datalist.moneyAllTotal ? this.datalist.moneyAllTotal : 0
            }}</span>
            元；
          </div>
          <div class="package-price-line clear">
            坐席消费金额：
            <span>{{
              this.datalist.seatMoneyTotal ? this.datalist.seatMoneyTotal : 0
            }}</span>
            元；坐席外消费金额：
            <span>{{
              this.datalist.seatOutMoneyTotal
                ? this.datalist.seatOutMoneyTotal
                : 0
            }}</span>
            元；
          </div>
        </div>

        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              :columns="columns"
              :locale="locale"
              :dataSource="data"
              bordered
              :pagination="pagination"
              :scroll="{ x: gettablewidth(), y: gettableheight() }"
            >
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>
    <div v-else class="no-permission" style="height:calc(100% - 50px)">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>
<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  data() {
    return {
      hasPermissions: false,
      searchList: {
        statisticsTime: "", //日期双向绑定
        groupName: "", //二级菜单双向绑定
        pageNum: 1,
        pageSize: 20
      },

      mealList: [
        {
          productKey: 202008,
          productName: 202008
        },
        {
          productKey: 202009,
          productName: 202009
        }
      ],
      datalist: {
        payMoneyAll: "0",
        payNewCouponsAll: "0",
        moneyAllTotal: "0",
        seatMoneyTotal: "0",
        seatOutMoneyTotal: "0",
        openSeatCustomer: "0"
      },
      table_spinning: false, //相当于加载按钮
      columns: [
        {
          title: "序号",
          dataIndex: "num",
          key: "num",
          show: true,
          width: 50
        },
        {
          title: "公司名称",
          dataIndex: "companyName",
          key: "companyName"
        },
        {
          title: "开通坐席数",
          dataIndex: "seatNum",
          align: "right",
          key: "seatNum"
        },
        {
          title: "坐席消费金额",
          dataIndex: "seatMoney",
          align: "right",
          key: "seatMoney"
        },
        {
          title: "云客服消费金额",
          dataIndex: "moneyAll",
          align: "right",
          key: "moneyAll"
        }
      ],
      locale: {},
      data: [],
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1,
        defaultPageSize: 20,
        total: "",
        showTotal: total => `共 ${total} 条数据`,
        showSizeChanger: true,
        pageSizeOptions: ["20", "40", "100"],
        onShowSizeChange: (current, pageSize) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          this.getdata(params);
          // this.getList()  //显示列表的接口名称
        },
        onChange: (current, size) => {
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          this.pagination.current = current;
          this.pagination.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.getdata(params);
          // this.getList()
        } // 点击页码事件
      }
    };
  },
  methods: {
    //禁用日期
    disabledDate(current) {
      //console.log(current)
      // console.log(moment().endOf('day'))
      //return current && current < moment().endOf('day');
      // console.log(this.searchList.groupName)
      // return current < this.searchList.groupName
    },
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 330;
      // odiv.offsetWidth - odiv.clientWidth;
    },
    //获取列表数据
    getdata(params) {
      this.table_spinning = true;
      getData.getqueuean(params).then(res => {
        this.table_spinning = false;
        //console.log(res)
        if (res.code == 200) {
          this.data = res.data.list;
          this.datalist = res.data;
          this.pagination.total = res.data.total;
          this.data.forEach((item, index) => {
            item.num = index + 1;
            if (!item.seatNum) {
              item.seatNum = 0;
            }
            if (!item.seatMoney) {
              item.seatMoney = 0;
            }
            if (!item.moneyAll) {
              item.moneyAll = 0;
            }
          });
        } else {
          this.data = [];
          this.datalist = [];
          this.pagination.total = 0;
          this.pagination.pageNum = 1;
        }
      });
    },
    //改变日期
    timeChange(e) {
      //console.log(this.timechangestart(e._d))
      this.searchList.statisticsTime = this.timechangestart(e._d);
    },
    //改变二级菜单
    mealChange() {
      //console.log(this.searchList.groupName)
    },

    //取消筛选
    screenClear() {
      this.pagination.pageNum = 1;
      this.searchList.groupName = this.timechangeend(new Date());
      this.searchList.statisticsTime = this.timechangestart(new Date());
      var data = {
        groupName: this.timechangeend(new Date()),
        pageNum: 1,
        pageSize: 20,
        statisticsTime: this.searchList.statisticsTime
      };
      this.getdata(data);
    },

    //选择筛选
    screenBtn() {
      this.pagination.pageNum = 1;
      var data = {
        groupName: this.searchList.groupName,
        pageNum: 1,
        pageSize: 20,
        statisticsTime: this.searchList.statisticsTime
      };
      this.getdata(data);
    },
    //补零操作
    test(str) {
      if (str < 10) {
        return "0" + str;
      } else {
        return str;
      }
    },
    //格式转化
    timechangestart(str) {
      return str.getFullYear() + "-" + this.test(str.getMonth() + 1);
    },
    timechangeend(str) {
      return str.getFullYear() + this.test(str.getMonth() + 1).toString();
    },
    //为mealList添加对象
    changemealList() {
      //console.log(this.searchList.groupName)
      this.mealList = [];
      var str = parseInt(this.timechangeend(new Date()));
      //var str = 202501
      for (var i = 202008; i <= str; i++) {
        //console.log(i)
        var erarr = parseInt(i.toString().substr(4, 2));
        //console.log(erarr)
        if (erarr <= 12 && erarr >= 1) {
          //console.log(i)
          this.mealList.push({
            productKey: i,
            productName: i
          });
        }
      }
      //console.log(this.mealList)
    }
  },

  mounted() {
    this.searchList.groupName = this.timechangeend(new Date());
    //this.searchList.groupName = '202008 组'
    this.searchList.statisticsTime = this.timechangestart(new Date());
    //this.searchList.statisticsTime = '2020-08'
    var data = {
      groupName: this.searchList.groupName,
      pageNum: 1,
      pageSize: 20,
      statisticsTime: this.searchList.statisticsTime
    };
    this.getdata(data);
    this.changemealList();
    this.hasPermissions = this.$store.state.pagepermission;
  }
};
</script>
<style scoped></style>



