<template>
  <div class="interfaceList">
    <div v-if="hasPermissions == true" style="width:100%;height:100%">
      <div class="interface-head">第三方产品结算月报表</div>
      <div class="interface_list_filter">
        <a-month-picker
          class="filter_item"
          v-model="select_month_str"
          @change="changeMonth"
          placeholder="选择月"
        />
        <a-select
          class="filter_item"
          v-model="search_info.productName"
          placeholder="产品"
          style="width: 120px"
          allowClear
          @change="productChange"
        >
          <a-select-option
            v-for="item in productList"
            :key="item.productKey"
            :value="item.productKey"
            :title="item.productName"
            ><div>{{ item.productName }}</div></a-select-option
          >
        </a-select>
        <a-select
          class="filter_item"
          v-model="search_info.mealSort"
          placeholder="套餐分类"
          @change="mealChange"
          allowClear
          style="width: 120px"
          :disabled="productactive"
        >
          <template v-for="item in mealList">
            <a-select-option
              :key="item.mealSort"
              :value="item.mealSort"
              :title="item.mealSortName"
              v-if="item.mealSortName"
              ><div>{{ item.mealSortName }}</div></a-select-option
            >
          </template>
        </a-select>

        <a-button
          class="filter_item"
          type="primary"
          :loading="getReportBtnLoad"
          @click="getThirdReport"
          >生成报表</a-button
        >
        <a-button
          class="filter_item"
          type="primary"
          :loading="getReportBtnLoad1"
          @click="openchoosereportmodal"
          >获取报表</a-button
        >
        <!-- <a-button class="filter_item" @click="shareModal=true">对外分享</a-button> -->
      </div>
      <div class="interface_content">
        <iframe
          width="100%"
          style="height: calc(100vh - 120px);"
          height="calc(100vh - 102px)"
          frameborder="0"
          :src="iframe_reportaddress"
        ></iframe>
      </div>

      <!--  对外分享 -->
      <a-modal
        class="shareModal"
        width="462px"
        title="对外分享"
        @ok="shareModalConfirm"
        @cancel="closeshareModal"
        v-model="shareModal"
      >
        <template slot="footer">
          <a-button key="confirm" type="danger" @click="shareModalConfirm"
            >复制链接及密码</a-button
          >
          <a-button key="cancel" @click="closeshareModal">取消</a-button>
        </template>
        <div class="share_item">
          <span>有效时间：</span>
          <a-radio-group
            v-model="reportList.reportSecretValidity"
            @change="shareChange"
            :options="plainOptions"
          />
        </div>
        <div class="share_item">
          <span>查看网址：</span>
          <a-input
            placeholder="网址"
            v-model="reportList.reportAddress"
            style="width:325px"
            disabled
          />
        </div>
        <div class="share_item">
          <span>查看密码：</span>
          <a-input
            placeholder="密码"
            disabled
            v-model="reportList.reportSecret"
            style="width:325px"
          />
        </div>
      </a-modal>

      <!-- 获取报表 -->
      <a-modal
        class="getreportmodal"
        title="已生成报表"
        width="1000px"
        v-model="getreportmodal"
        :footer="null"
      >
        <div class="getreport_content">
          <div class="small_list">
            <a-table
              rowKey="id"
              :columns="hasreportlist_columns"
              :data-source="hasreportlist_listdata"
              :pagination="false"
              :scroll="{ y: 300 }"
            >
              <div
                slot="productName"
                class="texthidden"
                style="width:80px"
                :title="text"
                slot-scope="text"
              >
                {{ text }}
              </div>
              <div
                slot="mealSortName"
                class="texthidden"
                style="width:80px"
                :title="text"
                slot-scope="text"
              >
                {{ text }}
              </div>
              <span slot="reportSecret" slot-scope="text, record">
                <a-input
                  placeholder="密码"
                  v-model="record.reportSecret"
                  style="width:150px"
                />
              </span>
              <div
                slot="reportAddress"
                class="texthidden"
                style="width:130px"
                :title="text"
                slot-scope="text"
              >
                {{ text }}
              </div>
              <div slot="reportSecretValidity" slot-scope="text, record">
                <a-select
                  v-model="record.reportSecretValidity"
                  style="width: 80px"
                >
                  <a-select-option
                    v-for="i in plainOptions"
                    :value="i.value"
                    :key="i.value"
                    >{{ i.label }}</a-select-option
                  >
                </a-select>
              </div>
              <span slot="operation" slot-scope="record">
                <span class="click_btn" @click="getcurhasreport(record)"
                  >查看</span
                >
                <span
                  class="click_btn"
                  style="margin-left:10px;"
                  @click="copycurreport(record)"
                  >复制链接及密码</span
                >
              </span>
            </a-table>
          </div>
        </div>
      </a-modal>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import * as getData from "@/api/api";
import moment from "moment";
export default {
  name: "thirdMonthReport",
  data() {
    return {
      hasPermissions: false,
      select_month_str: moment(),
      search_info: {
        statisticsTime: moment().format("YYYY-MM"),
        mealSort: undefined,
        mealSortName: undefined,
        productKey: undefined,
        productName: undefined
      },
      productactive: true,
      getReportBtnLoad: false,
      getReportBtnLoad1: false,
      productList: [],
      mealList: [],
      reportList: {},
      reportList_backup: {},
      iframe_reportaddress: "",
      shareModal: false,
      plainOptions: [
        { label: "1h", value: "1" },
        { label: "6h", value: "6" },
        { label: "12h", value: "12" },
        { label: "1d", value: "24" },
        { label: "3h", value: "36" },
        { label: "5h", value: "60" }
      ],
      reportinterval: undefined,
      getreportmodal: false,
      hasreportlist_columns: [
        {
          title: "月份",
          dataIndex: "statisticsTime",
          width: 70,
          scopedSlots: { customRender: "statisticsTime" }
        },
        {
          title: "产品",
          dataIndex: "productName",
          width: 100,
          scopedSlots: { customRender: "productName" }
        },
        {
          title: "套餐分类",
          dataIndex: "mealSortName",
          width: 100,
          scopedSlots: { customRender: "mealSortName" }
        },
        {
          title: "网址",
          dataIndex: "reportAddress",
          width: 150,
          scopedSlots: { customRender: "reportAddress" }
        },
        {
          title: "密码",
          dataIndex: "reportSecret",
          width: 170,
          scopedSlots: { customRender: "reportSecret" }
        },
        {
          title: "有效时间",
          dataIndex: "reportSecretValidity",
          width: 90,
          scopedSlots: { customRender: "reportSecretValidity" }
        },
        {
          title: "到期时间",
          dataIndex: "expDay",
          width: 130,
          scopedSlots: { customRender: "expDay" }
        },
        {
          title: "操作",
          key: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      hasreportlist_listdata: []
    };
  },
  created() {
    // getData.getThirdReport(this.search_info).then(res => {
    //   if(res.code == 200){
    //     this.reportList = res.data;
    //   }
    // })
  },
  mounted() {
    document.title = "第三方产品结算月报表";
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
      getData.getThirdMealList().then(res => {
        if (res.code == 200) {
          this.productList = res.data;
        }
      });
    }
  },
  methods: {
    productChange(value, option) {
      this.search_info.mealSort = undefined;
      if (value == undefined) {
        this.productactive = true;
      }
      for (let item of this.productList) {
        if (value == item.productKey) {
          this.mealList = item.mealList;
          //console.log(this.mealList)
          // this.mealList.forEach((item,index) =>{
          //   //console.log(item)
          //   if(item.mealSortName == ''){
          //     this.mealList.splice(index,1)
          //   }else{
          //   }
          //   //console.log(this.mealList)
          // })
          this.productactive = false;
          this.search_info.productKey = item.productKey;
          this.search_info.productName = item.productName;
        }
      }
      // let list = this.productList.filter(ele => ele.productName == value)[0];
      // this.mealList = list.mealList;
      // this.search_info.productKey = value;
      // this.search_info.productName = option.key;
    },
    mealChange(value) {
      for (let item of this.mealList) {
        if (value == item.mealSort) {
          this.search_info.mealSort = item.mealSort;
          this.search_info.mealSortName = item.mealSortName;
        }
      }
      // let list = this.mealList.filter(ele => ele.mealSortName == value)[0];
      // this.search_info.mealSort = list.mealSort;
      // this.search_info.mealSortName = list.mealSortName;
    },
    changeMonth(date, dateStr) {
      this.search_info.statisticsTime = dateStr;
      this.select_month_str = date;
    },
    // 获取报表打开modal选择已生成报表
    openchoosereportmodal() {
      this.getreportmodal = true;
      getData.selectHasReport({}).then(res => {
        this.hasreportlist_listdata = res.data;
      });
    },
    //   生成第三方报表
    getThirdReport() {
      if (this.search_info.productKey == undefined) {
        this.$message.error("请选择产品");
        return;
      }
      if (this.search_info.mealSort == undefined) {
        this.$message.error("请选择套餐分类");
        return;
      }
      this.getReportBtnLoad = true;
      getData
        .getThirdReport(this.search_info)
        .then(res => {
          if (res.code == 200) {
            this.reportList = res.data;
            this.reportList_backup = JSON.parse(JSON.stringify(res.data));
            this.interval_selectThirdReport();
          }
        })
        .catch(() => {
          this.$message.error("系统错误");
        });
    },
    // 获取报表  查看
    getcurhasreport(item) {
      //   let data = {
      //     statisticsTime: item.statisticsTime,
      //     mealSort: item.mealSort,
      //     mealSortName: item.mealSortName,
      //     productKey: item.productKey,
      //     productName: item.productName
      //   };
      //   console.log(item);
      let data = {
        reportSecret: item.reportSecret,
        reportUniqueCode: item.reportUniqueCode
      };
      getData.selectThirdReport(data).then(res => {
        if (res.code == 200) {
          this.iframe_reportaddress =
            res.data.reportAddress + "?report_code=" + res.data.reportSecret;
          this.getReportBtnLoad1 = false;
          this.reportList = res.data;
          this.reportList_backup = JSON.parse(JSON.stringify(res.data));
        } else if (res.code == 900) {
          this.getReportBtnLoad1 = false;
          this.$message.error("数据正在生成或不存在，请先生成报表");
        }
        this.getreportmodal = false;
      });
    },
    // 获取报表   复制网址和密码
    copycurreport(item) {
      let info =
        "查看网站:" +
        item.reportAddress +
        "查看密码:" +
        item.reportSecret +
        ",有效期:" +
        item.reportSecretValidity;
      try {
        if (window.clipboardData) {
          window.clipboardData.clearData();
          window.clipboardData.setData("Text", info);
        } else {
          let dom = document.createElement("input");
          dom.value = info;
          document.body.appendChild(dom);
          dom.select();
          document.execCommand("copy");
          dom.blur();
          document.body.removeChild(dom);
          dom = null;
        }
      } catch (e) {
        this.$message.error("网络错误，生成失败");
      }
      let data = {
        reportAddress: item.reportAddress,
        reportSecret: item.reportSecret,
        reportSecretValidity: item.reportSecretValidity,
        reportUniqueCode: item.reportUniqueCode
      };
      getData
        .editScrect(data)
        .then(res => {
          if (res.code == 200) {
            this.$message.success("复制成功");
          } else {
            this.$message.error(res.message);
          }
          this.getreportmodal = false;
        })
        .catch(() => {
          this.$message.error("系统错误");
        });
    },
    // 获取第三方报表
    // getThirdReport1() {
    //   if (this.search_info.productKey == undefined) {
    //     this.$message.error("请选择产品");
    //     return;
    //   }
    //   if (this.search_info.mealSort == undefined) {
    //     this.$message.error("请选择套餐分类");
    //     return;
    //   }
    //   this.getReportBtnLoad1 = true;
    //   getData.selectThirdReport(this.search_info).then(res => {
    //     if (res.code == 200) {
    //       this.iframe_reportaddress =
    //         res.data.reportAddress + "?report_code=" + res.data.reportSecret;
    //       this.getReportBtnLoad1 = false;
    //       this.reportList = res.data;
    //       this.reportList_backup = JSON.parse(JSON.stringify(res.data));
    //     } else if (res.code == 900) {
    //       this.getReportBtnLoad1 = false;
    //       this.$message.error("数据正在生成或不存在，请先生成报表");
    //     }
    //   });
    // },
    //   查询第三方报表是否完成
    select_ThirdReport() {
      // console.log(this.search_info,this.reportList)
      let data = {
        reportSecret: this.reportList.reportSecret,
        reportUniqueCode: this.reportList.reportUniqueCode
      };
      getData.selectThirdReport(data).then(res => {
        if (res.code == 200) {
          clearInterval(this.reportinterval);
          this.iframe_reportaddress =
            res.data.reportAddress + "?report_code=" + res.data.reportSecret;
          this.getReportBtnLoad = false;
          this.reportList = res.data;
          this.reportList_backup = JSON.parse(JSON.stringify(res.data));
        }
      });
    },
    // 每30秒请求一次接口查询第三方报表是否完成
    interval_selectThirdReport() {
      let report_timeout = setTimeout(() => {
        this.select_ThirdReport();
      }, 10000);
      this.reportinterval = setInterval(() => {
        if (report_timeout) {
          clearTimeout(report_timeout);
        }
        this.select_ThirdReport();
      }, 30000);
    },
    shareChange(e) {},
    // 复制网址和密码
    shareModalConfirm() {
      let info =
        "查看网站:" +
        this.reportList.reportAddress +
        ",查看密码:" +
        this.reportList.reportSecret +
        ",有效期:" +
        this.reportList.reportSecretValidity;
      try {
        if (window.clipboardData) {
          window.clipboardData.clearData();
          window.clipboardData.setData("Text", info);
        } else {
          let dom = document.createElement("input");
          dom.value = info;
          document.body.appendChild(dom);
          dom.select();
          document.execCommand("copy");
          dom.blur();
          document.body.removeChild(dom);
          dom = null;
        }
      } catch (e) {
        this.$message.error("网络错误，生成失败");
      }
      getData
        .editScrect(this.reportList)
        .then(res => {
          if (res.code == 200) {
            this.$message.success("复制成功");
          } else {
            this.$message.error(res.message);
          }
        })
        .catch(() => {
          this.$message.error("系统错误");
        });
    },
    // 关闭对外分享model
    closeshareModal() {
      this.shareModal = false;
      this.reportList = JSON.parse(JSON.stringify(this.reportList_backup));
    }
  }
};
</script>

<style scoped lang="scss">
.interfaceList {
  .interface_list_filter {
    padding: 10px 0 10px 20px;
    .filter_item {
      margin-right: 10px;
    }
  }
  .interface_content {
    width: 100%;
  }
}
.getreportmodal {
  .getreport_content {
    padding: 15px;
    .modal_item {
      height: 32px;
      line-height: 32px;
      margin-bottom: 15px;
    }
    .report_list {
      width: 100%;
      height: 100%;
    }
  }
  .ant-btn-danger {
    background: #f5222d;
    color: #fff;
  }
}
</style>



