import axios from "axios";
import qs from 'qs'

let per_http = axios.create({
    timeout: 10000,
    headers: {
        'Content-Type': 'application/json;charset=utf-8'
        // 'Content-Type': 'application/x-www-from-urlencoded'
    },
    baseURL: location.href.includes('53kf.com') ? 'http://cloudservice.53kf.com/ccs' : 'http://cloudservice.71baomu.com/ccs',
    withCredentials: true,//请求头带cookie
})
per_http.interceptors.request.use((config) => {
    if (config.method === 'post') {
        // config.data = qs.stringify(config.data)
    }
    return config;
})

per_http.interceptors.response.use((response) => {
    if (response.data.code == '201') {
        if (window.location.href.includes('53kf.com')) {
            window.location.href =
                "https://usermanagement.53kf.com";
        } else {
            window.location.href =
                "https://usermanagement.71baomu.com";
        }

        return new Promise(() => { })// 不进入请求返回层，防止弹出提示
    }
    return response;
}, (err) => {
    console.log(err);
    return err;
})
export {
    per_http,
    axios,
   
}



