        pccallBack: undefined,

        mobileinitiateWay: undefined,
        mobilekefuPic: undefined,
        mobilecallBack: undefined,
        interactive_menu: undefined,
        pull_visitor: undefined,
        invite_mandatory: undefined,

        facilitatorName: undefined,
        firstIndustry: undefined,
        industryList: [],
        industry: undefined
      },
      taglist: [
        {
          label: "使用中",
          value: "1"
        },
        {
          label: "功能新增",
          value: "2"
        },
        {
          label: "坐席增加",
          value: "3"
        },
        {
          label: "坐席减少",
          value: "4"
        },
        {
          label: "停用",
          value: "5"
        }
      ],
      meallist: [],
      set_age: '18'
    };
  },
  methods: {
    // 清除筛选
    clearsearchdata() {
      this.baseInfo_base = [];
      this.basecInfo_media = [];
      this.basecInfo_system = [];
      this.basecInfo_market = [];
      this.basecInfo_robot = [];
      this.basecInfo_else = [];
      this.cityList = {
        province: undefined,
        city: undefined
      };
      this.searchList = {
        dateTime: undefined,
        facilitatorName: undefined,
        firstIndustry: undefined,
        industry: undefined,
        industryList: [],
        customer_service: undefined,
        first_it: undefined,
        offline_priority: undefined,
        receive_priority: undefined,

        pcinitiateWay: undefined,
        pckefuPic: undefined,
        pccallBack: undefined,

        mobileinitiateWay: undefined,
        mobilekefuPic: undefined,
        mobilecallBack: undefined,
        interactive_menu: undefined,
        pull_visitor: undefined,
        invite_mandatory: undefined
      };
      this.stusa_value = undefined
      this.mea_name  = undefined
      // for (let i of this.meallist) {
      //   i.status = undefined;
      // }
    },
    // 地区省份选择onchange
    provenceChange(v) {
      if (v == undefined) {
        this.cityList.city = undefined;
      }
      for (let item of this.cities) {
        if (v == item.value) {
          this.cityList.city = undefined;
          this.chooseCities = item.children;
        }
      }
      this.searchdata_onchange();
    },
    // 地区城市选择onchange
    cityChange(data) {
      this.searchdata_onchange();
    },
    // 一级行业选择onchange
    industryChange(v) {
      if (v == undefined) {
        this.industryType = true;
      }
      this.searchList.industry = undefined;
      for (let item of this.industryList) {
        if (v == item.ccsIndustry.industryName) {
          this.industriesList = item.ccsIndustryLevelTwoList;
          if (
            item.ccsIndustryLevelTwoList == undefined ||
            item.ccsIndustryLevelTwoList == "" ||
            item.ccsIndustryLevelTwoList == null
          ) {
            this.industryType = true;
          } else {
            this.industryType = false;
          }
        }
      }
      this.searchdata_onchange();
    },
    onChange(date, dateString) {
      console.log(date, dateString);
      this.searchList.dateTime = dateString;
      this.searchdata_onchange();
    },
    // 基本信息onchange
    basecInfo_base_change(data) {
      this.searchdata_onchange();
    },
    // 每次筛选条件改变后传值给父组件
    searchdata_onchange(e) {
      let searchdata = {
        dateTime: undefined,
        isMobile: undefined,
        isWechat: undefined,
        isQq: undefined,
        isDeviceId: undefined,
        isFansId: undefined,
        isDeviceAuth: undefined,
        isPersonalAuth: undefined,
        isCompanyAuth: undefined,
        province: undefined,
        city: undefined,
        industry: undefined,
        industryList: [],
        facilitatorName: undefined,
        productList: [],
        distributiveRules: [],
        marketing: [],
        pcOptions: [],
        mobilesOptions: [],
        mediaDocking: [],
        systemOptions: [],
        robot: [],
        other: [],
        portraitBasic: null,
        functionUsing: null,
        productUsing: null
        // distributiveRules:{
        //   customer_service: undefined,
        //   first_it: undefined,
        //   offline_priority: undefined,
        //   receive_priority: undefined,
        // },
        // marketing:{
        //   lottery: undefined,
        //   exitToRetain: undefined,
        //   shortMessageSend: undefined,
        // },
        // pcOptions:{
        //   callBack: undefined,
        //   initiate: undefined,
        //   initiateWay: undefined,
        //   kefuPic: undefined,
        // },
        // mobilesOptions:{
        //   callBack: undefined,
        //   initiate: undefined,
        //   initiateWay: undefined,
        //   kefuPic: undefined,
        //   interactive_menu: undefined,
        // },
        // mediaDocking:{
        //   wechatPublic: undefined,
        //   appletOfWechat: undefined,
        //   appletOfBaidu: undefined,
        //   jinriToutiao: undefined,
        //   tencentSocialAdvertising: undefined,
        //   _360: undefined,
        //   kuaishou: undefined,
        // },
        // systemOptions:{
        //   serviceScheduling: undefined,
        //   useful_expressions: undefined,
        //   enclave_label: undefined,
        //   conversation_topics: undefined,
        //   visitors_forms: undefined,
        //   promote_links: undefined,
        // },
        // robot:{
        //   answerLibrary: undefined,
        //   cluesToAsk: undefined,
        // },
        // other:{
        //   app: undefined,
        //   developerAccount: undefined,
        //   handing_over: undefined
        // }
      };
      if (this.baseInfo_base.length !== 0) {
        searchdata.portraitBasic = "1";
        // 基本信息
        for (let i of this.baseInfo_base) {
          if (i == "mobile") {
            searchdata.isMobile = "1";
          } else if (i == "wechat") {
            searchdata.isWechat = "1";
          } else if (i == "qq") {
            searchdata.isQq = "1";
          } else if (i == "equimentId") {
            searchdata.isDeviceId = "1";
          } else if (i == "fansId") {
            searchdata.isFansId = "1";
          } else if (i == "localApprove") {
            searchdata.isDeviceAuth = "1";
          } else if (i == "personalApprove") {
            searchdata.isPersonalAuth = "1";
          } else if (i == "companyApprove") {
            searchdata.isCompanyAuth = "1";
          }
        }
      }
      // 地区
      if (this.cityList.province !== undefined) {
        searchdata.province = this.cityList.province;
        searchdata.city = this.cityList.city;
      }
      // 行业
      if (this.searchList.firstIndustry !== undefined) {
        if (this.searchList.industry !== undefined) {
          //searchdata.industry = this.searchList.industry;
          searchdata.industryList.push(this.searchList.industry)
        } else {
          //searchdata.industry = this.searchList.firstIndustry;
          this.industriesList.forEach(item =>{
            searchdata.industryList.push(item.id)
          })
        }
      }

      //时间
      if(this.searchList.dateTime !== undefined){
          searchdata.dateTime = this.searchList.dateTime
      }

      // 服务商
      if (this.searchList.facilitatorName !== undefined) {
        searchdata.facilitatorName = this.searchList.facilitatorName;
      }
      // 购买/功能标签
      // for (let i of this.meallist) {
      //   if (i.status !== undefined) {
      //     let k = {
      //       meal_name: i.mealName,
      //       meal_key: i.mealKey,
      //       status: i.status
      //     };
      //     searchdata.productList.push(k);
      //   }
      // }
      if(this.mea_name){
        searchdata.productList.push({
        meal_key:this.mea_name,
        status: this.stusa_value
      })
      }
      
      if (searchdata.productList.length > 0) {
        searchdata.productUsing = "1";
      }
      // 接待规则
      if (this.searchList.first_it !== undefined) {
        let item = {
          first_it: this.searchList.first_it
        };
        searchdata.distributiveRules.push(item);
      }
      if (this.searchList.customer_service !== undefined) {
        let item = {
          customer_service: this.searchList.customer_service
        };
        searchdata.distributiveRules.push(item);
      }
      if (this.searchList.offline_priority !== undefined) {
        let item = {
          offline_priority: this.searchList.offline_priority
        };
        searchdata.distributiveRules.push(item);
      }
      if (this.searchList.receive_priority !== undefined) {
        let item = {
          receive_priority: this.searchList.receive_priority
        };
        searchdata.distributiveRules.push(item);
      }
      // pc端设置
      if (
        this.searchList.pcinitiateWay !== undefined &&
        this.searchList.pcinitiateWay != 4
      ) {
        console.log(this.searchList.pcinitiateWay)
        let item = {
          initiate_way: this.searchList.pcinitiateWay
        };
        searchdata.pcOptions.push(item);
        // searchdata.pcOptions.initiateWay = this.searchList.pcinitiateWay;
      } else if (
        this.searchList.pcinitiateWay !== undefined &&
        this.searchList.pcinitiateWay == 4
      ) {
        let item = {
          initiate: "0"
        };
        searchdata.pcOptions.push(item);
      }
      if (this.searchList.pckefuPic !== undefined) {
        let item = {
          kefu_pic: this.searchList.pckefuPic
        };
        searchdata.pcOptions.push(item);
        // searchdata.pcOptions.kefuPic = this.searchList.pckefuPic;
      }
      if (this.searchList.pccallBack !== undefined) {
        let item = {
          call_back: this.searchList.pccallBack
        };
        searchdata.pcOptions.push(item);
        // searchdata.pcOptions.callBack = this.searchList.pccallBack;
      }
      // 移动端设置
      if (
        this.searchList.mobileinitiateWay !== undefined &&
        this.searchList.mobileinitiateWay != 3
      ) {
        let item = {
          initiate_way: this.searchList.mobileinitiateWay
        };
        searchdata.mobilesOptions.push(item);
        // searchdata.mobilesOptions.initiateWay = this.searchList.mobileinitiateWay;
      } else if (
        this.searchList.mobileinitiateWay !== undefined &&
        this.searchList.mobileinitiateWay == 3
      ) {
        let item = {
          initiate: "0"
        };
        searchdata.mobilesOptions.push(item);
        // searchdata.mobilesOptions.initiate = "0";
      }
      if (this.searchList.mobilekefuPic !== undefined) {
        let item = {
          kefu_pic: this.searchList.mobilekefuPic
        };
        searchdata.mobilesOptions.push(item);
        // searchdata.mobilesOptions.kefuPic = this.searchList.mobilekefuPic;
      }
      if (this.searchList.mobilecallBack !== undefined) {
        let item = {
          call_back: this.searchList.mobilecallBack
        };
        searchdata.mobilesOptions.push(item);
        // searchdata.mobilesOptions.callBack = this.searchList.mobilecallBack;
      }
      if (this.searchList.interactive_menu !== undefined) {
        let item = {
          interactive_menu: this.searchList.interactive_menu
        };
        searchdata.mobilesOptions.push(item);
        // searchdata.mobilesOptions.interactive_menu = this.searchList.interactive_menu;
      }
      if(this.searchList.pull_visitor !== undefined){
        let item = {
          pull_visitor: this.searchList.pull_visitor
        };
        searchdata.mobilesOptions.push(item)
      }
      if(this.searchList.invite_mandatory !== undefined){
        let item = {
            invite_mandatory: this.searchList.invite_mandatory
        }
         searchdata.mobilesOptions.push(item)
      }
      // 媒体对接
      if (this.basecInfo_media.length !== 0) {
        for (let i of this.basecInfo_media) {
          if (i == "wechatPublic") {
            let item = {
              wechat_public: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.wechatPublic = "1";
          } else if (i == "appletOfWechat") {
            let item = {
              applet_of_wechat: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.appletOfWechat = "1";
          } else if (i == "appletOfBaidu") {
            let item = {
              applet_of_baidu: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.appletOfBaidu = "1";
          } else if (i == "jinriToutiao") {
            let item = {
              jinri_toutiao: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.jinriToutiao = "1";
          } else if (i == "tencentSocialAdvertising") {
            let item = {
              tencent_social_advertising: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.tencentSocialAdvertising = "1";
          } else if (i == "_360") {
            let item = {
              _360: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking._360 = "1";
          } else if (i == "kuaishou") {
            let item = {
              kuaishou: "1"
            };
            searchdata.mediaDocking.push(item);
            // searchdata.mediaDocking.kuaishou = "1";
          }
        }
        // } else {
        //   delete searchdata.mediaDocking;
      }

      // 系统设置
      if (this.basecInfo_system.length !== 0) {
        for (let i of this.basecInfo_system) {
          if (i == "serviceScheduling") {
            let item = {
              service_scheduling: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.serviceScheduling = "1";
          } else if (i == "useful_expressions") {
            let item = {
              useful_expressions: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.useful_expressions = "1";
          } else if (i == "enclave_label") {
            let item = {
              enclave_label: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.enclave_label = "1";
          } else if (i == "conversation_topics") {
            let item = {
              conversation_topics: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.conversation_topics = "1";
          } else if (i == "visitors_forms") {
            let item = {
              visitors_forms: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.visitors_forms = "1";
          } else if (i == "promote_links") {
            let item = {
              promote_links: "1"
            };
            searchdata.systemOptions.push(item);
            // searchdata.systemOptions.promote_links = "1";
          }
        }
        // } else {
        //   delete searchdata.systemOptions;
      }

      // 营销
      if (this.basecInfo_market.length !== 0) {
        for (let i of this.basecInfo_market) {
          if (i == "lottery") {
            let item = {
              lottery: "1"
            };
            searchdata.marketing.push(item);
            // searchdata.marketing.lottery = "1";
          } else if (i == "exitToRetain") {
            let item = {
              exit_to_retain: "1"
            };
            searchdata.marketing.push(item);
            // searchdata.marketing.exitToRetain = "1";
          } else if (i == "shortMessageSend") {
            let item = {
              short_message_send: "1"
            };
            searchdata.marketing.push(item);
            // searchdata.marketing.shortMessageSend = "1";
          }
        }
        // } else {
        //   delete searchdata.marketing;
      }

      // 机器人
      if (this.basecInfo_robot.length !== 0) {
        for (let i of this.basecInfo_robot) {
          if (i == "answerLibrary") {
            let item = {
              answer_library: "1"
            };
            searchdata.robot.push(item);
            // searchdata.robot.lottery = "1";
          } else if (i == "cluesToAsk") {
            let item = {
              clues_to_ask: "1"
            };
            searchdata.robot.push(item);
            // searchdata.robot.exitToRetain = "1";
          }
        }
        // } else {
        //   delete searchdata.robot;
      }

      // 其他
      if (this.basecInfo_else.length !== 0) {
        for (let i of this.basecInfo_else) {
          if (i == "app") {
            let item = {
              app: "1"
            };
            searchdata.other.push(item);
            // searchdata.other.app = "1";
          } else if (i == "developerAccount") {
            let item = {
              developer_account: "1"
            };
            searchdata.other.push(item);
            // searchdata.other.developerAccount = "1";
          } else if (i == "handing_over") {
            let item = {
              handing_over: "1"
            };
            searchdata.other.push(item);
            // searchdata.other.handing_over = "1";
          }
        }
        // } else {
        //   delete searchdata.other;
      }

      // searchdata.distributiveRules = JSON.stringify(searchdata.distributiveRules);
      // searchdata.pcOptions = JSON.stringify(searchdata.pcOptions);
      // searchdata.mobilesOptions = JSON.stringify(searchdata.mobilesOptions);
      // searchdata.mediaDocking = JSON.stringify(searchdata.mediaDocking);
      // searchdata.systemOptions = JSON.stringify(searchdata.systemOptions);
      // searchdata.marketing = JSON.stringify(searchdata.marketing);
      // searchdata.robot = JSON.stringify(searchdata.robot);
      // searchdata.other = JSON.stringify(searchdata.other);
      if (
        searchdata.distributiveRules.length > 0 ||
        searchdata.pcOptions.length > 0 ||
        searchdata.mobilesOptions.length > 0 ||
        searchdata.mediaDocking.length > 0 ||
        searchdata.systemOptions.length > 0 ||
        searchdata.marketing.length > 0 ||
        searchdata.robot.length > 0 ||
        searchdata.other.length > 0
      ) {
        searchdata.functionUsing = "1";
      }
      this.$emit("getsearch_data", searchdata);
    },

    
  },
  mounted(){
    // var data = new Date()
    // this.searchList.dateTime = data; 
  },
  watch: {
    industry1(data) {
      this.industryList = JSON.parse(JSON.stringify(data));
    },
    industry2(data) {
      // this.industriesList = JSON.parse(JSON.stringify(data));
    },
    facilitatorselect(data) {
      this.serviceList = data;
    },
    portraitmeallist(data) {
      this.meallist = [];
      for (let i of data) {
        let item = {
          mealName: i.mealName,
          mealKey: i.mealKey,
          status: undefined
        };
        this.meallist.push(item);
      }
      // this.meallist.push({
      //     mealKey: this.mea_name,
      //     status: this.stusa_value
      // })
    }
  }
};
</script>


