<template>
  <div id="app" style="height: 100%">
    <products-list v-if="show == true"></products-list>
    <my-aside v-if="show == true"></my-aside>
    <div class="app-show">
      <!-- <a-config-provider :locale="zh_CN">
        <keep-alive>
          <router-view v-if="isRouterAlive && $route.meta.keepAlive" />
        </keep-alive>
      </a-config-provider> -->
      <a-config-provider :locale="zh_CN">
        <router-view v-if="isRouterAlive && !$route.meta.keepAlive" />
      </a-config-provider>
    </div>
    <div style="clear: both"></div>
  </div>
</template>

<script>
import myAside from "@/components/aside/myasidetwo.vue";
import productsList from "@/components/productList/productsList";
import zh_CN from "ant-design-vue/lib/locale-provider/zh_CN";
import moment from "moment";
import "moment/locale/zh-cn";
import axios from "axios";
import * as getData from "@/api/api";

moment.locale("zh-cn");

export default {
  name: "App",
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      zh_CN,
      isRouterAlive: true,
      show: true
    };
  },
  mounted() {},
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    }
  },
  created() {},
  components: {
    myAside,
    productsList
  },
  watch: {
    $route(to, from) {
      if (to.name == "orderInformation" || to.name == "Chatrecord") {
        this.show = false;
      } else {
        this.show = true;
      }
    }
  }
};
</script>

<style lang="scss">
#app {
  /*min-width: 1200px;*/
  /*background: rgba(245, 246, 247, 1);*/
  height: 100%;
}
#app:after {
  content: "";
  display: block;
  visibility: hidden;
  clear: both;
  width: 100%;
  height: 0;
  font-size: 0;
  zoom: 1;
}
</style>
<style>
@import "assets/css/manage";
</style>
<style lang="scss">
.shareModal {
  .ant-modal-body {
    padding: 15px;
    .share_item {
      height: 32px;
      line-height: 32px;
      margin-bottom: 15px;
    }
  }
  .ant-btn-danger {
    background: #f5222d;
    color: #fff;
  }
}
</style>



