<template>
  <div style="width:100%;height:100%">
    <div class="QAlist clear">
      <div class="question_list" v-if="questionclassify_list.length > 0">
        <!-- 企业问答库    展示问题分类列表 -->
        <div
          v-show="type == 'show'"
          class="question_item"
          :class="current_questionclassityid == item.id ? 'active' : ''"
          v-for="item in questionclassify_list"
          :key="item.id"
          @click="item.id == 'all' ? getall_questionclassifty_show():cur_questionclassifty(item)"
        >
          <div class="item_title clear">
            <div class="tit_name" :title="item.category">{{item.category}}</div>
            <div class="tit_num">({{item.count}})</div>
          </div>
        </div>

        <!-- 行业模板问答库   编辑问题分类列表 -->
        <div
          v-show="type == 'edit'"
          class="question_item edit"
          :class="current_questionclassityid == item.type_id ? 'active' : ''"
          v-for="item in questionclassify_list"
          :key="item.type_id"
          @click="cur_questionclassifty_edit(item)"
        >
          <div class="item_title clear">
            <div class="item_title_box clear">
              <div class="tit_name_edit" :title="item.category">{{item.type_name}}</div>
              <div class="tit_num">({{item.count}})</div>
            </div>
            <div
              v-show="item.type_name!== '未分类'"
              class="questionclassify_editicon"
              @click.stop="edit_questionclassify_name(item)"
            >
              <img src="../../../assets/qapool/edit_icon.svg" style="width:16px;height:16px" alt />
            </div>
            <div
              v-show="item.type_name!== '未分类'"
              class="questionclassify_deleteicon"
              @click.stop="open_tipmodal(item,'questionclassify')"
            >
              <img
                src="../../../assets/qapool/delete_icon_blue.svg"
                style="width:16px;height:16px"
                alt
              />
            </div>
          </div>
        </div>
      </div>
      <div class="question_list" v-else>
        <div class="question_null">暂无数据</div>
      </div>

      <!-- 企业问答库   展示问题答案列表 -->
      <div class="small_list" v-if="type == 'show'">
        <a-table
          rowKey="knowledge_id"
          :columns="answer_columns_show"
          :data-source="answer_listdata"
          :row-selection="{ selectedRowKeys: questionselectedKeys, onChange: questionSelectChange_show }"
          :pagination="false"
          :scroll="{ y: gettableheight() }"
        >
          <span slot="question" slot-scope="text,record">
            <a-popover overlayClassName="popover_card" destroyTooltipOnHide placement="top">
              <template slot="content">
                <div v-html="record.answer"></div>
              </template>
              <div style="width:100%;height:100%" v-html="text"></div>
            </a-popover>
          </span>
          <span slot="operation" slot-scope="record">
            <span class="click_btn" @click="open_tipmodal(record,'business_question')">删除</span>
          </span>
        </a-table>
      </div>

      <!-- 行业模板问答库   编辑问题答案列表 -->
      <div class="small_list" v-if="type == 'edit'">
        <a-table
          rowKey="knowledge_id"
          :columns="answer_columns_edit"
          :data-source="answer_listdata"
          :row-selection="{ selectedRowKeys: questionselectedKeys, onChange: questionSelectChange_edit }"
          :pagination="false"
          :scroll="{ y: gettableheight() }"
        >
          <span slot="question" slot-scope="text,record">
            <a-popover overlayClassName="popover_card_edit" destroyTooltipOnHide placement="top">
              <template slot="content">
                <div v-html="record.answer"></div>
              </template>
              <div style="width:100%;height:100%" v-html="text"></div>
            </a-popover>
          </span> 
          <span slot="operation" slot-scope="record">
            <span class="click_btn" @click="open_editquestionmodal(record)">编辑</span>
            <span
              class="click_btn"
              style="margin-left:10px;"
              @click="open_tipmodal(record,'question')"
            >删除</span>
          </span>
        </a-table>
      </div>

      <!-- 删除问题分类提示 -->
      <Tipmodal
        :show="tipmodal.show"
        :title="tipmodal.title"
        :type="tipmodal.type"
        :content_tip="tipmodal.content_tip"
        @getshow="close_tipmodal"
        @ok="tipmodal.ok == 'question' ? modal_debounce(delete_question,1000,tipmodal.info) :tipmodal.ok == 'questionclassify' ? modal_debounce(delete_questionclassify,1000,tipmodal.info) :modal_debounce(delete_BusinessQuestion,1000,tipmodal.info)"
      ></Tipmodal>

      <!-- 编辑问题 -->
      <Questioninfo
        :show="editquestionDrawer.show"
        :questioninfo="editquestionDrawer.info"
        :curclassify_list="questionclassify_list"
        :cur_questionclassityid="current_questionclassityid"
        :type="editquestionDrawer.type"
        @getshow="close_editquestionmodal"
        @refresh_questionlist="refresh_questionlist"
        @refresh_classify="refresh_classifylist"
      ></Questioninfo>
    </div>
  </div>
</template>
<script>
import Tipmodal from "@/components/common/tip_type.vue";
import Questioninfo from "./QuestionInfo";
export default {
  props: [
    "type",
    "question_classifylist_show",
    "cur_Businessid",
    "curtwolevel_industry",
    "question_classifylist_edit"
  ],
  components: {
    Tipmodal,
    Questioninfo
  },
  data() {
    return {
      current_questionclassityid: "all",
      questionclassify_list: [],
      cur_companyid: "",
      answer_listdata: [],
      refreshlist: false,
      answer_columns_show: [
        {
          title: "序号",
          dataIndex: "num",
          width: 60
        },
        {
          title: "问题名称",
          dataIndex: "question",
          scopedSlots: { customRender: "question" }
        },
        {
          title: "操作",
          key: "operation",
          width: 120,
          scopedSlots: { customRender: "operation" }
        }
      ],

      answer_columns_edit: [
        {
          title: "序号",
          dataIndex: "num",
          width: 60
        },
        {
          title: "问题名称",
          dataIndex: "question",
          scopedSlots: { customRender: "question" }
        },
        {
          title: "操作",
          key: "operation",
          width: 120,
          scopedSlots: { customRender: "operation" }
        }
      ],
      questionselectedKeys: [],
      tipmodal: {
        show: false,
        ok: "questionclassify",
        info: {},
        title: "温馨提示",
        type: "warning",
        content_tip: "删除后不可恢复，你确定要删除吗？"
      },
      editquestionDrawer: {
        show: false,
        type: "edit",
        info: {}
      }
    };
  },
  methods: {
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 190;
    },
    // 切换问题分类列表item获取问题列表
    cur_questionclassifty(item) {
      // if(item.id == this.current_questionclassityid){
      //   return;
      // }
      this.current_questionclassityid = item.id;
      let data = {
        category_id: item.id
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/selectClassQuestion", data)
        .then(res => {
          if (res.data.code == 101) {
            this.answer_listdata = res.data.data.data;
            for (let i = 0; i < this.answer_listdata.length; i++) {
              this.answer_listdata[i].num = i + 1;
            }
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 企业id 获取 问题列表
    getall_questionclassifty_show() {
      // if(this.current_questionclassityid == "all"){
      //   return;
      // }
      this.current_questionclassityid = "all";
      let data = {
        company_id: this.cur_companyid
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/selectAllQuestion", data)
        .then(res => {
          if (res.data.code == 101) {
            this.answer_listdata = res.data.data.data;
            for (let i = 0; i < this.answer_listdata.length; i++) {
              this.answer_listdata[i].num = i + 1;
            }
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 分类id 获取 问题列表
    cur_questionclassifty_edit(item) {
      // if(item.type_id == this.current_questionclassityid){
      //   return;
      // }
      this.current_questionclassityid = item.type_id;
      let data = {
        type_id: item.type_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/selectTempQuestionsByTypeId", data)
        .then(res => {
          if (res.data.code == 101) {
            this.answer_listdata = res.data.data;
            for (let i = 0; i < this.answer_listdata.length; i++) {
              this.answer_listdata[i].num = i + 1;
            }
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 编辑问题分类名称
    edit_questionclassify_name(item) {
      this.$emit("editclassify", "classify", item);
    },
    // 打开提示对话框
    open_tipmodal(item, ok) {
      this.tipmodal.show = true;
      this.tipmodal.info = item;
      this.tipmodal.ok = ok;
    },
    // 关闭提示对话框
    close_tipmodal(value) {
      this.tipmodal.show = value;
    },
    // 删除企业问答库问题
    delete_BusinessQuestion(item) {
      let data = {
        knowledge_id: item.knowledge_id
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/deleteZskQuestionById", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("删除成功");
          }
          this.close_tipmodal(false);
          this.$emit("refreshallclassify");
          this.refreshlist = true;
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 删除分类名称
    delete_questionclassify(item) {
      let data = {
        type_id: item.type_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/deleteClassification", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("删除成功");
          }
          this.close_tipmodal(false);
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          this.refresh_classifylist();
        });
    },
    // 打开编辑问题框 获取问题详情
    open_editquestionmodal(item) {
      this.editquestionDrawer.show = true;
      let data = {
        knowledge_id: item.knowledge_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/selectTempQuestions", data)
        .then(res => {
          if (res.data.code == 101) {
            this.editquestionDrawer.info = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 关闭编辑问题框
    close_editquestionmodal() {
      this.editquestionDrawer.show = false;
    },
    //删除问题
    delete_question(item) {
      let data = {
        knowledge_id: item.knowledge_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/deleteTempQuestionsByKnowId", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("删除成功");
          }
          this.close_tipmodal(false);
          //   this.refresh_classifylist();
          for (let i of this.questionclassify_list) {
            if (this.current_questionclassityid == i.type_id) {
              i.count = i.count - 1;
            }
          }
          this.refresh_questionlist();
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 企业问答库列表勾选onchange
    questionSelectChange_show(selectedRowKeys) {
      this.questionselectedKeys = selectedRowKeys;
      let copyarr = [];
      for (let item of this.questionselectedKeys) {
        for (let key of this.answer_listdata) {
          if (key.knowledge_id == item) {
            let obj = {
              question: key.question,
              answer: key.answer,
              knowledge_id: key.knowledge_id
            };
            copyarr.push(obj);
          }
        }
      }
      this.$emit("getcopydata", copyarr);
    },
    // 模板问答库列表勾选onchange
    questionSelectChange_edit(selectedRowKeys) {
      this.questionselectedKeys = selectedRowKeys;
      let copyarr = [];
      for (let item of this.questionselectedKeys) {
        for (let key of this.answer_listdata) {
          if (key.knowledge_id == item) {
            copyarr.push(key.id);
          }
        }
      }
      this.$emit("gettransferdata", copyarr);
    },
    // 刷新当前问题列表
    refresh_questionlist() {
      let item = {
        type_id: this.current_questionclassityid
      };
      this.cur_questionclassifty_edit(item);
    },
    refresh_classifylist() {
      this.$emit("refreshclassify", this.curtwolevel_industry);
    }
  },
  computed: {},
  watch: {
    // 问题分类 展示
    question_classifylist_show(data) {
      this.questionclassify_list = JSON.parse(JSON.stringify(data));
      if (this.questionclassify_list.length == 0) {
        this.answer_listdata = [];
      } else {
        let allcount = 0;
        for (let item of this.questionclassify_list) {
          allcount += item.count;
        }
        let obj = {
          id: "all",
          category: "全部问题",
          count: allcount
        };
        this.questionclassify_list.unshift(obj);
        if (this.refreshlist == true) {
          this.cur_questionclassifty({ id: this.current_questionclassityid });
          this.refreshlist = false;
        } else {
          this.getall_questionclassifty_show();
        }
      }
    },
    // 问题分类  编辑
    question_classifylist_edit(data) {
      this.questionclassify_list = JSON.parse(JSON.stringify(data));
      if(data.length > 0){
          this.cur_questionclassifty_edit(this.questionclassify_list[0]);
      }else{
          this.answer_listdata = [];
      }
    },
    cur_Businessid(data) {
      this.cur_companyid = data;
    }
  }
};
</script>
<style lang="scss">
.popover_card {
  max-width: calc(100% - 800px);
  max-height: 200px;
  overflow-x: hidden;
  overflow-y: auto;
}
.popover_card_edit {
  max-width: calc(100% - 840px);
  max-height: 200px;
  overflow-x: hidden;
  overflow-y: auto;
}
.QAlist {
  width: 100%;
  min-width: 650px;
  height: 100%;
  border: 1px solid #d8dfea;
  .question_list {
    float: left;
    width: 240px;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    border-right: 1px solid #d8dfea;
    .question_item {
      width: 100%;
      height: 40px;
      padding-left: 10px;
      cursor: pointer;
      &:hover {
        background-color: #e7f4fd;
      }
      &.active {
        background-color: #eaebed;
      }
      .item_title {
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(40, 51, 75, 1);
        line-height: 40px;
        .tit_name {
          float: left;
          max-width: 180px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .tit_num {
          float: left;
          margin-left: 5px;
          width: 35px;
        }
      }
    }
    .question_item.edit {
      &:hover {
        .item_title {
          .questionclassify_editicon {
            display: block;
          }
          .questionclassify_deleteicon {
            display: block;
          }
        }
      }
      .item_title {
        .item_title_box {
          float: left;
          width: 150px;
        }
        .tit_name_edit {
          float: left;
          max-width: 110px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .questionclassify_editicon {
          display: none;
          width: 30px;
          height: 100%;
          padding: 0 7px;
          float: left;
        }
        .questionclassify_deleteicon {
          display: none;
          width: 30px;
          height: 100%;
          padding: 0 7px;
          float: left;
        }
      }
    }
    .question_null {
      width: 100%;
      height: 80px;
      padding-left: 10px;
      text-align: center;
      line-height: 80px;
      color: #bfbfbf;
      font-size: 12px;
    }
  }
  .small_list {
    float: left;
    width: calc(100% - 240px);
    height: 100%;
  }
}
</style>


