<template>
  <div class="page ">
    <div class="interface-head">
      <a @click="back"> <a-icon type="left" /> 人工质检</a>
    </div>
    <!-- <div style="width:100%;height:100%">
      <a-spin />
    </div> -->
    <div class="page-box">
      <div
        class="clear search-condition-box"
        style="margin-top:10px;margin-bottom:10px"
      >
        <!-- <div class="search-condition-box-item" style="width: 80px">
          <a-button type="primary" @click="showModal">
            new质检结果
          </a-button>
        </div> -->

        <a-modal
          title="质检"
          :visible="visible"
          centered
          @ok="handleOk"
          @cancel="handleCancel"
          okText="提交"
        >
          <div style="width:100%;height:80px;" class="sele">
            <div
              class="search-condition-box-item"
              style="width: 70px;line-height:30px;margin-left:40px;margin-top:20px"
            >
              行业：
            </div>
            <div
              class="search-condition-box-item"
              style="width: 270px;margin-top:20px"
            >
              <!-- :display-render="displayRender" -->
              <a-cascader
                :options="yearList"
                :field-names="{
                  label: 'industry_name',
                  value: 'id',
                  children: 'second_level'
                }"
                :value="value"
                expand-trigger="hover"
                placeholder="选择行业"
                :display-render="displayRender"
                @change="onChange1"
                style="width:300px;margin-left:-30px"
              />
            </div>
          </div>
        </a-modal>
        <a-modal
          title="暂不质检"
          :visible="visibletwo"
          centered
          @ok="nozhijian"
          @cancel="handleCanceltwo"
          okText="确定"
          cancelText="取消"
        >
          <div
            style="height:60px;line-height:30px;margin-left:40px;margin-top:20px;"
          >
            暂不质检的客户将会从当前列表中移除，确定暂不质检吗？
          </div>
        </a-modal>
      </div>

      <div style="display: flex;overflow-x: auto;">
        <div class="form-box" :style="{ height: scrollerHeight }">
          <div class="title">
            <div>客户基本信息 ( {{ companydata.account }} )</div>
            <div>
              <a :disabled="alldata.length <= 1" @click="handleOktwo"
                ><span>下一客户</span></a
              >
              <a :disabled="alldata.length <= 1" @click="handleOktwo"
                ><a-icon type="right"
              /></a>
            </div>
          </div>
          <div class="info">
            <div>公司名称：{{ companydata.company_name }}</div>
            <div>
              账号类型：{{
                companydata.account_type == 1 ? "客户账号" : "测试账号"
              }}
            </div>
            <div>
              行业类型： {{ companydata.industry }}
              <!-- 行业类型：{{
                companydata.industry.includes(",")
                  ? companydata.industry.split(",")[1]
                  : companydata.industry
              }} -->
            </div>
            <div>注册时间：{{ companydata.register_time }}</div>
            <div>质检时间：{{ companydata.check_time }}</div>
          </div>
          <div style="height: calc( 100% - 167px )">
            <div
              style="width:50%;float:right;box-shadow: 1px 0px 0px 0px #D8DFEA, -1px 0px 0px 0px #D8DFEA;height:100%"
            >
              <div class="dialogue">
                <div>对话内容</div>
                <div>
                  <a :disabled="pricflag"><a-icon type="left"/></a>
                  <a
                    class="nexta"
                    @click="reductionnextindex"
                    :disabled="pricflag"
                    ><span>上一条</span></a
                  >
                  <a @click="addnextindex" :disabled="nextflag"
                    ><span>下一条</span></a
                  >
                  <a :disabled="nextflag"><a-icon type="right"/></a>
                </div>
              </div>
              <a-spin
                :spinning="table_spinning"
                tip="数据加载中..."
                style="height:calc(100% - 46px);overflow: auto"
              >
                <div class="chat">
                  <!-- <div>
            <v-html
              >手机:&nbsp;[MOBILE]ff9e50e5abf3f7d8b0fe6332068065fd[/MOBILE]<br />抽中奖品:&nbsp;默认奖品</v-html
            >
          </div> -->

                  <a-list :data-source="data.msgList" :split="false">
                    <a-list-item slot="renderItem" slot-scope="item, index">
                      <div :class="item.type == 'p' ? 'chat-zhu' : 'chat-ke'">
                        <!-- //p客服 g访客 t 系统 -->
                        <div>
                          {{
                            item.type == "p"
                              ? "客服"
                              : item.type == "g"
                              ? "访客"
                              : ""
                          }}
                          {{ item.msg_time }}
                        </div>
                        <div v-html="item.msg"></div>
                      </div>
                    </a-list-item>
                  </a-list>
                </div>
              </a-spin>
            </div>

            <div style="width:50%;float:left;height:100%">
              <div class="title">
                <a @click="tofang" :class="iskeyflag ? 'keyactive' : ''"
                  >访问轨迹</a
                >
                <a
                  style="margin-left: 10px"
                  @click="tokeyword"
                  :class="iskeyflag ? '' : 'keyactive'"
                  >关键词</a
                >
              </div>
              <div v-show="iskeyflag" class="bottom">
                <div class="botmain">
                  <a-list
                    :data-source="keyWordList"
                    :split="false"
                    :locale="locale"
                  >
                    <a-list-item slot="renderItem" slot-scope="item">
                      <div style="margin-left: 10px">
                        {{ item }}
                      </div>
                    </a-list-item>
                  </a-list>
                </div>
              </div>

              <div class="bottom" ref="ele" v-show="!iskeyflag">
                <!-- :pagination="pagination" -->
                <a-spin
                  :spinning="table_spinning1"
                  tip="数据加载中..."
                  style="height:calc(100% - 46px);overflow: auto"
                >
                  <div class="botmain">
                    <a-checkbox
                      :checked="allchecked"
                      @change="changeallcheckout"
                      style="margin: 10px 10px"
                      v-if="accesspathList.length > 0"
                    >
                      全选选择
                    </a-checkbox>
                    <a-checkbox-group
                      @change="changecheckout"
                      v-model="checkedList"
                      style="width:100%"
                    >
                      <a-list
                        :data-source="accesspathList"
                        :split="false"
                        :locale="locale"
                      >
                        <!-- traceList -->
                        <a-list-item slot="renderItem" slot-scope="item">
                          <div class="wang">
                            <a-checkbox :value="item.trace">
                              <a :href="item.trace" target="_blank">{{
                                item.title
                              }}</a>
                            </a-checkbox>
                          </div>
                          <!-- <div style="margin-right: 20px;width:265px">
                          {{ item.talk_time }}
                        </div> -->
                        </a-list-item>
                      </a-list>
                    </a-checkbox-group>
                  </div>
                </a-spin>
                <!-- <div style="height:10px"></div> -->
              </div>
            </div>
          </div>

          <div
            style="height:60px;width:100%;border-top: 1px solid #CED5E0;position:relative;"
          >
            <a-button
              type="primary"
              style="position:absolute;top:14px;right:120px"
              @click="showModal"
            >
              质检
            </a-button>
            <a-button
              style="position:absolute;top:14px;right:20px;"
              @click="visibletwo = true"
            >
              暂不质检
            </a-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  components: {},
  data() {
    return {
      transferdata: [],
      iskeyflag: false,
      keyWordList: [],
      checkedList: [],
      allchecked: false,
      CancelToken: "",
      source: "",
      table_spinning: false,
      table_spinning1: false,
      pagenumxl: false,
      //传参过来接收
      company_id: "",
      nextindex: 1,
      pricflag: true,
      nextflag: false,
      totalindex: 1,
      companydata: {
        industry: ""
      },
      alldata: [],
      companyindex: "",
      searchListcan: "",

      accesspathList: [],

      data: [
        {
          title:
            "网站标题网站标题网站标题网站标题网站标题网站标题网站标题网站标题网站标题网站标题网站标题网站…",
          time: "2020-01-25 12:12:12"
        }
      ],
      visible: false,
      visibletwo: false,
      locale: {},
      value: [],
      value1: "",
      yearList: [],
      //分页
      pagination: {
        current: 1,
        pageSize: 5,
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 5, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        searchList: {},
        // showQuickJumper: true,
        pageSizeOptions: ["5", "10", "20"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          //   this.paramsCheck(params);
          //   this.regUserList(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          this.pagination.current = current;
          this.pagination.pageSize = size;
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
        } // 点击页码事件
      }
    };
  },
  methods: {
    back() {
      this.$router.push({
        name: "Manualinspection",
        params: {
          searchcan: this.searchListcan
        }
      });
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    format(shijianchuo) {
      //shijianchuo是整数，否则要parseInt转换
      var time = new Date(shijianchuo);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      var h = time.getHours();
      var mm = time.getMinutes();
      var s = time.getSeconds();
      return (
        y +
        "-" +
        this.add0(m) +
        "-" +
        this.add0(d) +
        " " +
        this.add0(h) +
        ":" +
        this.add0(mm) +
        ":" +
        this.add0(s)
      );
    },

    // 表格宽度
    gettablewidth() {
      // let w = 0;
      // for (let i of this.columns) {
      //   w = w + i.width;
      // }
      // return w;
    },
    //数组去重
    unique(arr1) {
      const res = new Map();
      return arr1.filter(a => !res.has(a.title) && res.set(a.title, 1));
    },

    // 表格高度
    gettableheight() {
      //let height = this.$refs.ele.offsetHeight;
      return 100 + "px";
    },
    getdata(id, index) {
      const self = this;

      this.data = [];
      // console.log(id, index);
      var data = {
        company_id: this.company_id,
        num: this.nextindex
      };
      this.table_spinning = true;
      this.CancelToken = axios.CancelToken;
      this.source = this.CancelToken.source();
      if (this.nextindex == 1) {
        this.table_spinning1 = true;
        this.$http
          .post("portrayal/Inspection/getTrace", this.qs.stringify(data), {
            // cancelToken: new CancelTokens(c => {
            //   self.CancelToken = c;
            // })
            cancelToken: this.source.token
          })
          .then(res => {
            //console.log(res);
            if (res.data.code == 101) {
              this.accesspathList = res.data.result.traceList;
              this.table_spinning1 = false;
            } else {
              this.accesspathList = [];
              this.table_spinning1 = false;
            }
          });

        //huoqu关键词列表
        var keydata = {
          company_id: this.company_id
        };
        this.$http
          .post("/portrayal/Inspection/getKeyWords", this.qs.stringify(keydata))
          .then(res => {
            console.log(res);
            if (res.data.code == 101) {
              this.keyWordList = res.data.result.keyWordList;
            } else {
              this.keyWordList = [];
            }
          });
      }

      this.$http
        .post("portrayal/Inspection/getTalkMsg", this.qs.stringify(data), {
          // cancelToken: new CancelTokens(c => {
          //   self.CancelToken = c;
          // })
          cancelToken: this.source.token
        })
        .then(res => {
          if (res.data.code == 101) {
            res.data.result.msgList.forEach(item => {
              item.msg_time = this.format(item.msg_time);
              //item.talk_time = this.format(item.talk_time);
            });
            if (
              !res.data.result.msgList ||
              res.data.result.msgList.length == 0
            ) {
              this.nextflag = true;
            } else {
              this.nextflag = false;
            }
            if (this.nextindex <= 1) {
              this.pricflag = true;
            } else {
              this.pricflag = false;
            }
            this.data = res.data.result;
            //this.pagination.total = this.data.length;
            this.table_spinning = false;
          } else {
            //this.totalindex = 1;
            this.nextflag = true;
            this.data = [];
            this.$message.error(res.data.errorMsg);
            this.table_spinning = false;
          }
          // var attr = JSON.parse(decodeURIComponent(res.data));
          // console.log(JSON.parse(decodeURIComponent(res.data)), 111);
        });
    },
    handleOk() {
      //console.log(this.value[this.value.length - 1]);
      // console.log(JSON.parse(JSON.stringify(this.checkedList)));
      this.transferdata = [];
      let transferdata = JSON.parse(JSON.stringify(this.checkedList));
      transferdata.forEach(item => {
        this.accesspathList.forEach(res => {
          if (item == res.trace) {
            this.transferdata.push(res.title);
          }
        });
      });
      // this.transferdata = [];
      // for (let item of transferdata) {
      //   this.transferdata.push(item);
      // }
      if (this.value.length == 0) {
        this.$message.error("请先选择行业");
      } else {
        //console.log(JSON.stringify(this.transferdata));
        var data = {
          company_id: this.company_id,
          industry: this.value[this.value.length - 1],
          user_id: this.$store.state.userinfo[1].user.usermgtUserId,
          key_word_list: JSON.stringify(this.keyWordList),
          trace_title_list: JSON.stringify(this.transferdata)
        };
        this.$http
          .post("portrayal/Inspection/modifyIndustry", this.qs.stringify(data))
          .then(res => {
            // console.log(res);
            if (res.data.code == 101) {
              this.visible = false;
              //this.visibletwo = true;
              this.value = "";
              this.$message.success("质检成功");
              this.caozuowan();
            } else {
              this.visible = false;
              this.$message.error(res.data.errorMsg);
            }
          });
      }
    },
    //改变多选框
    changecheckout(e) {
      //console.log(e);
      if (this.accesspathList.length == this.checkedList.length) {
        this.allchecked = true;
      } else {
        this.allchecked = false;
      }
    },
    //全选
    changeallcheckout(e) {
      // console.log(e);
      this.allchecked = e.target.checked;
      //console.log(this.checkedList);
      if (this.allchecked) {
        this.accesspathList.forEach(item => {
          this.checkedList.push(item.trace);
        });
      } else {
        this.checkedList = [];
      }
    },
    //去关键词
    tokeyword() {
      this.iskeyflag = true;
    },
    tofang() {
      this.iskeyflag = false;
    },
    //暂不质检
    nozhijian() {
      this.source.cancel();
      var data = {
        company_id: this.company_id,
        user_id: this.$store.state.userinfo[1].user.usermgtUserId
      };
      this.$http
        .post(
          "/portrayal/Inspection/notModifyIndustry",
          this.qs.stringify(data)
        )
        .then(res => {
          if (res.data.code == 101) {
            //this.visibletwo = false;
            this.$message.success("操作成功");
            this.caozuowan();
            //this.visibletwo = false;
          } else {
            this.visibletwo = false;
            this.$message.error(res.data.errorMsg);
          }
        });
    },
    handleOktwo() {
      this.source.cancel();
      this.companyindex++;
      //   if (this.companyindex == this.alldata.length) {
      //     this.$message.error("暂无下一条数据");
      //     this.back();
      //   } else {
      if (this.companyindex > this.alldata.length) {
        this.companyindex = 1;
      }
      this.nextindex = 1;
      this.company_id = this.alldata[this.companyindex - 1].company_id;
      this.allchecked = false;
      this.checkedList = [];
      this.getdata();
      this.companydata = this.alldata[this.companyindex - 1];
      this.showModal();
      this.visible = false;
      //}
      this.visibletwo = false;
      //console.log("下一条");
      // this.$http.post("portrayal/Inspection/msgAndTrace").then(res => {});
    },
    //操作完成变化
    caozuowan() {
      this.source.cancel();
      //console.log(this.companyindex);
      var index = this.companyindex - 1;
      this.alldata.splice(index, 1);
      if (this.companyindex > this.alldata.length) {
        this.companyindex = 1;
      }
      if (this.alldata.length == 0) {
        this.$message.error("暂无下一条数据");
        this.visible = false;
        this.visibletwo = false;
        this.back();
      } else {
        this.nextindex = 1;
        this.companydata = this.alldata[this.companyindex - 1];
        this.company_id = this.alldata[this.companyindex - 1].company_id;
        this.allchecked = false;
        this.checkedList = [];
        this.getdata();
        this.showModal();
        this.visible = false;
        this.visibletwo = false;
      }
    },
    //点击下一条
    addnextindex() {
      this.source.cancel();
      // console.log("111");
      this.nextindex++;
      if (this.nextindex > 1) {
        this.pricflag = false;
      }
      // if (this.nextindex >= this.totalindex) {
      //   this.nextflag = true;
      // }

      this.getdata();
    },
    //点击上一条
    reductionnextindex() {
      this.source.cancel();
      this.nextindex--;
      this.nextflag = false;
      if (this.nextindex <= 1) {
        this.pricflag = true;
      }

      this.getdata();
    },

    handleCancel() {
      this.visible = false;
      this.value = [];
    },
    handleCanceltwo() {
      this.visibletwo = false;
    },
    showModal() {
      //console.log(this.yearList);
      this.value = [];
      //console.log(this.companydata.industry_id);
      if (this.companydata.industry_id == "") {
        this.value = [];
      } else {
        this.yearList.forEach(res => {
          if (res.id != this.companydata.industry_id) {
            res.second_level.forEach(item => {
              if (item.id == this.companydata.industry_id) {
                this.value.push(res.id, item.id);
              }
            });
          } else {
            this.value.push(res.id);
          }
        });
      }
      //console.log(this.value);
      this.visible = true;
    },
    onChange1(value) {
      //console.log(value);
      this.value = value;
      //console.log(this.value);
      //console.log(this.value[this.value.length - 1]);
    },
    displayRender({ labels }) {
      return labels[labels.length - 1];
    }
  },
  computed: {
    scrollerHeight: function() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 100 + "px"; //自定义高度需求
    }
  },
  created() {
    // window.onbeforeunload = function(e) {
    //   e = e || window.event;
    //   if (e) {
    //     this.$router.push({
    //       name: "Manualinspection"
    //     });
    //   }
    //   this.$router.push({
    //     name: "Manualinspection"
    //   });
    // };
    if (this.$route.params.id) {
    } else {
      this.$router.push({
        name: "Manualinspection"
      });
    }
  },
  mounted() {
    //
    //console.log(this.$route);
    this.company_id = this.$route.params.id;
    this.alldata = this.$route.params.data;
    this.companydata = this.alldata[this.$route.params.index - 1];
    this.companyindex = this.$route.params.index;
    this.searchListcan = this.$route.params.serch;
    this.getdata();
    this.$http.post("portrayal/industry/list").then(res => {
      //console.log(res);
      if (res.status == 200) {
        this.yearList = res.data.result;
      } else {
        this.yearList = [];
      }
    });
  }
  //ruguolinggeshikonglinggeshentinengbunengzaiyicixiangyu
  // beforeRouteLeave(to, from, next) {
  //   if (to.path == "/Manualinspection") {
  //     to.meta.keepAlive = true;
  //     console.log("huilai");
  //   }
  //   next();
  // }
};
</script>

<style scoped lang="scss">
.form-box {
  width: 100%;
  border: 1px solid #ced5e0;
  border-radius: 4px;
  min-width: 980px;
  // overflow-x: auto;
  // position: relative;
  flex-shrink: 0;
}
.chat {
  width: 100%;
  height: calc(100% - 46px);
  overflow: auto;
  //   border-bottom: 1px solid #ced5e0;
  .chat-zhu {
    margin-left: 12px;
    width: calc(100% - 24px);
    margin-top: 12px;
    margin-bottom: 5px;
    div:first-of-type {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #21a0f0;
      line-height: 20px;
    }
    div:nth-of-type(2) {
      margin-top: 6px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #403f3f;
      line-height: 20px;
    }
  }
  .chat-ke {
    margin-left: 12px;
    width: calc(100% - 24px);
    margin-top: 12px;
    margin-bottom: 5px;
    div:first-of-type {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #0552a4;
      line-height: 20px;
    }
    div:nth-of-type(2) {
      margin-top: 6px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #403f3f;
      line-height: 20px;
    }
  }
}
.keyactive {
  color: #000;
}
.title {
  width: 100%;
  height: 45px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #28334b;
  line-height: 45px;
  background: #f7f8fa;
  padding-left: 15px;
  border-bottom: 1px solid #ced5e0;
  position: relative;
  div:nth-child(1) {
    height: 100%;
    position: absolute;
    left: 15px;
  }
  div:nth-child(2) {
    height: 100%;
    position: absolute;
    right: 20px;
  }
}
.info {
  width: 100%;
  padding-right: 10%;
  height: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ced5e0;
  padding-left: 15px;
}
.dialogue {
  width: 100%;
  height: 45px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #28334b;
  line-height: 45px;
  background: #f7f8fa;
  padding-left: 15px;
  border-bottom: 1px solid #ced5e0;
  div:first-of-type {
    float: left;
  }
  div:nth-of-type(2) {
    float: right;
    margin-right: 20px;
    color: #218bfc;
    .nexta {
      margin-right: 10px;
    }
  }
}
.bottom {
  // height: calc(100% - 450px);
  height: calc(100% - 46px);
  width: 100%;
  //overflow: auto;
  .botmain {
    height: 100%;
    overflow: auto;
  }
}
.wang {
  color: #218bfc;
  margin-left: 13px;
  width: 80%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ant-spin-container > .ant-list-items > .ant-list-item:last-child {
  border-bottom: none;
}
</style>



