import Vue from "vue";
import Router from "vue-router";
import regUser from "@/page/regUser";
import activeUser from "@/page/activeUser";
import lostUser from "@/page/lostUser";
import orderInformation from "@/page/orderInformation";
import packageMonthReport from "@/page/packageMonthReport";
import meal_MonthReport from "@/page/meal_MonthReport";
import mealYearreport from "../page/mealYearreport.vue"
import meal_TrendReport from '@/page/meal_TrendReport'
import packageYearReport from "@/page/packageYearReport";
import thirdMonthReport from "@/page/thirdMonthReport";
import IndustryTargetCount from "@/page/IndustryTargetCount";
import Industry_QApool from "@/page/Industry_QApool/Industry_QApool";
import Keywords from "@/page/Newkeywords/Newkeywords";
import industryManage from "@/page/industryManage/industryManage";
//import Keywords from '@/page/Keywords/Keywords'
import PermissionSetting from "@/page/PermissionSetting";

import Chatrecord from "../page/Chatrcordtwo";
import Statistics from "../page/statistics/statistics";
import Queueanalysis from "../page/queueanalysis";
import Openaccount from "../page/openaccount";
import Detailedmrr from "../page/detailedMRRtwo";
import Channel from "../page/Channel";
import Test from "../page/text";
import Manualinspection from "../page/manualinspection/Manualinspection";
import Inspectionresults from "../page/manualinspection/newInspectionresultstwo";
import Newview from "../page/newview.vue"

Vue.use(Router);
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  console.log(location,777)
  return originalPush.call(this, location).catch(err => err);
};
export default new Router({
  // mode: 'history',
  routes: [
    {
      path: "/",
      hidden: true,
      redirect: { path: "/regUser" }
    },
    {
      path: "/regUser",
      name: "regUser",
      component: regUser
    },
    {
      path: "/Newview",
      name: "Newview",
      component: Newview
    },
    {
      path: "/activeUser",
      name: "activeUser",
      component: activeUser
    },
    {
      path: "/lostUser",
      name: "lostUser",
      component: lostUser
    },
    {
      path: "/orderInformation",
      name: "orderInformation",
      component: orderInformation
    },
    {
      path: "/packageMonthReport",
      name: "packageMonthReport",
      component: packageMonthReport
    },
    {
      path: "/meal_MonthReport",
      name: "meal_MonthReport",
      component: meal_MonthReport
    },
    {
      path: "/mealYearreport",
      name: "mealYearreport",
      component: mealYearreport
    },
    {
      path: "/meal_TrendReport",
      name: "meal_TrendReport",
      component: meal_TrendReport
    },
    {
      path: "/packageYearReport",
      name: "packageYearReport",
      component: packageYearReport
    },
    {
      path: "/IndustryTargetCount",
      name: "IndustryTargetCount",
      component: IndustryTargetCount
    },
    {
      path: "/thirdMonthReport",
      name: "thirdMonthReport",
      component: thirdMonthReport
    },
    {
      path: "/Industry_QApool",
      name: "Industry_QApool",
      component: Industry_QApool
    },
    {
      path: "/Keywords",
      name: "Keywords",
      component: Keywords
    },
    {
      path: "/PermissionSetting",
      name: "PermissionSetting",
      component: PermissionSetting
    },
    //聊天记录
    {
      path: "/Chatrecord",
      name: "Chatrecord",
      component: Chatrecord
    },
    //行业指标统计
    {
      path: "/Statistics",
      name: "Statistics",
      component: Statistics
    },
    //队列分析
    {
      path: "/Queueanalysis",
      name: "Queueanalysis",
      component: Queueanalysis
    },
    //开户比
    {
      path: "/Openaccount",
      name: "Openaccount",
      component: Openaccount
    },
    //详细MRR
    {
      path: "/Detailedmrr",
      name: "Detailedmrr",
      component: Detailedmrr
    },
    //渠道
    {
      path: "/Channel",
      name: "Channel",
      component: Channel
    },
    {
      path: "/Test",
      component: Test
    },
    //人工质检
    {
      path: "/Manualinspection",
      name: "Manualinspection",
      component: Manualinspection
      // meta:{
      //     keepAlive: true
      // },
    },
    //质检结果
    {
      path: "/Inspectionresults",
      name: "Inspectionresults",
      component: Inspectionresults
    },
    {
      path: "/industryManage",
      name: "industryManage",
      component: industryManage
    }
  ]
});



