<template>
  <div class="left_sidebar" id="left_sidebar" v-if="urlname.includes('53kf')">
    <iframe
      id="sidebar"
      src="https://usermanagement.53kf.com/sidebar/sidebar.html"
      frameborder="0"
    ></iframe>
  </div>
  <div class="left_sidebar" id="left_sidebar" v-else>
    <iframe
      id="sidebar"
      src="https://usermanagement.71baomu.com/sidebar/sidebar.html"
      frameborder="0"
    ></iframe>
  </div>
</template>
<script>
export default {
  name: "productsList",
  data() {
    return {
      urlname: ""
    };
  },
  mounted() {
    this.urlname = window.location.href;

    var sidebarframe = document.getElementById("sidebar");
    sidebarframe.onload = function() {
      if (window.location.href.includes("53kf")) {
        sidebarframe.contentWindow.postMessage(
          { appname: "cloud", data: {} },
          "*"
        );
      } else {
        sidebarframe.contentWindow.postMessage(
          { appname: "cloud", data: {} },
          "*"
        );
      }
    };
  },
  methods: {}
};
</script>

<style>
.left_sidebar {
  width: 60px;
  height: 100%;
  position: absolute;
  z-index: 9999;
}
.left_sidebar :hover {
  background-color: rgba(35, 47, 69, 0.1);
}
.left_sidebar div {
  width: 100%;
  height: 100%;
}
.left_sidebar iframe {
  width: 100%;
  height: 100%;
}
</style>



