<template>
   <!-- 提示对话框 -->
    <a-modal
      v-model="modal_show"
      :title="title"
      :footer="null"
      @cancel="close_modal"
      :width="380"
    >
      <div class="modal_content">
        <div class="modal_body_item clear positionrelative">
          <div class="type_img">
            <img v-show="type == 'error'" src="@/assets/qapool/Error.svg" style="width:20px;height:20px;" alt="">
            <img v-show="type == 'question'" src="@/assets/qapool/Question.svg" style="width:20px;height:20px;" alt="">
          </div>
          <div class="tip_content">{{content_tip}}</div>
        </div>
        <div class="modal_footer">
          <a-button
            type="primary"
            @click="close_modal(false)"
          >取消</a-button>
          <a-button style="margin-left:15px;" @click="ok">确定</a-button>
        </div>
      </div>
    </a-modal>
</template>
<script>
export default {
  props:['show','title','type','content_tip'],
  data() {
    return {

    }
  },
  methods:{
    // 关闭对话框
    close_modal(value){
      this.$emit("getshow", value);
    },
    ok(){
      this.$emit('ok')
    }
  },
  computed:{
    modal_show:{
      set(value) {
        this.close_modal(value);
      },
      get() {
        return this.show;
      }
    }
  }
}
</script>
<style lang="scss">
.type_img{
  float: left;
  width: 20px;
  height: 20px;
}
.tip_content{
  float: left;
  margin-left: 5px;
  width: calc(100% - 25px);
  line-height: 20px;
  height: 100%;
}
</style>


