//获取mac地址
function getmac(next) {
  let url = window.location.href;
  // console.log("获取mac地址")
  var targetExtensionId = "benjmbndnehlnfafcpgemhaaongbibnh";
  var port = chrome.runtime.connect(targetExtensionId, {
    name: "connect"
  });
  // console.log(port);
  port.onDisconnect.addListener(function (message) {
    $Message.error("请先下载插件"); //引导网页版下载插件
    port.onDisconnect.removeListener();
  });
  var mac = "";
  var macurl = "";
  var mackey = "";
  port.onMessage.addListener(function (e) {
    mac = e;
    if (mac) {
      console.log("获取到的mac地址为:" + JSON.stringify(mac.res));
      //console.log("获取到的mac地址为:" + JSON.stringify(mac).res);
      macurl = JSON.stringify(mac.res);
      // console.log(macurl)
      //console.log(that.macurl, "ai");
      // console.log(macurl, arr);
      if (store.state.saas_token == '') {

        let arr = window.location.href.split("token=");
        if (arr[1] == undefined) {
          if (sessionStorage.getItem("saas_ykf_token")) {

          } else {
            if (url.includes("53kf")) {
              window.location.href =
                "https://usermanagement.53kf.com/#/?url=http://cloudservice.53kf.com/#/regUser";
            } else {
              window.location.href =
                "https://usermanagement.71baomu.com/#/?url=http://cloudservice.71baomu.com/#/regUser";
            }
            return;
          }
        } else {
          store.dispatch("updatesaastoken", arr[1]);
          sessionStorage.setItem("saas_ykf_token", arr[1])
        }
        console.log(arr, "arr[1]是=", arr[1], "token是", store.state.saas_token)
      }


      // let arr1 = arr[1].split("&");
      // let arr2 = arr1[0].split("=");
      var token = sessionStorage.getItem("saas_ykf_token")

      var login_data = {
        token: token,
        mac: JSON.stringify(mac.res)
      }
      // console.log(login_data)
      const getLogin = (login_data) => {
        return axios({
          url: '/ccs/ccs/login',
          method: "post",
          headers: { "Content-Type": 'application/x-www-form-urlencoded' },
          data: qs.stringify(login_data)
        }).then(res =>
          res.data
        )
        // .post(`/ccs/ccs/login`, qs.stringify(login_data))
        // .then(res => res.data);
      };

      getLogin(login_data).then(res => {
        // console.log(res)
        if (res.code == 200) {
          getData.getId().then(res => {
            //console.log("login")
            axios.defaults.headers["ccs-access-token"] = res.data;
            per_http.defaults.headers["ccs-access-token"] = res.data;
            store.dispatch("updateuserid", res.data);
            let data = {
              id: res.data
            };
            getData.getuserinfo(data).then(res => {
              store.dispatch("updateuserinfo", res.data);
              var rou_name = window.location.href.split("/#/")[1] ? window.location.href.split("/#/")[1] : 'regUser'
              console.log(rou_name)
              var routr_name = rou_name.includes("token") ? 'regUser' : rou_name
              casepagepermission(routr_name, res.data, next);
            });
          });
          //完成了套餐业绩月报表和年报表中对、列表数据的筛选和更新
        } else {
          // console.log("login")
          if (url.includes("53kf")) {
            window.location.href =
              "https://usermanagement.53kf.com/#/?url=http://cloudservice.53kf.com/#/regUser";
          } else {
            window.location.href =
              "https://usermanagement.71baomu.com/#/?url=http://cloudservice.71baomu.com/#/regUser";
          }
        }
      });

      if (mac.res == "Time out") {
        $Message.error("该电脑设备信息验证过期，无法继续使用");
      } else {
      }
    } else {
      $Message.error("mac地址获取不成功,插件识别失败");
    }
  });
  var getkeyurl;
  if (window.location.href.includes("53kf")) {
    getkeyurl = "https://usermanagement.53kf.com/newsaasmanage/login/getKey";
  } else {
    getkeyurl = "https://usermanagement.71baomu.com/newsaasmanage/login/getKey";
  }
  axios.post(getkeyurl).then(res => {
    // console.log(res);
    if (res.data.code == 101) {
      mackey = res.data.data;
      // console.log(res.data.data);
      port.postMessage({ key: mackey });
    } else {
      mackey = "";
    }
  });
}

// 企业关键词登录验证
function keywords_login(user, resolve) {
  let gettokendata = {
    mobiles: user[1].user.phone
  };
  let url = window.location.href.includes("53kf.com")
    ? "https://usermanagement.53kf.com/newsaasmanage/login/getVeriftyToken"
    : "https://usermanagement.71baomu.com/newsaasmanage/login/getVeriftyToken";
  axiosrequest.post(url, gettokendata).then(res => {
    if (res.data.code == 101) {
      let data = {
        token: res.data.data
      };
      keywordhttp.post("/javaTalkKeyWord/login/verifyLogin", data).then(res => {
        if (res.data.code == 101) {
          resolve();
        } else {
          message.error(res.data.message);
          resolve();
        }
      });
    }
  });
}
// 企业问答库登录验证
function qapool_login(user, resolve) {
  let gettokendata = {
    mobiles: user[1].user.phone
  };
  let url = window.location.href.includes("53kf.com")
    ? "https://usermanagement.53kf.com/newsaasmanage/login/getVeriftyToken"
    : "https://usermanagement.71baomu.com/newsaasmanage/login/getVeriftyToken";
  axiosrequest.post(url, gettokendata).then(res => {
    if (res.data.code == 101) {
      let data = {
        token: res.data.data
      };
      qapoolhttp.post("/newknowledgebase/login/verifyLogin", data).then(res => {
        if (res.data.code == 101) {
          resolve();
        } else {
          message.error(res.data.message);
          resolve();
        }
      });
    }
  });
}
// 确定相应页面权限
function casepagepermission(routename, user, next) {
  // console.log(routename,1001);
  if (routename == "PermissionSetting") {
    updatepagepermission("0000", user[0].permissionList, next);
  } else if (routename == "regUser") {
    updatepagepermission("1000", user[0].permissionList, next);
  } else if (routename == "activeUser") {
    updatepagepermission("2000", user[0].permissionList, next);
  } else if (routename == "meal_MonthReport") {
    // console.log(routename,1002);
    updatepagepermission("3001", user[0].permissionList, next);
  } else if (routename == "packageMonthReport") {
    updatepagepermission("3002", user[0].permissionList, next);
  } else if (routename == "mealYearreport") {
    updatepagepermission("3003", user[0].permissionList, next);
  } else if (routename == "meal_TrendReport") {
    // console.log(routename,8888888);
    updatepagepermission("3005", user[0].permissionList, next);
  } 
  else if (routename == "Queueanalysis") {
    updatepagepermission("7000", user[0].permissionList, next);
  } else if (routename == "Openaccount") {
    updatepagepermission("8000", user[0].permissionList, next);
  } else if (routename == "Detailedmrr") {
    updatepagepermission("9000", user[0].permissionList, next);
  } else if (routename == "Channel") {
    updatepagepermission("10000", user[0].permissionList, next);
  } else if (routename == "thirdMonthReport") {
    updatepagepermission("3004", user[0].permissionList, next);
  } else if (routename == "Statistics") {
    //console.log('lll')
    updatepagepermission("6000", user[0].permissionList, next);
  } else if (routename == "Manualinspection") {
    updatepagepermission("1500", user[0].permissionList, next);
  } else if (routename == "industryManage") {
    updatepagepermission("1501", user[0].permissionList, next);
  } else if (routename == "Keywords") {
    let keywordslogin = new Promise((resolve, reject) => {
      keywords_login(user, resolve);
    });
    keywordslogin.then(res => {
      updatepagepermission("4000", user[0].permissionList, next);
    });
  } else if (routename == "Industry_QApool") {
    let poollogin = new Promise((resolve, reject) => {
      qapool_login(user, resolve);
    });
    poollogin.then(res => {
      updatepagepermission("5000", user[0].permissionList, next);
    });
  } else {
    next();
  }
}

// 更新页面权限 跳转
function updatepagepermission(pagecode, PermissionList, next) {
  console.log(pagecode,PermissionList,1111111)
  for (let item of PermissionList) {
    if (item.code == pagecode) {
      if (item.is_own == 1) {
        store.dispatch("updatepermission", true);
        next();
      } else {
        store.dispatch("updatepermission", false);
        next();
      }
    }
  }
}

new Vue({
  el: "#app",
  store,
  router,
  components: { App },
  template: "<App/>"
});



