<template>
  <div class="workingInformation">
    <div class="clear search-condition-box">
      <div class="search-condition-box-item">
        <a-input placeholder="账号" allowClear v-model="searchList.account" />
      </div>
      <div class="search-condition-box-item">
        <a-select
          v-model="searchList.tabs"
          style="width: 100%;"
          allowClear
          placeholder="标签"
          @change="tabsChange"
        >
          <a-select-option
            v-for="(item, index) in formList"
            :key="index"
            :value="item.id"
            >{{ item.name }}</a-select-option
          >
        </a-select>
      </div>
      <div class="search-condition-box-item" style="width: auto">
        <a-button @click="screenClear">取消筛选</a-button>
        <a-button type="primary" @click="screenBtn">筛选</a-button>
      </div>
    </div>
    <div class="form-box">
      <a-table
        :columns="columns"
        :locale="locale"
        :dataSource="workingInformationList.list"
        :pagination="pagination"
      >
        <a slot="manageTag" slot-scope="text, record">
          <span
            style="color:rgba(40,51,75,1);"
            v-if="
              record.financeTag == 1 &&
                (record.manageTag == 1 ||
                  record.operateTag == 1 ||
                  record.serviceTag == 1)
            "
            >财务、</span
          >
          <span
            style="color:rgba(40,51,75,1);"
            v-else-if="record.financeTag == 1"
            >财务</span
          >
          <span
            style="color:rgba(40,51,75,1);"
            v-if="
              record.manageTag == 1 &&
                (record.operateTag == 1 || record.serviceTag == 1)
            "
            >管理、</span
          >
          <span
            style="color:rgba(40,51,75,1);"
            v-else-if="record.manageTag == 1"
            >管理</span
          >
          <span
            style="color:rgba(40,51,75,1);"
            v-if="record.operateTag == 1 && record.serviceTag == 1"
            >运营、</span
          >
          <span
            style="color:rgba(40,51,75,1);"
            v-else-if="record.operateTag == 1"
            >运营</span
          >
          <span style="color:rgba(40,51,75,1);" v-if="record.serviceTag == 1"
            >客服</span
          >
        </a>
        <span slot="account" slot-scope="text">
          <span v-html="text"></span>
        </span>
        <span slot="accountId" slot-scope="text">
          <span v-html="text"></span>
        </span>
      </a-table>
    </div>
  </div>
</template>

<script>
import * as getData from "../../api/api";

export default {
  name: "workingInformation",
  props: ["workingInformationList", "workingId"],
  created() {},
  data() {
    return {
      searchList: {
        account: "",
        tabs: undefined,
        companyId: undefined,
        financeTag: null,
        manageTag: null,
        operateTag: null,
        serviceTag: null,
        pageNum: 1,
        pageSize: 20
      },
      formList: [
        { id: "financeTag", name: "财务" },
        { id: "manageTag", name: "管理" },
        { id: "operateTag", name: "运营" },
        { id: "serviceTag", name: "客服" }
      ],
      columns: [
        { title: "序号", dataIndex: "num", key: "num" },
        {
          title: "账号",
          dataIndex: "account",
          key: "account",
          scopedSlots: { customRender: "account" }
        },
        { title: "姓名", dataIndex: "accountName", key: "accountName" },
        { title: "手机号码", dataIndex: "phone", key: "phone" },
        { title: "QQ", dataIndex: "qq", key: "qq" },
        { title: "邮箱", dataIndex: "email", key: "email" },
        { title: "微信", dataIndex: "wechat", key: "wechat" },
        {
          title: "标签",
          dataIndex: "manageTag",
          key: "manageTag",
          scopedSlots: { customRender: "manageTag" }
        },
        {
          title: "账号 ID",
          dataIndex: "accountId",
          key: "accountId",
          scopedSlots: { customRender: "accountId" }
        },
        {
          title: "微信粉丝 ID",
          dataIndex: "wechatFansId",
          key: "wechatFansId"
        },
        {
          title: "手机设备 ID",
          dataIndex: "mobileDeviceId",
          key: "mobileDeviceId"
        }
      ],
      data: [{}],
      pagination: {
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          console.log(current);
          this.pagination.defaultCurrent = current;
          this.pagination.defaultPageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = pageSize;
          this.paramsCheck(params);
          console.log(params);
          this.getworkInfo(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          console.log(current);
          this.pagination.defaultCurrent = current;
          this.pagination.defaultPageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.paramsCheck(params);
          console.log(params);
          this.getworkInfo(params);
          // this.getList()
        } // 点击页码事件
      },

      locale: {
        // emptyText:'暂无数据'
      },
      Id: null
    };
  },
  mounted() {
    //1.xizhicai zhu
    //2.sunshangxiang nei
    //3.shenliubei fan
    //4.taishici fan
    //5.xuhuang fan
    //6.jiezhangjiao zhong
    //7.yangxiu fan
    // 8. caochong zhong
  },
  methods: {
    tabsChange(v) {
      console.log(v);
      console.log(this.workingInformationList);
      console.log(this.searchList.tabs);
    },
    getworkInfo(params) {
      getData.getworkInfo(params).then(res => {
        console.log(res);
        this.pagination.total = res.data.total;
        this.workingInformationList.list = res.data.list;
        console.log(this.workingInformationList.list);
        for (var i = 0; i < this.workingInformationList.list.length; i++) {
          this.workingInformationList.list[i].num =
            i +
            1 +
            (this.pagination.defaultCurrent - 1) *
              this.pagination.defaultPageSize;
        }
        console.log(this.searchList.account);
        if (this.searchList.account == "") {
          return;
        }
        console.log(this.searchList.account);
        this.workingInformationList.list.map(item => {
          //遍历
          console.log(item);
          item.account = this.brightKeyword(item.account);
          // item.accountId = this.brightKeyword(item.accountId)
        }); //到这里search方法结束
        console.log(this.searchList.account);
      });
    },
    brightKeyword(val) {
      let keyword = this.searchList.account; //获取输入框输入的内容
      if (val.indexOf(keyword) !== -1) {
        //判断这个字段中是否包含keyword
        //如果包含的话，就把这个字段中的那一部分进行替换成html字符
        return val.replace(keyword, `<font color='#42cccc'>${keyword}</font>`);
      } else {
        return val;
      }
    },
    screenBtn() {
      var params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      console.log(params);
      this.getworkInfo(params);
    },
    screenClear() {
      this.searchList = {
        account: "",
        tabs: undefined,
        financeTag: null,
        manageTag: null,
        operateTag: null,
        serviceTag: null,
        pageNum: this.pagination.defaultCurrent,
        pageSize: this.pagination.defaultPageSize
      };
      this.searchList.companyId = this.workingId;
      this.screenBtn();
    },
    paramsCheck(params) {
      params.companyId = this.workingId;
      if (this.searchList.tabs == "financeTag") {
        params.financeTag = 1;
      } else if (this.searchList.tabs == "manageTag") {
        params.manageTag = 1;
      } else if (this.searchList.tabs == "operateTag") {
        params.operateTag = 1;
      } else if (this.searchList.tabs == "serviceTag") {
        params.serviceTag = 1;
      }
      if (params.account == "") {
        params.account = null;
      }
      params.account = this.searchList.account;
      // params.accountId=this.searchList.account
      console.log(params);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>



