<template>
  <a-drawer
    wrapClassName="questioninfo"
    placement="right"
    :closable="false"
    width="480"
    :visible="draewr_show"
    :maskClosable="true"
    @close="close(false)"
  >
    <div class="close_icon" @click="close(false)"></div>
    <a-tabs
      v-model="tabskey"
      default-active-key="1"
      style="width:100%;height:100%;"
      :tabBarStyle="tabBarStyle"
    >
      <a-tab-pane key="1" :tab="title">
        <div class="question_body clear">
          <div class="question_item">
            <div class="item_title" style="padding: 10px 0 5px 0;">标准问题</div>
            <div class="positionrelative">
              <a-input
                style="width:430px;"
                v-model="standardquestion"
                placeholder="标准问题"
                @focus="standardquestion_nullerr = false"
                :class="standardquestion_nullerr ? 'null_err' :''"
              />
              <div
                class="questioninfoTip"
                :class="[{'questioninfoTip-show':standardquestion_nullerr}]"
              >
                <p class="questioninfoTip-center" v-html="questioninfoTip"></p>
              </div>
            </div>
          </div>
          <div class="question_item">
            <div class="item_title">相似问题</div>
            <div
              class="alike_item clear positionrelative"
              v-for="item in alikequestion_list"
              :key="item.knowledge_id"
            >
              <a-input
                :class="[item.id > 1 ?'alike_input_delete' :'alike_input',item.input_nullerr ? 'null_err' :'']"
                v-model="item.question"
                @focus="focusitem(item)"
                :placeholder="'相似问题'+item.id"
                @blur="alikequescheck(item)"
              />
              <div
                class="delete_question_icon"
                v-if="item.id > 1"
                @click="delete_alikequestion(item)"
              >
                <img
                  src="../../../assets/qapool/delete_icon.svg"
                  style="width:16px;height:16px"
                  alt
                />
              </div>
              <div class="questioninfoTip" :class="[{'questioninfoTip-show':item.input_nullerr}]">
                <p class="questioninfoTip-center" v-html="questioninfoTip"></p>
              </div>
            </div>
            <div
              class="add_alikequestion"
              v-show="alikequestion_list.length < 10"
              @click="add_alikequestion"
            >+ 相似问题</div>
          </div>
          <div class="question_item clear">
            <div class="item_title_block">所属分类：</div>
            <div class="question_classify_box">
              <a-radio-group name="radioGroup" :default-value="1" v-model="question_classify_radio">
                <a-radio :value="1">已有分类</a-radio>
                <a-radio :value="2">新增分类</a-radio>
              </a-radio-group>
            </div>
            <div class="classify_select">
              <div
                style="width: 240px;"
                class="positionrelative"
                v-if="question_classify_radio == '1'"
              >
                <a-select v-model="has_classifty_key" placeholder="已有分类" style="width:100%">
                  <a-select-option
                    :value="item.type_id"
                    v-for="item in has_classifty_list"
                    :key="item.type_id"
                  >{{item.type_name}}</a-select-option>
                </a-select>
                <!-- <div
                  class="qapoolTip"
                  style="margin-left:65px;"
                  :class="[{'qapoolTip-show':has_classifty_key_nullerr}]"
                >
                  <p class="qapoolTip-center" v-html="qapoolTip"></p>
                </div>-->
              </div>
              <div
                v-if="question_classify_radio == '2'"
                class="positionrelative"
                style="width: 240px;"
              >
                <a-input
                  v-model="newaddclassify_inputkey"
                  placeholder="输入新增分类名称"
                  @focus="newaddclassify_inputkey_nullerr = false"
                  :class="newaddclassify_inputkey_nullerr ? 'null_err' :''"
                />
                <div
                  class="qapoolTip"
                  :class="[{'qapoolTip-show':newaddclassify_inputkey_nullerr}]"
                >
                  <p class="qapoolTip-center" v-html="qapoolTip"></p>
                </div>
              </div>
            </div>
          </div>
          <div class="question_item">
            <div class="item_title">答案</div>
            <div
              class="answer_editor positionrelative"
              :id="answerdata_nullerr ? 'nulltext_err' :''"
            >
              <WangEditor @catchData="getcatchdata" ref="wangeditor_ref"></WangEditor>
              <div class="questioninfoTip" :class="[{'questioninfoTip-show':answerdata_nullerr}]">
                <p class="questioninfoTip-center" v-html="questioninfoTip"></p>
              </div>
            </div>
          </div>
        </div>
        <div class="question_footer">
          <a-button
            type="primary"
            @click="type == 'add' ? modal_debounce(keep_questioninfo_add,1000) : modal_debounce(keep_questioninfo_edit,1000)"
          >保存</a-button>
          <a-button style="margin-left:15px;" @click="close(false)">取消</a-button>
        </div>
      </a-tab-pane>
    </a-tabs>
  </a-drawer>
</template>
<script>
import WangEditor from "./WangEditor";
export default {
  components: {
    WangEditor
  },
  props: [
    "type",
    "show",
    "curclassify_list",
    "questioninfo",
    "cur_questionclassityid"
  ],
  data() {
    return {
      tabskey: "1",
      title: "新增问题",
      tabBarStyle: {
        padding: "0 15px"
      },
      // 关键问题
      standardquestion: "",
      standardquestion_nullerr: false,
      // 相似问题
      all_alikequestion_list: [],
      alikequestion_list: [
        {
          id: 1,
          input_nullerr: false,
          knowledge_id: "",
          parent_id: "",
          question: "",
          answer: "",
          status: 1
        }
      ],

      curclassify_key: "",
      question_classify_radio: 1,
      questioninfoTip: "",
      qapoolTip: "",

      has_classifty_list: [
        {
          type_id: 1
        }
      ],
      has_classifty_key: undefined,
      has_classifty_key_nullerr: false,

      newaddclassify_inputkey: "",
      newaddclassify_inputkey_nullerr: false,

      answerdata: "",
      answerdata_nullerr: false,
      answerdata_null: false
    };
  },
  methods: {
    // 初始化
    init() {
      this.standardquestion = "";
      this.standardquestion_nullerr = false;
      this.alikequestion_list = [
        {
          id: 1,
          question: "",
          input_nullerr: false
        }
      ];
      (this.question_classify_radio = 1),
        (this.questioninfoTip = ""),
        (this.qapoolTip = ""),
        (this.has_classifty_key = undefined);
      this.has_classifty_key_nullerr = false;
      this.newaddclassify_inputkey = "";
      this.newaddclassify_inputkey_nullerr = undefined;
      this.answerdata = "";
      this.answerdata_nullerr = false;
      this.answerdata_null = false;
      this.$refs.wangeditor_ref.cleartext();
    },
    // 获取富文本编辑框内容
    getcatchdata(data) {
      this.answerdata = data;
    },
    // 关闭编辑框
    close(value) {
      this.init();
      this.$emit("getshow", value);
    },
    // 添加相似问题
    add_alikequestion() {
      let obj = {
        id: this.alikequestion_list.length + 1,
        question: "",
        input_nullerr: false
      };
      this.alikequestion_list.push(obj);
    },
    // 删除相似问题
    delete_alikequestion(item) {
      for (let i = 0; i < this.alikequestion_list.length; i++) {
        if (this.alikequestion_list[i].id == item.id) {
          this.alikequestion_list.splice(i, 1);
        }
      }
      if (this.type == "edit") {
        for (let i = 0; i < this.all_alikequestion_list.length; i++) {
          if (this.all_alikequestion_list[i].id == item.id) {
            this.all_alikequestion_list.splice(i, 1);
          }
        }
        if (this.all_alikequestion_list > 9) {
          this.alikequestion_list = this.all_alikequestion_list.slice(0, 10);
        }
      }
      for (let i = 0; i < this.alikequestion_list.length; i++) {
        this.alikequestion_list[i].id = i + 1;
      }
    },
    // 聚焦
    focusitem(item) {
      item.input_nullerr = false;
      this.$forceUpdate();
    },
    // 失去焦点相似问题重复核验
    alikequescheck(item) {
      if (
        this.replace_null(item.question) ==
          this.replace_null(this.standardquestion) &&
        item.question !== ""
      ) {
        item.input_nullerr = true;
        this.questioninfoTip = "问题名称重复";
        this.$forceUpdate();
        return false;
      }
      for (let i of this.alikequestion_list) {
        if (
          item.id !== i.id &&
          this.replace_null(item.question) == this.replace_null(i.question) &&
          item.question !== ""
        ) {
          if (item.id > i.id) {
            item.input_nullerr = true;
          } else {
            i.input_nullerr = true;
          }
          this.questioninfoTip = "问题名称重复";
          this.$forceUpdate();
          return false;
        }
      }
    },
    // 问题编辑框输入核验
    questioncheck() {
      // 标准问题核验
      if (this.replace_null(this.standardquestion) == "") {
        this.standardquestion_nullerr = true;
        this.questioninfoTip = "请输入问题";
        return false;
      }
      //   相似问题为空
      //   for (let i = 0; i < this.alikequestion_list.length; i++) {
      //     if (this.replace_null(this.alikequestion_list[i].question) == "") {
      //       this.alikequestion_list[i].input_nullerr = true;
      //       this.questioninfoTip = "请输入问题";
      //       return false;
      //     }
      //   }
      // 相似问题重复
      for (let item of this.alikequestion_list) {
        let checkstate = this.alikequescheck(item);
        if (checkstate == false) {
          return false;
        }
      }
      for (let key of this.alikequestion_list) {
        if (key.input_nullerr == true) {
          return false;
        }
      }
      //   分类核验
      if (this.question_classify_radio == 2) {
        if (this.replace_null(this.newaddclassify_inputkey) == "") {
          this.newaddclassify_inputkey_nullerr = true;
          this.qapoolTip = "请输入分类名称";
          return false;
        }
      }
      if (this.answerdata_null == true || this.answerdata == "") {
        this.answerdata_nullerr = true;
        this.questioninfoTip = "请输入答案";
        return false;
      }
      return true;
    },
    // 新增问题保存
    keep_questioninfo_add() {
      let check = this.questioncheck();
      if (check == false) {
        return;
      }
      let alikelist = [];
      for (let item of this.alikequestion_list) {
        if (this.replace_null(item.question) !== "") {
          alikelist.push(item.question);
        }
      }
      let data = {};
      if (this.question_classify_radio == 1) {
        data = {
          cmd: "have",
          question: this.standardquestion,
          answer: this.answerdata,
          info: alikelist.length == 0 ? undefined : JSON.stringify(alikelist),
          type_id: this.has_classifty_key
        };
      } else if (this.question_classify_radio == 2) {
        data = {
          cmd: "new",
          question: this.standardquestion,
          answer: this.answerdata,
          info: alikelist.length == 0 ? undefined : JSON.stringify(alikelist),
          type_name: this.newaddclassify_inputkey,
          type_parent: this.has_classifty_list[0].type_parent
        };
      }
      this.$qapool_http
        .post("/newknowledgebase/tempBase/addTempQuestions", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("添加成功");
            this.close(false);
            this.$emit("refresh_classify");
          } else if (res.data.code == 233) {
            this.newaddclassify_inputkey_nullerr = true;
            this.qapoolTip = "名称已存在";
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 编辑问题保存
    keep_questioninfo_edit() {
      let check = this.questioncheck();
      if (check == false) {
        return;
      }
      let alikelist = [];
      for (let item of this.alikequestion_list) {
        if (this.replace_null(item.question) !== "") {
          let obj = {
            question: item.question,
            knowledge_id: item.knowledge_id
          };
          alikelist.push(obj);
        }
      }
      let data = {};
      if (this.question_classify_radio == 1) {
        data = {
          knowledge_id: this.questioninfo.templateQuestion.knowledge_id,
          cmd: "have",
          question: this.standardquestion,
          answer: this.answerdata,
          info: alikelist.length == 0 ? undefined : JSON.stringify(alikelist),
          type_id: this.has_classifty_key
        };
      } else if (this.question_classify_radio == 2) {
        data = {
          knowledge_id: this.questioninfo.templateQuestion.knowledge_id,
          cmd: "new",
          question: this.standardquestion,
          answer: this.answerdata,
          info: alikelist.length == 0 ? undefined : JSON.stringify(alikelist),
          type_name: this.newaddclassify_inputkey,
          type_parent: this.has_classifty_list[0].type_parent
        };
      }
      this.$qapool_http
        .post("/newknowledgebase/tempBase/updateTempQuestions", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("修改成功");
            if (
              this.question_classify_radio == 2 ||
              (this.question_classify_radio == 1 &&
                this.has_classifty_key !== this.curclassify_key)
            ) {
              this.$emit("refresh_classify");
            } else {
              this.$emit("refresh_questionlist");
            }
            this.close(false);
          } else if (res.data.code == 233) {
            this.newaddclassify_inputkey_nullerr = true;
            this.qapoolTip = "名称已存在";
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    }
  },
  computed: {
    draewr_show: {
      set(value) {
        this.close(value);
      },
      get() {
        return this.show;
      }
    }
  },
  watch: {
    type(data) {
      if (data == "add") {
        this.title = "新增问题";
      } else if (data == "edit") {
        this.title = "编辑问题";
      }
    },
    show(data) {
      if (this.type == "add") {
        this.title = "新增问题";
        this.has_classifty_key = this.has_classifty_list[0].type_id;
        this.curclassify_key = this.has_classifty_list[0].type_id;
      } else if (this.type == "edit") {
        this.title = "编辑问题";
        this.has_classifty_key = this.cur_questionclassityid;
        this.curclassify_key = this.cur_questionclassityid;
      }
    },
    curclassify_list(data) {
      this.has_classifty_list = JSON.parse(JSON.stringify(data));
    },
    answerdata(data) {
      let data1 = JSON.parse(JSON.stringify(data))
        .replace("<p>", "")
        .replace("</p>", "")
        .replace("<br>", "");
      if (this.replace_null(data1) == "") {
        // this.answerdata_nullerr = true;
        this.answerdata_null = true;
      } else {
        this.answerdata_nullerr = false;
        this.answerdata_null = false;
      }
    },
    questioninfo(data) {
      this.all_alikequestion_list = data.similarQuestion;
      this.alikequestion_list = this.all_alikequestion_list.slice(0, 10);
      for (let i = 0; i < this.alikequestion_list.length; i++) {
        this.alikequestion_list[i].id = i + 1;
        this.alikequestion_list[i].input_nullerr = false;
      }
      if (this.alikequestion_list.length == 0) {
        this.alikequestion_list = [
          {
            id: 1,
            question: ""
          }
        ];
      }
      this.standardquestion = data.templateQuestion.question;
      this.$refs.wangeditor_ref.addtext(data.templateQuestion.answer);
      if (data.templateQuestion.answer.includes("<p>")) {
        this.answerdata = data.templateQuestion.answer;
      } else {
        this.answerdata = "<p>" + data.templateQuestion.answer + "</p>";
      }
    }
  },
  mounted() {}
};
</script>
<style lang="scss">
.close_icon {
  position: absolute;
  right: 11px;
  top: 13px;
  width: 14px;
  height: 15px;
  cursor: pointer;
  z-index: 9999;
  background-size: cover;
  background-image: url("../../../assets/qapool/close_icon.svg");
}
.question_body {
  padding: 0 15px 20px 15px;
  max-height: calc(100vh - 100px);
  overflow-x: hidden;
  overflow-y: auto;
  .question_item {
    width: 100%;
    .item_title {
      width: 100%;
      font-size: 14px;
      padding: 20px 0 5px 0;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(40, 51, 75, 1);
      line-height: 18px;
    }
    .item_title_block {
      width: 70px;
      height: 18px;
      font-size: 14px;
      float: left;
      padding: 20px 0 5px 0;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(40, 51, 75, 1);
      line-height: 18px;
    }
    .alike_item {
      margin-bottom: 10px;
      height: 32px;
      .alike_input {
        float: left;
        width: 430px;
      }
      .alike_input_delete {
        float: left;
        width: 400px;
      }
      .delete_question_icon {
        float: left;
        margin-left: 10px;
        cursor: pointer;
      }
    }
    .add_alikequestion {
      width: 450px;
      font-size: 14px;
      color: #218bfc;
      cursor: pointer;
      width: 80px;
    }
    .question_classify_box {
      margin-top: 18px;
      float: left;
    }
    .classify_select {
      margin: 10px 0 0 70px;
      width: 100%;
      float: left;
    }
    .answer_editor {
      width: 452px;
      height: 155px;
      border: 1px solid;
      border-color: #ccc;
    }
  }
}
.question_footer {
  width: 480px;
  height: 44px;
  background: rgba(247, 248, 250, 1);
  box-shadow: 0px 1px 0px 0px rgba(206, 213, 224, 1);
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 6px 20px;
  text-align: right;
}
</style>


