<template>
  <div class="industry_keywords">
    <div v-if="hasPermissions == true" style="width:100%;height:100%">
      <div class="page_head">
        <div class="page_title">关键词统计</div>
        <div class="page_tabs clear">
          <div
            class="tabpage"
            :class="curtabpage == 1 ? 'active' : ''"
            @click="change_curtabpage(1)"
          >
            <span>行业统计关键词</span>
          </div>
          <div
            class="tabpage"
            :class="curtabpage == 2 ? 'active' : ''"
            @click="change_curtabpage(2)"
          >
            <span>行业关键词</span>
          </div>
          <div
            class="tabpage"
            :class="curtabpage == 3 ? 'active' : ''"
            @click="change_curtabpage(3)"
          >
            <span>行业过滤词</span>
          </div>
        </div>
      </div>

      <div class="page_body">
        <div class="body_div" v-show="curtabpage == 1">
          <statisticalkeywords
            :alloneindustrylist="allonelevel_industrylist"
            :alltwoindustrylist="alltwolevel_industrylist"
            ref="industrykeywords_ref"
          ></statisticalkeywords>
        </div>
        <div class="body_div" v-show="curtabpage == 2">
          <industrykeywords
            :alloneindustrylist="allonelevel_industrylist"
            :alltwoindustrylist="alltwolevel_industrylist"
            ref="companykeywords_ref"
          ></industrykeywords>
        </div>
        <div class="body_div" v-show="curtabpage == 3">
          <industrythesaurus ref="FilterThesaurus_ref"></industrythesaurus>
        </div>
      </div>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import statisticalkeywords from "./Statistical_keywords";
import industrykeywords from "./Industry_keywords";
import industrythesaurus from "./Industry_thesaurus";
// import sha1 from "sha1";
export default {
  components: {
    statisticalkeywords,
    industrykeywords,
    industrythesaurus
  },
  data() {
    return {
      curtabpage: 1,
      hasPermissions: false,
      allonelevel_industrylist: [],
      alltwolevel_industrylist: [],

      msg: "Fj|uU920fsJ19fk1610699564624"
    };
  },
  methods: {
    change_curtabpage(num) {
      this.curtabpage = num;
      if (num == 1) {
        this.$refs.industrykeywords_ref.getkeywordslist();
      } else if (num == 2) {
        this.$refs.companykeywords_ref.getkeywordslist();
      } else if (num == 3) {
        this.$refs.FilterThesaurus_ref.getcommonlist();
      }
    },
    // 获取一级行业
    getallindustry_one() {
      this.$keywords_http
        .get("/javaTalkKeyWord/keyWord/selectPrimaryIndustry")
        .then(res => {
          if (res.data.code == 101) {
            this.allonelevel_industrylist = JSON.parse(res.data.result);
            //1.sunce 2.guohuai 3.zhangchunhua 4spjiaxu 5.chenqun 6.sima 7.zhouyu 8dengai
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 获取所有二级行业
    getallindustry_two() {
      let data = {
        type: "all"
      };
      this.$keywords_http
        .post("/javaTalkKeyWord/keyWord/selectSecondaryIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.alltwolevel_industrylist = JSON.parse(res.data.result);
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    }
  },
  created() {
    document.title = "关键词统计";
  },
  mounted() {
    // console.log(sha1(this.msg));
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
      this.getallindustry_one();
      this.getallindustry_two();
      var _this = this;
      setTimeout(function() {
        _this.$refs.industrykeywords_ref.getkeywordslist();
      }, 1000);
    }
  }
};
</script>

<style scoped lang="scss">
.industry_keywords {
  width: 100%;
  height: 100%;
  .page_head {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid rgba(216, 223, 234, 1);
    padding: 0 0 0 20px;
    background: rgba(247, 248, 250, 1);
    .page_title {
      line-height: 40px;
      height: 40px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(69, 82, 102, 1);
    }
    .page_tabs {
      height: 37px;
      .tabpage {
        float: left;
        margin-right: 20px;
        line-height: 37px;
        font-size: 14px;
        cursor: pointer;
        &.active {
          color: #3b99fc;
          border-bottom: solid 3px #3b99fc;
        }
        &:hover {
          color: #3b99fc;
        }
      }
    }
  }
  .page_body {
    width: 100%;
    height: calc(100% - 80px);
    padding: 10px 15px;
    .body_div {
      width: 100%;
      height: 100%;
    }
  }
}
</style>



