<template>
  <div>
    <div
      v-if="hasPermissions == true"
      style="width:100%;height:calc(100% - 50px)"
    >
      <div class="interface-head">
        渠道
      </div>
      <div class="page-box">
        <div class="clear search-condition-box">
          <div class="search-condition-box-item" style="width:170px">
            <a-month-picker
              @change="timeChange"
              v-model="statisticsTime"
              placeholder="选择日期"
            />
          </div>
          <div class="search-condition-box-item">
            <!-- <a-button @click="screenClear">取消筛选</a-button> -->
            <a-button type="primary" @click="screenBtn">筛选</a-button>
          </div>
        </div>
        <div class="package-price">
          <div class="package-price-line clear">
            总注册量：
            <span>{{ alllist.registerCustomerTotal }}</span
            >； 总开户量：
            <span>{{ alllist.openCustomerTotal }}</span>
          </div>
        </div>
        

        <div class="form-box">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              :columns="columns"
              :dataSource="data"
              bordered
              :pagination="false"
              :scroll="{ x: gettablewidth(), y: gettableheight() }"
            >
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>

    <div v-else class="no-permission" style="height:calc(100% - 50px)">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>
<script>
import * as getData from "../api/api";
import moment from "moment";
export default {
  data() {
    return {
      alllist: {
        registerCustomerTotal: 0,
        openCustomerTotal: 1
      },
      hasPermissions: false,
      table_spinning: false,
      columns: [
        {
          title: "渠道名称",
          dataIndex: "comeFromName",
          key: 1
        },
        {
          title: "注册量",
          dataIndex: "registerCustomer",
          key: 2,
          sorter: (a, b) => a.registerCustomer - b.registerCustomer
        },
        {
          title: "开户量",
          dataIndex: "openCustomer",
          key: 3,
          sorter: (a, b) => a.openCustomer - b.openCustomer
        },
        {
          title: "开户比",
          dataIndex: "openProportion",
          key: 4,
          sorter: (a, b) =>
            parseFloat(a.openProportion) - parseFloat(b.openProportion)
        }
      ],
      data: [],
      statisticsTime: ""
    };
  },
  methods: {
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 250;
    },
    getdata(params) {
      this.table_spinning = true;
      getData.getchannel(params).then(res => {
        //console.log(res)
        if (res.code == 200) {
          res.data.list.forEach(item => {
            item.openProportion = (item.openProportion * 100).toFixed(2) + "%";
          });
          this.data = res.data.list;
          this.alllist = res.data;
          this.table_spinning = false;
        } else {
          this.data = [];
          this.table_spinning = false;
        }
      });
    },
    getdatafour(params){
        this.table_spinning = true;
        getData.getchannel(params).then(res =>{
          if(res.code == 200){
            res.data.list.forEach(item =>{
              item.openProportion = (item.openProportion * 100).toFixed(2) + '%'
            })
            this.data = res.data.list;
            this.alllist = this.data;
            this.table_spinning = false;
          }else{
            this.data = [];
            this.alllist = {}
          }
        })


        //获取控件组件
        this.table_spinning = true;
        getData.getchannel(params).then(res =>{
          if(res.code == 200){
            res.data.list.forEach(item =>{
              item.openProportion = (item.openProportion * 100).toFixed(2)
            })
            this.data = res.data.list;
            this.alllist = res.data;
          }else{
            //徐州、冀州、兖州、青州、扬州、荆州、梁州、雍州和豫州。
          }
        })
    },
    timeChange() {
      //console.log(this.timechangestart(this.statisticsTime._d))
      this.statisticsTime = this.timechangestart(this.statisticsTime._d);
    },
    //补零操作
    test(str) {
      if (str < 10) {
        return "0" + str;
      } else {
        return str;
      }
    },
    //格式转化
    timechangestart(str) {
      return str.getFullYear() + "-" + this.test(str.getMonth() + 1);
    },
    //筛选
    screenBtn() {
      var data = {
        statisticsTime: this.statisticsTime
      };
      this.getdata(data);
    }
  },
  mounted() {
    document.title = "渠道";
    this.hasPermissions = this.$store.state.pagepermission;
    this.statisticsTime = this.timechangestart(new Date());
    var data = {
      statisticsTime: this.statisticsTime
    };
    this.getdata(data);
  }
};
</script>



