<template>
  <div class="industry_qapool">
    <div v-if="hasPermissions == true" style="width:100%;height:100%">
      <div class="page_head">
        <div class="page_title">行业问答库</div>
        <div class="page_tabs clear">
          <div
            class="tabpage"
            :class="curtabpage == 1 ? 'active' : ''"
            @click="change_curtabpage(1)"
          >
            <span>企业问答库（开通AI机器人）</span>
          </div>
          <div
            class="tabpage"
            :class="curtabpage == 2 ? 'active' : ''"
            @click="change_curtabpage(2)"
          >
            <span>行业模板问答库</span>
          </div>
        </div>
      </div>
      <div class="page_body">
        <div class="body_div" v-show="curtabpage == 1">
          <BusinessQApool ref="business_ref"></BusinessQApool>
        </div>
        <div class="body_div" v-show="curtabpage == 2">
          <IndustrytemplateQApool ref="industrytemplateqapool_ref"></IndustrytemplateQApool>
        </div>
      </div>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>
<script>
import BusinessQApool from "./Business_QApool";
import IndustrytemplateQApool from "./Industrytemplate_QApool";
export default {
  components: {
    BusinessQApool,
    IndustrytemplateQApool
  },
  data() {
    return {
      hasPermissions: false,
      curtabpage: 1
    };
  },
  methods: {
    change_curtabpage(num) {
      this.curtabpage = num;
      if (num == 1) {
        if (this.$refs.business_ref.Business !== undefined) {
          this.$refs.business_ref.getquestionclassify(
            this.$refs.business_ref.Business
          );
          this.$refs.business_ref.getindustrytemplate_all();
        }
      } else if (num == 2) {
        if (this.$refs.industrytemplateqapool_ref.curtwolevel_industry !== "") {
          this.$refs.industrytemplateqapool_ref.refreshclassify_list();
        }
      }
    }
  },
  created() {
    document.title = "行业问答库";
  },
  mounted() {
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
    }
  }
};
</script>
<style scoped lang='scss'>
.industry_qapool {
  width: 100%;
  height: 100%;
  .page_head {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid rgba(216, 223, 234, 1);
    padding: 0 0 0 20px;
    background: rgba(247, 248, 250, 1);
    .page_title {
      line-height: 40px;
      height: 40px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(69, 82, 102, 1);
    }
    .page_tabs {
      height: 37px;
      .tabpage {
        float: left;
        margin-right: 20px;
        line-height: 37px;
        font-size: 14px;
        cursor: pointer;
        &.active {
          color: #3b99fc;
          border-bottom: solid 3px #3b99fc;
        }
        &:hover {
          color: #3b99fc;
        }
      }
    }
  }
  .page_body {
    width: 100%;
    height: calc(100% - 80px);
    padding: 10px 15px;
    .body_div {
      width: 100%;
      height: 100%;
    }
  }
}
</style>


