import axios from "axios";

let url = window.location.href.includes("53kf.com")
  ? "https://portrayal.53kf.com/"
  : "https://portrayal.71baomu.com/";
console.log();
const instance = axios.create({
  baseURL: url,
  headers: { "Content-Type": "application/json" }
});
export default instance;



