import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 应用初始状态
const state = {
    user_id: "",
    userinfo: [],
    pagepermission: false,
    saas_token: ""
}

// 定义所需的 mutations
const mutations = {
    UPDATEUSERID(state, userid) {
        state.user_id = userid;
    },
    UPDATEUSERINFO(state, user) {
        state.userinfo = user;
    },
    UPDATEPAGEPERMISSION(state, pagepermission) {
        state.pagepermission = pagepermission
    },
    UPDATASAASTOKEN(state, token) {
        state.saas_token = token
    }
}

// 创建 store 实例
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


