import axios from "axios";
import qs from 'qs'
import Vue from 'vue'
import { message } from 'ant-design-vue';


const clearRequest = {
    source: axios.CancelToken.source(),
}

// 普通请求
const keywordhttp = axios.create({
    baseURL: location.href.includes('53kf.com') ? 'https://talkkeyword.53kf.com' : 'https://talkkeyword.71baomu.com',
    // timeout: 30000,
    headers: {
        // 'Content-Type': 'application/json'
        'Content-Type': 'application/x-www-form-urlencoded'
    },
    withCredentials: true,//请求头带cookie
});

// 配置发送请求拦截器
keywordhttp.interceptors.request.use(config => {
    config.cancelToken = clearRequest.source.token;// 这句很重要
    if (config.method === 'post') {
        config.data = qs.stringify(config.data)
    }
    return config;
}, error => {
    return Promise.reject(error)
})


// 添加响应拦截器(当响应回复回登陆页时跳转)
keywordhttp.interceptors.response.use(function (response) {
    // 是否返回登陆页
    let origin = window.location.origin;
    if (response.data.code == 101) {
    }
    else if (response.data.code == '201') {
        if (window.location.href.includes('53kf.com')) {
            window.location.href =
                "https://usermanagement.53kf.com/#/?url=" + 'http://cloudservice.53kf.com/#/Industry_Keywords';
        } else {
            window.location.href =
                "https://usermanagement.71baomu.com/#/?url=" + 'http://cloudservice.71baomu.com/#/Industry_Keywords';
        }
        return new Promise(() => { })// 不进入请求返回层，防止弹出提示
    }
    else {
        message.error(response.data.errorMsg);
    }
    return response
}, function (error) {
    return Promise.reject(error)
})

export {
    keywordhttp
}



