<template>
  <div class="orderInformation">
    <div class="clear search-condition-box">
      <div
        class="search-condition-box-item search-condition-box-item-fifter"
        style="font-size: 0;"
      >
        <a-select
          v-model="fifter"
          style="width: 95px;display: inline-block;"
          @change="fifterChange"
        >
          <a-select-option
            v-for="(item, index) in fifterList"
            :key="index"
            :value="item.value"
            >{{ item.name }}</a-select-option
          >
        </a-select>
        <a-input
          style="width: 105px;"
          placeholder="请输入"
          v-model="searchList.companyId"
        />
      </div>
      <div class="search-condition-box-item">
        <a-input
          placeholder="订单编号"
          allowClear
          v-model="searchList.orderId"
        />
      </div>
      <div class="search-condition-box-item">
        <a-select
          v-model="searchList.orderSignType"
          style="width: 100%;"
          allowClear
          placeholder="标识类型"
          @change="tabsChange"
        >
          <a-select-option
            v-for="(item, index) in formList"
            :key="index"
            :value="item.id"
            >{{ item.name }}</a-select-option
          >
        </a-select>
      </div>
      <div class="search-condition-box-item">
        <a-input placeholder="标识" allowClear v-model="searchList.orderSign" />
      </div>
      <div class="search-condition-box-item">
        <a-range-picker
          format="YYYY-MM-DD"
          v-model="time"
          @change="timeChange"
        />
      </div>
      <div class="search-condition-box-item">
        <a-select
          v-model="searchList.productKey"
          style="width: 95px;display: inline-block;"
          allowClear
          placeholder="产品"
          @change="mealChange"
        >
          <a-select-option
            v-for="(item, index) in mealList"
            :key="index"
            :value="item.productKey"
            :title="item.productName"
            >{{ item.productName }}</a-select-option
          >
        </a-select>
        <a-select
          v-model="searchList.mealKey"
          style="width: 95px;display: inline-block;margin-left: 6px;"
          :disabled="mealType"
          allowClear
          placeholder="套餐"
        >
          <a-select-option
            v-for="(item, index) in mealesList"
            :key="index"
            :value="item.mealKey"
            :title="item.mealName"
            >{{ item.mealName }}</a-select-option
          >
        </a-select>
      </div>
      <div class="search-condition-box-item" style="width: auto">
        <a-button @click="clearScreen">取消筛选</a-button>
        <a-button type="primary" @click="screenBtn">筛选</a-button>
      </div>
    </div>
    <div class="form-box">
      <a-table
        format="YYYY-MM-DD HH:mm:ss"
        :columns="columns"
        :locale="locale"
        :dataSource="data"
        :pagination="pagination"
        :scroll="{ x: gettablewidth(), y: gettableheight() }"
      >
        <span slot="orderSignType" slot-scope="text">
          <span v-if="text == 1">账号</span>
          <span v-if="text == 2">机器人</span>
          <span v-if="text == 3">手机号码</span>
        </span>
      </a-table>
    </div>
  </div>
</template>

<script>
import * as getData from "../api/api";
import moment from "moment";

export default {
  name: "orderInformation",
  props: ["orderid"],
  created() {},
  data() {
    return {
      searchList: {
        companyId: "",
        mealKey: undefined,
        productKey: undefined,
        orderId: "",
        orderSign: "",
        orderSignType: undefined,
        orderTimeEnd: "",
        orderTimeStart: "",
        pageNum: 1,
        pageSize: 20
      },
      formList: [
        { id: "1", name: "账号" },
        { id: "2", name: "机器人" },
        { id: "3", name: "手机号码" }
      ],
      columns: [
        { title: "序号", dataIndex: "num", key: "num", width: 50 },
        {
          title: "账号",
          dataIndex: "openAccount",
          key: "openAccount",
          width: 300
        },
        { title: "套餐", dataIndex: "mealName", key: "mealName", width: 200 },
        { title: "订单编号", dataIndex: "orderId", key: "orderId", width: 300 },
        {
          title: "结算时间",
          dataIndex: "settlementTime",
          key: "settlementTime",
          width: 150
        },
        {
          title: "标识类型",
          dataIndex: "orderSignType",
          key: "orderSignType",
          width: 120,
          scopedSlots: { customRender: "orderSignType" }
        },
        { title: "标识", dataIndex: "orderSign", key: "orderSign", width: 120 },
        {
          title: "订单数量",
          dataIndex: "orderAmount",
          key: "orderAmount",
          width: 120
        },
        {
          title: "订单金额（元）",
          dataIndex: "orderPrice",
          key: "orderPrice",
          width: 120
        },
        { title: "到期时间", dataIndex: "expDay", key: "expDay", width: 150 },
        {
          title: "备注",
          dataIndex: "orderRemarks",
          key: "orderRemarks",
          width: 300
        }
      ],
      data: [{}],
      price: "",
      pagination: {
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => {
          return `共 ${total} 条`;
        },
        onShowSizeChange: (current, pageSize) => {
          console.log(current);
          this.pagination.defaultCurrent = current;
          this.pagination.defaultPageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = pageSize;
          this.paramsCheck(params);
          this.getOrderInfo(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          console.log(current);
          this.pagination.defaultCurrent = current;
          this.pagination.defaultPageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.paramsCheck(params);
          this.getOrderInfo(params);
          // this.getList()
        } // 点击页码事件
      },
      fifter: "companyId",
      fifterList: [
        { value: "companyId", name: "公司ID" },
        { value: "companyName", name: "公司名称" },
        { value: "account", name: "主账号" },
        { value: "companyArg", name: "ARG" }
      ],
      mealList: [],
      mealesList: [],
      mealType: true,
      locale: {
        // emptyText:'暂无数据'
      },
      time: undefined,
      meal: undefined
    };
  },
  mounted() {
    var self = this;
    $(function() {
      setTimeout(function() {
        $(".ant-pagination").prepend(
          "<li " +
            "class=" +
            '"ant-pagination-total-text">' +
            "订单总金额：" +
            '<span class="order-text" style="color:rgba(244, 64, 36, 1)">' +
            self.price +
            "</span>" +
            " 元     </li>"
        );
      }, 50);
    });
    getData.getproductInfo().then(res => {
      console.log(res.data);
      this.mealList = res.data;
      console.log(this.$route.query);
      this.searchList.companyId = this.orderid;
      console.log(this.searchList.companyId);
      this.searchList.productKey = this.$route.query.productKey;
      this.mealChange(this.searchList.productKey);
      this.searchList.mealKey = this.$route.query.mealKey;
      // this.searchList.orderTimeStart = '2020-01'+'-01 00:00:00'
      // this.searchList.orderTimeEnd = moment().endOf('month').format('YYYY-MM-DD') + " 23:59:59";
      this.searchList.orderTimeStart = this.$route.query.selectTime
        ? this.$route.query.selectTime + "-01 00:00:00"
        : moment().format("YYYY-MM") + "-01 00:00:00";
      this.searchList.orderTimeEnd = this.$route.query.selectTime
        ? moment(this.$route.query.selectTime)
            .endOf("month")
            .format("YYYY-MM-DD") + " 23:59:59"
        : moment()
            .endOf("month")
            .format("YYYY-MM-DD") + " 23:59:59";
      console.log(this.searchList.orderTimeStart, this.searchList.orderTimeEnd);
      this.time = [
        moment(this.searchList.orderTimeStart),
        moment(this.searchList.orderTimeEnd).endOf("month")
      ];
      this.screenBtn();
    });
  },
  methods: {
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 400;
    },
    tabsChange(v) {
      console.log(v);
      console.log(this.searchList.tabs);
    },
    fifterChange(v) {
      console.log(v);
    },
    timeChange(date, dateString) {
      console.log(date, dateString);
      if (dateString[0] == "") {
        this.searchList.orderTimeStart = "";
      } else {
        this.searchList.orderTimeStart = dateString[0] + " 00:00:00";
      }
      if (dateString[1] == "") {
        this.searchList.orderTimeEnd = "";
      } else {
        this.searchList.orderTimeEnd = dateString[1] + " 23:59:59";
      }
    },
    screenBtn() {
      var params = JSON.parse(JSON.stringify(this.searchList));
      this.paramsCheck(params);
      console.log(params);
      this.getOrderInfo(params);
    },
    paramsCheck(params) {
      if (this.fifter == "companyId") {
        params.companyId = this.searchList.companyId;
      } else if (this.fifter == "companyName") {
        params.payCompanyName = this.searchList.companyId;
        params.companyId = null;
      } else if (this.fifter == "account") {
        params.openAccount = this.searchList.companyId;
        params.companyId = null;
      } else if (this.fifter == "companyArg") {
        params.payCompanyArg = this.searchList.companyId;
        params.companyId = null;
      }
      if (params.mealKey == undefined) {
        params.mealKey = null;
      }
      if (params.productKey == undefined) {
        params.productKey = null;
      }
      if (params.orderSignType == undefined) {
        params.orderSignType = null;
      }
      if (params.companyId == "") {
        params.companyId = null;
      }
      if (params.payCompanyName == "") {
        params.payCompanyName = null;
      }
      if (params.openAccount == "") {
        params.openAccount = null;
      }
      if (params.payCompanyArg == "") {
        params.payCompanyArg = null;
      }
      if (params.orderId == "") {
        params.orderId = null;
      }
      if (params.orderSign == "") {
        params.orderSign = null;
      }
      if (params.orderTimeEnd == "") {
        params.orderTimeEnd = null;
      }
      if (params.orderTimeStart == "") {
        params.orderTimeStart = null;
      }
    },
    getOrderInfo(params) {
      getData.getorderInfo(params).then(res => {
        console.log(res);
        this.pagination.total = res.data.total;
        this.data = res.data.list;
        this.price = res.data.sumPrice;
        var self = this;
        console.log(this.data.length);
        for (var i = 0; i < this.data.length; i++) {
          this.data[i].num =
            i +
            1 +
            (this.pagination.defaultCurrent - 1) *
              this.pagination.defaultPageSize;
        }
        $(function() {
          if (self.data.length == 0) {
            return;
          }
          if (
            $(".ant-pagination .ant-pagination-total-text span").hasClass(
              "order-text"
            )
          ) {
            $(".order-text").html(self.price);
          } else {
            $(".ant-pagination").prepend(
              "<li " +
                "class=" +
                '"ant-pagination-total-text">' +
                "订单总金额：" +
                '<span class="order-text" style="color:rgba(244, 64, 36, 1)">' +
                self.price +
                "</span>" +
                " 元     </li>"
            );
            $(".order-text").html(self.price);
          }
        });
      });
    },
    mealChange(v) {
      console.log(v);
      if (v == undefined) {
        this.mealType = true;
        this.searchList.mealKey = undefined;
      }
      for (let item of this.mealList) {
        if (v == item.productKey) {
          this.mealesList = item.mealList;
          this.mealType = false;
          this.searchList.mealKey = undefined;
        }
      }
    },
    clearScreen() {
      this.time = undefined;
      this.mealType = true;
      this.searchList = {
        companyId: "",
        productKey: undefined,
        mealKey: undefined,
        orderId: "",
        orderSign: "",
        orderSignType: undefined,
        orderTimeEnd: "",
        orderTimeStart: "",
        pageNum: this.pagination.defaultCurrent,
        pageSize: this.pagination.defaultPageSize
      };
      this.screenBtn();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderInformation {
  display: block;
  position: relative;
}
</style>



