<template>
  <div class="interfaceList reguser">
    <div v-if="hasPermissions == true" style="width:100%;height:100%">
      <div class="interface-head">注册客户</div>
      <div class="page-box">
        <div class="clear search-condition-box">
          <div
            class="search-condition-box-item search-condition-box-item-fifter"
            style="font-size: 0;"
          >
            <a-select
              v-model="fifter"
              style="width: 95px;display: inline-block;"
              @change="fifterChange"
            >
              <a-select-option
                v-for="(item,index)  in  fifterList"
                :key="index"
                :title="item.name"
                :value="item.value"
              >{{item.name}}</a-select-option>
            </a-select>
            <a-input style="width: 105px;" placeholder="请输入" v-model="searchList.companyId" />
          </div>
          <div class="search-condition-box-item">
            <a-select
              v-model="searchList.belongSaleName"
              style="width: 100%;"
              allowClear
              placeholder="客户经理"
              @change="managerChange"
            >
              <a-select-option
                v-for="(item,index)  in  managerList"
                :key="index"
                :title="item.belongSaleName"
                :value="item.belongSaleName"
              >{{item.belongSaleName}}</a-select-option>
            </a-select>
          </div>
          <div class="search-condition-box-item">
            <a-select
              v-model="searchList.facilitatorName"
              style="width: 100%;"
              allowClear
              placeholder="服务商"
              @change="serviceChange"
            >
              <a-select-option
                v-for="(item,index)  in  serviceList"
                :key="index"
                :title="item.facilitatorName"
                :value="item.facilitatorName"
              >{{item.facilitatorName}}</a-select-option>
            </a-select>
          </div>
          <div class="search-condition-box-item">
            <a-range-picker format="YYYY-MM-DD" v-model="time" @change="timeChange" />
          </div>
          <div class="search-condition-box-item">
            <a-select
              v-model="cityList.province"
              style="width: 95px;display: inline-block;"
              allowClear
              placeholder="省"
              @change="provenceChange"
            >
              <a-select-option
                v-for="(item,index)  in  cities"
                :key="index"
                :title="item.value"
                :value="item.value"
              >{{item.value}}</a-select-option>
            </a-select>
            <a-select
              v-model="cityList.city"
              style="width: 95px;display: inline-block;margin-left: 6px;"
              :disabled="cityType"
              allowClear
              placeholder="市"
              @change="cityChange"
            >
              <a-select-option
                v-for="(item,index)  in  chooseCities"
                :key="index"
                :title="item.value"
                :value="item.value"
              >{{item.value}}</a-select-option>
            </a-select>
          </div>
          <div class="search-condition-box-item">
            <a-select
              v-model="searchList.firstChannel"
              style="width: 100%;"
              allowClear
              placeholder="渠道"
              @change="channelChange"
            >
              <a-select-option
                v-for="(item,index)  in  channelList"
                :key="index"
                :title="item.firstChannel"
                :value="item.firstChannel"
              >{{item.firstChannel}}</a-select-option>
            </a-select>
          </div>
          <div class="search-condition-box-item">
            <a-button @click="screenClear">取消筛选</a-button>
            <a-button type="primary" @click="screenBtn">筛选</a-button>
          </div>
        </div>
        <div class="form-box" style="height:calc(100vh - 300px)">
          <a-spin :spinning="table_spinning" tip="数据加载中...">
            <a-table
              rowKey="num"
              :pagination="pagination"
              :columns="columns"
              :locale="locale"
              :dataSource="data"
              :scroll="{ x:gettablewidth(), y: gettableheight() }"
            >
              <span slot="companyName" slot-scope="text" href="javascript:;">
                <span v-html="text"></span>
              </span>
              <span slot="account" slot-scope="text" href="javascript:;">
                <span v-html="text"></span>
              </span>
              <span slot="kfxt" slot-scope="text">
                <span v-if="text == 1">已开通</span>
                <span v-if="text == 2">未开通</span>
              </span>
              <a
                :href="text"
                target="_blank"
                rel="noreferrer"
                slot="companyWeb"
                slot-scope="text"
              >{{text}}</a>
            </a-table>
          </a-spin>
        </div>
      </div>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>

<script>
import * as getData from "../api/api";
import moment from "moment";
import city from "../assets/js/cities";
import axios from "axios";
export default {
  name: "regUser",
  created() {
    this.cities = city;
    // axios.defaults.headers['token'] = '1'
  },
  data() {
    return {
      hasPermissions: false,
      searchList: {
        belongSaleName: undefined,
        facilitatorName: undefined,
        firstChannel: undefined,
        companyId: "",
        companyName: "",
        account: "",
        companyArg: "",
        registerTimeEnd: "",
        registerTimeStart: "",
        pageNum: 1,
        pageSize: 20
      },
      formList: [{ id: "1", name: "全部" }],
      managerList: [],
      serviceList: [],
      channelList: [],
      fifter: "companyId",
      fifterList: [
        { value: "companyId", name: "公司ID" },
        { value: "companyName", name: "公司名称" },
        { value: "account", name: "主账号" },
        { value: "companyArg", name: "ARG" }
      ],
      cities: [],
      chooseCities: [],
      cityList: {
        province: undefined,
        city: undefined
      },
      cityType: true,
      time: undefined,
      columns: [
        { title: "序号", dataIndex: "num", key: "num", width: 50 },
        {
          title: "公司名称",
          dataIndex: "companyName",
          key: "companyName",
          width: 300,
          scopedSlots: { customRender: "companyName" }
        },
        {
          title: "主账号 ID",
          dataIndex: "accountId",
          key: "accountId",
          width: 120
        },
        {
          title: "主账号",
          dataIndex: "account",
          key: "account",
          width: 300,
          scopedSlots: { customRender: "account" }
        },
        {
          title: "咨询页面",
          dataIndex: "companyWeb",
          key: "companyWeb",
          width: 300,
          scopedSlots: { customRender: "companyWeb" }
        },
        {
          title: "注册时间",
          dataIndex: "registerTime",
          key: "registerTime",
          width: 150
        },
        {
          title: "注册手机号码",
          dataIndex: "mobile",
          key: "mobile",
          width: 120
        },
        {
          title: "注册 IP 地区",
          dataIndex: "address",
          key: "address",
          width: 120
        },
        {
          title: "首次渠道",
          dataIndex: "firstChannel",
          key: "firstChannel",
          width: 120
        },
        {
          title: "首次关键词",
          dataIndex: "firstKeyword",
          key: "firstKeyword",
          width: 120
        },
        {
          title: "客服经理",
          dataIndex: "belongSaleName",
          key: "belongSaleName",
          width: 120
        },
        {
          title: "服务商",
          dataIndex: "facilitatorName",
          key: "facilitatorName",
          width: 150
        },
        {
          title: "是否开通客服系统",
          dataIndex: "kfxt",
          key: "kfxt",
          width: 120,
          scopedSlots: { customRender: "kfxt" }
        }
      ],
      data: [{}],
      pagination: {
        current: 1,
        pageSize: 20,
        defaultCurrent: 1, // 默认当前页数
        defaultPageSize: 20, // 默认当前页显示数据的大小
        showSizeChanger: true,
        total: "",
        // showQuickJumper: true,
        pageSizeOptions: ["20", "40", "100"],
        showTotal: total => `共 ${total} 条`, // 显示总数
        onShowSizeChange: (current, pageSize) => {
          this.pagination.current = current;
          this.pagination.pageSize = pageSize;
          this.searchList.pageNum = current;
          this.searchList.pageSize = pageSize;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = 1;
          params.pageSize = pageSize;
          this.paramsCheck(params);
          this.regUserList(params);
          // this.getList()  //显示列表的接口名称
        },
        // 改变每页数量时更新显示
        onChange: (current, size) => {
          this.pagination.current = current;
          this.pagination.pageSize = size;
          this.searchList.pageNum = current;
          this.searchList.pageSize = size;
          var params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = current;
          params.pageSize = size;
          this.paramsCheck(params);
          this.regUserList(params);
          // this.getList()
        } // 点击页码事件
      },

      locale: {
        // emptyText:'暂无数据'
      },
      table_spinning: false
    };
  },
  mounted() {
    document.title = "注册客户";
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
      //渲染服务商等列表
      getData.getRegSfc({}).then(res => {
        this.managerList = res.data.saleList;
        this.serviceList = res.data.facilitatorList;
        this.channelList = res.data.channelList;
        // this.serviceList.map((ele,index) => {
        //   if(!ele.facilitatorName){
        //     ele.facilitatorName = ele.facilitatorId
        //   }
        // })
      });
      let today = moment().format("YYYY-MM-DD");
      let last3 = moment()
        .subtract("days", 3)
        .format("YYYY-MM-DD"); //3天前
      this.time = [moment(last3, "YYYY-MM-DD"), moment(today, "YYYY-MM-DD")];
      this.searchList.registerTimeStart = last3 + " 00:00:00";
      this.searchList.registerTimeEnd = today + " 23:59:59";
      var params = {
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize,
        registerTimeStart: last3 + " 00:00:00",
        registerTimeEnd: today + " 23:59:59"
      };
      this.regUserList(params);
    }
  },
  methods: {
    // 替换字符串中的空格和回车
    replace_null(str) {
      return str
        .replace(/\ +/g, "")
        .replace(/[ ]/g, "")
        .replace(/[\r\n]/g, "");
    },
    // 表格宽度
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w = w + i.width;
      }
      return w;
    },
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 250;
    },
    // 注册用户列表
    regUserList(params) {
      this.table_spinning = true;
      getData.regUserList(params).then(res => {
        this.table_spinning = false;
        this.pagination.total = res.data.total;
        this.data = res.data.list;
        for (var i = 0; i < this.data.length; i++) {
          this.data[i].num =
            i + 1 + (this.pagination.current - 1) * this.pagination.pageSize;
        }
        if (this.searchList.companyId == "") {
          return;
        }
        if (this.fifter == "companyId") {
          this.data.map(item => {
            //遍历
            item.companyId = this.brightKeyword(item.companyId);
          }); //到这里search方法结束
        } else if (this.fifter == "companyName") {
          this.data.map(item => {
            //遍历
            item.companyName = this.brightKeyword(item.companyName);
          }); //到这里search方法结束
        } else if (this.fifter == "account") {
          this.data.map(item => {
            //遍历
            item.account = this.brightKeyword(item.account);
          }); //到这里search方法结束
        } else if (this.fifter == "companyArg") {
          this.data.map(item => {
            //遍历
            item.companyArg = this.brightKeyword(item.companyArg);
          }); //到这里search方法结束
        }
      });
    },
    brightKeyword(val) {
      let keyword = this.replace_null(this.searchList.companyId); //获取输入框输入的内容
      if (val.indexOf(keyword) !== -1) {
        //判断这个字段中是否包含keyword
        //如果包含的话，就把这个字段中的那一部分进行替换成html字符
        return val.replace(keyword, `<font color='#42cccc'>${keyword}</font>`);
      } else {
        return val;
      }
    },
    managerChange(v) {},
    serviceChange(v) {},
    channelChange(v) {},
    fifterChange(v) {
      this.searchList.choose = v;
    },
    timeChange(date, dateString) {
      console.log(this.time, date, dateString);
      if (dateString[0] == "") {
        this.searchList.registerTimeStart = "";
      } else {
        this.searchList.registerTimeStart = dateString[0] + " 00:00:00";
      }
      if (dateString[1] == "") {
        this.searchList.registerTimeEnd = "";
      } else {
        this.searchList.registerTimeEnd = dateString[1] + " 23:59:59";
      }
    },
    provenceChange(v) {
      if (v == undefined) {
        this.cityList.city = undefined;
        this.cityType = true;
      }
      for (let item of this.cities) {
        if (v == item.value) {
          this.cityList.city = undefined;
          this.chooseCities = item.children;
          if (!item.children) {
            this.cityType = true;
          } else {
            this.cityType = false;
          }
        }
      }
    },
    cityChange(v) {},
    screenBtn() {
      this.pagination.current = 1;
      var params = JSON.parse(JSON.stringify(this.searchList));
      params.pageNum = 1;
      console.log(params);
      this.paramsCheck(params);
      this.regUserList(params);
    },
    screenClear() {
      this.cityType = true;
      this.time = undefined;
      this.pagination.current = 1;
      this.searchList = {
        belongSaleName: undefined,
        facilitatorName: undefined,
        firstChannel: undefined,
        companyId: "",
        companyName: "",
        account: "",
        companyArg: "",
        registerTimeEnd: "",
        registerTimeStart: "",
        pageNum: 1,
        pageSize: this.pagination.pageSize
      };
      this.cityList = {
        province: undefined,
        city: undefined
      };
      this.screenBtn();
    },
    paramsCheck(params) {
      if (this.fifter == "companyId") {
        params.companyId = this.replace_null(this.searchList.companyId);
      } else if (this.fifter == "companyName") {
        params.companyName = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      } else if (this.fifter == "account") {
        params.account = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      } else if (this.fifter == "companyArg") {
        params.companyArg = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      }
      if (params.belongSaleName == undefined) {
        params.belongSaleName = null;
      }
      if (params.facilitatorName == undefined) {
        params.facilitatorName = null;
      }
      if (params.firstChannel == undefined) {
        params.firstChannel = null;
      }
      if (params.registerTimeEnd == "") {
        params.registerTimeEnd = null;
      }
      if (params.registerTimeStart == "") {
        params.registerTimeStart = null;
      }
      if (params.companyId == "") {
        params.companyId = null;
      }
      if (params.companyName == "") {
        params.companyName = null;
      }
      if (params.companyArg == "") {
        params.companyArg = null;
      }
      if (params.account == "") {
        params.account = null;
      }
      params.province = this.cityList.province;
      params.city = this.cityList.city;
      if (params.city == undefined) {
        params.city = null;
      }
      if (params.province == undefined) {
        params.province = null;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



