export const updateuserid = ({ commit }, userid) => {
    commit('UPDATEUSERID', userid)
}
export const updateuserinfo = ({ commit }, user) => {
    commit('UPDATEUSERINFO', user)
}
export const updatepermission = ({ commit }, pagepermission) => {
    commit('UPDATEPAGEPERMISSION', pagepermission)
}

export const updatesaastoken = ({ commit }, saas_token) => {
    commit('UPDATASAASTOKEN', saas_token)
}



