<template>
  <div class="aside">
    <div class="aside-head">
      <div>云客服管理平台</div>
    </div>
    <!-- <div
      class="aside-head-item"
      v-for="item in asideHead"
      :key="item.name"
      :class="{
        'aside-head-item-active':
          currentactive == item.route || currentactive == item.routemore
      }"
      @click="$router.push({ name: item.route })"
    >
      {{ item.name }}
    </div> -->
    <div>
      <a-menu
        mode="inline"
        :open-keys="openKeys"
        style="width: 100%; background-color: #f7f8fa"
        :default-selected-keys="selekeys"
        @openChange="onOpenChange"
        @select="titlechange"
      >
        <a-sub-menu key="sub1">
          <span slot="title"
            ><a-icon type="usergroup-add" /><span>人工质检</span></span
          >
          <a-menu-item key="Manualinspection" title="人工质检">
            <span>人工质检</span>
          </a-menu-item>
          <a-menu-item key="Keywords" title="关键词统计">
            <span>关键词统计</span>
          </a-menu-item>
          <a-menu-item key="industryManage" title="行业管理">
            <span>行业管理</span>
          </a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="sub2">
          <span slot="title"
            ><a-icon type="appstore" /><span>客户管理</span></span
          >
          <a-menu-item key="activeUser" title="活跃客户">
            <span>活跃客户</span>
          </a-menu-item>
          <a-menu-item key="regUser" title="注册客户">
            <span>注册客户</span>
          </a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="sub4">
          <span slot="title"
            ><a-icon type="area-chart" /><span>数据分析</span></span
          >
          <a-menu-item key="meal_MonthReport" title="套餐业绩月报表">
            <span>套餐业绩月报表</span>
          </a-menu-item>
          <a-menu-item key="packageMonthReport" title="套餐分类业绩报表">
            <span>套餐分类业绩报表</span>
          </a-menu-item>
          <a-menu-item key="thirdMonthReport" title="第三方产品结算月报表">
            <span>第三方产品结算月报表</span>
          </a-menu-item>
          <a-menu-item key="Queueanalysis" title="队列分析">
            <span>队列分析</span>
          </a-menu-item>
          <a-menu-item key="Openaccount" title="开户比">
            <span>开户比</span>
          </a-menu-item>
          <a-menu-item key="Detailedmrr" title="详细 MRR">
            <span>详细 MRR</span>
          </a-menu-item>
          <a-menu-item key="Channel" title="渠道">
            <span>渠道</span>
          </a-menu-item>
          <a-menu-item key="Newview" title="新页面">
            <span>新页面</span>
          </a-menu-item>
          <a-menu-item key="Statistics" title="行业指标统计">
            <span>行业指标统计</span>
          </a-menu-item>
          <!-- <a-menu-item key="Industry_QApool" title="行业指标统计">
            <span>行业问答库</span>
          </a-menu-item> -->
        </a-sub-menu>
        <a-menu-item key="PermissionSetting" title="权限管理">
          <a-icon type="setting" />
          <span>权限管理</span>
        </a-menu-item>
      </a-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "myaside",
  data() {
    return {
      selekeys: ["activeUser"],
      rootSubmenuKeys: ["sub1", "sub2", "sub4"],
      openKeys: ["sub1"],

      asideHead: [
        {
          name: "人工质检",
          route: "Manualinspection",
          routemore: "Inspectionresults",
        },
        {
          name: "注册客户",
          route: "regUser",
          routemore: "regUser",
        },
        {
          name: "活跃客户",
          route: "activeUser",
          routemore: "activeUser",
        },
        {
          name: "套餐业绩月报表",
          route: "meal_MonthReport",
          routemore: "meal_MonthReport",
        },
        {
          name: "套餐分类业绩报表",
          route: "packageMonthReport",
          routemore: "packageYearReport",
        },
        {
          name: "队列分析",
          route: "Queueanalysis",
          routemore: "Queueanalysis",
        },
        {
          name: "开户比",
          route: "Openaccount",
          routemore: "Openaccount",
        },
        {
          name: "详细MRR",
          route: "Detailedmrr",
          routemore: "Detailedmrr",
        },
        {
          name: "渠道",
          route: "Channel",
          routemore: "Channel",
        },
        {
          name: "第三方产品结算月报表",
          route: "thirdMonthReport",
          routemore: "thirdMonthReport",
        },
        {
          name: "行业指标统计",
          route: "Statistics",
          routemore: "Statistics",
        },
        // {
        //   name: "行业指标统计",
        //   route: "IndustryTargetCount",
        //   routemore: "IndustryTargetCount"
        // },
        {
          name: "关键词统计",
          route: "Keywords",
          routemore: "Keywords",
        },

        {
          name: "行业问答库",
          route: "Industry_QApool",
          routemore: "Industry_QApool",
        },
        {
          name: "权限设置",
          route: "PermissionSetting",
          routemore: "PermissionSetting",
        },
        {
          name: "新页面",
          route: "Newview",
          routemore: "Newview",
        },
      ],
      currentactive: "regUser",
    };
  },
  created() {
    //console.log(window.location);
    //this.updateactive();
    var arrt = window.location.hash.indexOf("?");
    //console.log(arrt);
    if (arrt == -1) {
      this.selekeys[0] = window.location.hash.slice(2);
    } else {
      this.selekeys[0] = window.location.hash.slice(2, arrt);
    }

    //console.log(this.selekeys);
    this.changeselekys();
  },
  methods: {
    updateactive() {
      this.currentactive = this.$route.name;
    },
    onOpenChange(openKeys) {
      console.log(openKeys);
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },
    titlechange(obj) {
      console.log(obj);
      this.$router.push({ name: obj.key });
      this.selekeys[0] = obj.key;
    },
    changeselekys() {
      if (
        this.selekeys[0] == "Manualinspection" ||
        this.selekeys[0] == "Keywords" ||
        this.selekeys[0] == "industryManage"
      ) {
        this.openKeys[0] = "sub1";
      } else if (
        this.selekeys[0] == "activeUser" ||
        this.selekeys[0] == "regUser"
      ) {
        this.openKeys[0] = "sub2";
      } else {
        this.openKeys[0] = "sub4";
      }
    },
  },
  watch: {
    // 如果路由有变化，更新列表
    //$route: "changeselekys"
  },
};
</script>

<style scoped>
.aside {
  position: relative;
  z-index: 1;
  margin-left: 60px;
}
</style>



