<template>
  <div style="width:100%;" class="twobox" @click="boxflag = false">
    <div class="clear search-condition-box">
      <div class="search-condition-box-item">
        {{ searchList.gongsiname }}
      </div>
      <div class="search-condition-box-item">
        <a-input v-model="searchList.gongsiid" @change="changeid" />
        <!-- <div style="width: 105px;">公司ID：1111</div> -->
      </div>
      <div class="search-condition-box-item">
        <a-range-picker
          format="YYYY-MM-DD"
          v-model="datatime"
          @change="timeChange"
        />
      </div>
      <div class="search-condition-box-item" style="width: auto">
        <a-button @click="clearScreen">取消筛选</a-button>
        <a-button type="primary" @click="screenBtn">筛选</a-button>
      </div>
    </div>
    <!-- :row-key="record => record.login.uuid" -->
    <div>
      <a-table
        :rowKey="record => record.dataIndex"
        style="width:100%;"
        :columns="columns"
        :data-source="data"
        :pagination="pagination"
        :loading="loading"
        @change="handleTableChange"
        :scroll="{ x: gettablewidth(), y: gettableheight() }"
      >
        <span slot="talkMessage" slot-scope="text, name, index">
          <span
            v-html="name.talkMessage"
            @mouseover="changeboxflag(name.id, text, index)"
          ></span>
        </span>
      </a-table>
      <div class="totashu">共计：{{ totalshu }} 条</div>
    </div>
    <div class="danbox" v-if="boxflag">
      <div v-for="item in childdata" :key="item.id" class="box_centent">
        <div>{{ item.talkTime }}</div>
        <div v-html="item.talkMessage"></div>
      </div>
    </div>
  </div>
</template>
<script>
import * as getData from "../api/api";
const columns = [
  { title: " ", dataIndex: "index", key: "num", show: true, width: 150 },
  {
    title: "聊天内容",
    dataIndex: "talkMessage",
    key: "talkMessage",
    // sorter: true,
    // slots: { title: '聊天内容' },
    // width: '20%',
    scopedSlots: { customRender: "talkMessage" }
  },

  {
    title: "聊天时间",
    dataIndex: "talkTime",
    key: "talkTime",
    value: ""
  }
];

export default {
  data() {
    return {
      searchList: {
        orderId: "ddd",
        companyId: "1111",
        gongsiid: "公司ID:1111",
        gongsiname: ""
      },

      boxflag: false,

      data: [],
      dataset: [],
      alldata: [],
      childdata: [],
      pagination: {},
      loading: false,
      columns,

      starttime: "2000-09-02 09:58:22",
      endtime: "2100-09-02 09:58:22",

      datatime: "",
      record: "",
      totalshu: ""
    };
  },
  mounted() {
    this.starttime = this.getLastDate();
    this.endtime = this.getTodayDate();
    (this.searchList.companyId = this.$route.query.companyId),
      (this.searchList.gongsiid = "公司ID:" + this.$route.query.companyId);

    this.getdata({
      pageNum: 1,
      talkMessageTimeEnd: this.endtime,
      talkMessageTimeStart: this.starttime
    });
  },
  methods: {
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        w += i.width;
      }
      return w;
    },
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 250;
    },
    //改变公司id
    changeid(e) {
      //console.log(this.searchList.gongsiid)
      this.searchList.companyId = this.searchList.gongsiid.replace(
        /[^0-9]/gi,
        ""
      );
      //console.log(this.searchList.companyId)
    },

    customRow() {
      return {
        on: {
          mouseenter: e => {
            //console.log(e.originalTarget.dataset.rowKey)
            //console.log(dataIndex)
            //console.log(this.alldata[e.originalTarget.dataset.rowKey])
            //console.log(e)
            if (e.target.dataset.rowKey) {
              this.boxflag = true;
              this.childdata = this.alldata[e.target.dataset.rowKey];
            }
          }, // 鼠标移入行
          mouseout: e => {
            //console.log(e)
            if (e.pageY >= 662 || e.pageY <= 176) this.boxflag = false;
          }
        }
      };
    },
    changeboxflag(id, text, index) {
      //console.log(id,index)
      this.alldata.forEach(item => {
        if (id == item[0].id) {
          this.childdata = item;
          this.boxflag = true;
        }
      });
    },

    changeboxfalse(index) {
      //console.log(index)
      if (index == 0 || index == this.data.length - 1) {
        this.boxflag = false;
      }
    },

    handleTableChange(pagination, filters, sorter) {
      //console.log(pagination);
      const pager = { ...this.pagination };
      pager.current = pagination.current;
      this.pagination = pager;
      this.getdata({
        pageNum: pagination.current,
        pageSize: pagination.pageSize,
        talkMessageTimeEnd: this.endtime,
        talkMessageTimeStart: this.starttime
      });
    },
    //请求数据
    getdata(params = {}) {
      this.loading = true;
      var arr = [];
      getData
        .getchatrecord({
          companyId: this.searchList.companyId,
          pageSize: 10,
          ...params
        })
        .then(res => {
          //console.log(res)
          if (res.code == 200) {
            const pagination = { ...this.pagination };
            // console.log(Math.ceil(res.data.list.length/10))
            this.searchList.gongsiname = "公司名称：" + res.data.company_name;
            pagination.total = res.data.total;
            //console.log(pagination.total)
            this.loading = false;
            this.alldata = res.data.list;
            this.totalshu = res.data.total;
            res.data.list.forEach(item => {
              arr.push(item[0]);
            });

            this.data = arr;
            this.pagination = pagination;
          } else {
            this.loading = false;
            this.data = [];
            this.searchList.gongsiname = "公司名称：";
          }
        });
    },

    changechild() {
      //console.log(1)
    },

    //补零操作
    test(str) {
      if (str < 10) {
        return "0" + str;
      } else {
        return str;
      }
    },
    //格式转化
    timechangestart(str) {
      return (
        str.getFullYear() +
        "-" +
        this.test(str.getMonth() + 1) +
        "-" +
        this.test(str.getDate()) +
        " " +
        "00" +
        ":" +
        "00" +
        ":" +
        "00"
      );
    },
    timechangeend(str) {
      return (
        str.getFullYear() +
        "-" +
        this.test(str.getMonth() + 1) +
        "-" +
        this.test(str.getDate()) +
        " " +
        "23" +
        ":" +
        "59" +
        ":" +
        "59"
      );
    },
    //筛选
    screenBtn() {
      this.getdata({
        pageNum: 1,
        talkMessageTimeEnd: this.endtime,
        talkMessageTimeStart: this.starttime
      });
    },

    //取消筛选
    clearScreen() {
      this.starttime = this.getLastDate();
      this.endtime = this.getTodayDate();
      this.datatime = "";
      this.getdata({
        pageNum: 1,
        talkMessageTimeEnd: this.endtime,
        talkMessageTimeStart: this.starttime
      });
    },
    //改变时间
    timeChange(e) {
      //console.log(e)
      this.starttime = this.timechangestart(e[0]._d);
      this.endtime = this.timechangeend(e[1]._d);
      //console.log(this.starttime)
      //console.log(this.endtime)
    },

    //获取十天前
    getLastDate() {
      var date = new Date();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hh = date.getHours();
      var mm = date.getMinutes();
      var ss = date.getSeconds();
      var year = date.getFullYear();

      var ym;
      var year1;
      day = day - 10;
      if (day <= 0) {
        day = mygetday(day, month, year);
        var months = mygetmonth(month);
        ym = months.split(",");
        year1 = ym[0];
        day = FormatDate(day);
        month = FormatDate(ym[1]);
        return (
          year1 +
          "-" +
          this.test(month) +
          "-" +
          this.test(day) +
          " " +
          this.test(hh) +
          ":" +
          this.test(mm) +
          ":" +
          this.test(ss)
        );
      } else {
        return (
          year +
          "-" +
          this.test(month) +
          "-" +
          this.test(day) +
          " " +
          this.test(hh) +
          ":" +
          this.test(mm) +
          ":" +
          this.test(ss)
        );
      }
    },
    //获取今天
    getTodayDate() {
      var date = new Date();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      var hh = date.getHours();
      var mm = date.getMinutes();
      var ss = date.getSeconds();
      var year = date.getFullYear();

      return (
        year +
        "-" +
        this.test(month) +
        "-" +
        this.test(day) +
        " " +
        this.test(hh) +
        ":" +
        this.test(mm) +
        ":" +
        this.test(ss)
      );
    },

    mygetday(day, month, year) {
      var daynum = 30;
      if (
        month == 2 ||
        month == 4 ||
        month == 6 ||
        month == 8 ||
        month == 9 ||
        month == 1 ||
        month == 11
      ) {
        daynum = 31;
      } else if (month == 3) {
        if (year % 4 == 0) {
          daynum = 29;
        } else {
          daynum = 28;
        }
      }
      day = daynum + day;
      return day;
    },

    mygetmonth(month) {
      var year = new Date().getFullYear();
      if (month == 1) {
        month = 12;
        year = year - 1;
      } else {
        month = month - 1;
      }
      return year + "," + month;
    },
    FormatDate(d) {
      if (d.toString().length == 1) {
        d = "0" + d;
      }
      return d;
    }
  }
};
</script>
<style scoped>
.search-condition-box-item {
  margin-left: 50px;
  margin-top: 40px;
  margin-bottom: 30px;
}
.app-show {
  width: 100%;
}
.twobox {
  position: fixed;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.danbox {
  width: 60%;
  height: 70%;
  overflow: auto;
  position: fixed;
  left: 30%;
  top: 18%;
  background: floralwhite;
}
.box_centent {
  margin: 20px;
}
.box_centent div:first-of-type {
  margin: 5px;
}
.box_centent div:nth-of-type(2) {
  /* float: right; */
}
.totashu {
  float: right;
  margin: 20px;
}
</style>



