<style scoped lang='scss'>
.portray_information {
  width: 770px;
  margin: 5px 0;
  .portray_item {
    margin-bottom: 20px;
    .portray_item_name {
      font-size: 16px;
      color: rgba(40, 51, 75, 1);
      line-height: 24px;
      margin-bottom: 10px;
    }
    .item_msg {
      display: flex;
      background: #fff;
      padding: 15px 15px 5px;
      flex-wrap: wrap;
      .item_msg_info {
        width: 185px;
        margin-bottom: 10px;
        .info_top {
          font-size: 12px;
          color: rgba(98, 119, 140, 1);
          line-height: 18px;
        }
        .info_bottom {
          font-size: 12px;
          color: rgba(40, 51, 75, 1);
          line-height: 18px;
        }
      }
    }
    .portray_second_item {
      margin-bottom: 15px;
      .second_item_title {
        padding-left: 15px;
        width: 770px;
        height: 32px;
        background: rgba(206, 213, 224, 1);
        font-size: 14px;
        color: rgba(40, 51, 75, 1);
        line-height: 32px;
      }
    }
  }
}
</style>
<template>
  <div class="portray_information">
    <div class="portray_item">
      <div class="portray_item_name">基础信息</div>
      <div class="item_msg">
        <div class="item_msg_info">
          <div class="info_top">手机</div>
          <div class="info_bottom">{{PortraitBasicinfo.isMobile == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">微信</div>
          <div class="info_bottom">{{PortraitBasicinfo.isWechat == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">QQ</div>
          <div class="info_bottom">{{PortraitBasicinfo.isQq == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">设备 ID</div>
          <div class="info_bottom">{{PortraitBasicinfo.isDeviceId == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">粉丝 ID</div>
          <div class="info_bottom">{{PortraitBasicinfo.isFansId == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">本机认证</div>
          <div class="info_bottom">{{PortraitBasicinfo.isDeviceAuth == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">个人认证</div>
          <div class="info_bottom">{{PortraitBasicinfo.isPersonalAuth == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">企业认证</div>
          <div class="info_bottom">{{PortraitBasicinfo.isCompanyAuth == '1' ? '有' : '-'}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">地区</div>
          <div
            class="info_bottom"
          >{{ActiveCustomer.city !== null ? ActiveCustomer.city : ActiveCustomer.province !== null ? ActiveCustomer.province : "-"}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">行业</div>
          <div class="info_bottom">{{ActiveCustomer.industry || "-"}}</div>
        </div>
        <div class="item_msg_info">
          <div class="info_top">服务商</div>
          <div class="info_bottom">{{ActiveCustomer.facilitatorName || "-"}}</div>
        </div>
      </div>
    </div>

    <div class="portray_item">
      <div class="portray_item_name">购买产品/功能标签</div>
      <div class="item_msg" v-if="PortraitProductUsing.productList !== null">
        <div
          class="item_msg_info"
          v-for="item of PortraitProductUsing.productList"
          :key="item.mealKey"
        >
          <div class="info_top">{{item.meal_name}}</div>
          <div
            class="info_bottom"
          >{{item.status == 1 ? "新增" : item.status == 2 ? "减少" : item.status == 3 ? "开通" :item.status == 4 ? "停用" :"-"}}</div>
        </div>
      </div>
      <div class="item_msg" v-else>
        <div class="item_msg_info">
          <div class="info_top">暂无任何产品</div>
        </div>
      </div>
    </div>

    <div class="portray_item">
      <div class="portray_item_name">功能使用标签</div>
      <div class="portray_second_item">
        <div class="second_item_title">接待规则</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">分配方式</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.distributiveRules.first_it == "1" ? "自动分配" : PortraitFunctionUsing.distributiveRules.first_it == "2" ? "手动分配" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">接待优先</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.distributiveRules.receive_priority == "1" ? "人工客服" : PortraitFunctionUsing.distributiveRules.receive_priority == "2" ? "机器人" : PortraitFunctionUsing.distributiveRules.receive_priority == "3" ? "智能引导" : PortraitFunctionUsing.distributiveRules.receive_priority == "4" ? "场景引导" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">离线优先</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.distributiveRules.offline_priority == "1" ? "机器人" : PortraitFunctionUsing.distributiveRules.offline_priority == "2" ? "场景引导" : PortraitFunctionUsing.distributiveRules.offline_priority == "3" ? "留言" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">接待客服</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.distributiveRules.customer_service == "1" ? "指定客服" : PortraitFunctionUsing.distributiveRules.customer_service == "2" ? "区域分流" : PortraitFunctionUsing.distributiveRules.customer_service == "3" ? "搜索引擎" : PortraitFunctionUsing.distributiveRules.customer_service == "4" ? "推广链接" :"-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">PC 端设置</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">开启主动发起</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.pcOptions.initiate == "4" ? "关闭" : PortraitFunctionUsing.pcOptions.initiate == "1" || PortraitFunctionUsing.pcOptions.initiate == "2" || PortraitFunctionUsing.pcOptions.initiate == "3" ? "开启" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">主动发起方式</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.pcOptions.initiate_way == "1" ? "浮动窗口" : PortraitFunctionUsing.pcOptions.initiate_way == "2" ? "邀请框" : PortraitFunctionUsing.pcOptions.initiate_way == "3" ? "交替发起" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">客服图标</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.pcOptions.kefu_pic == "1" ? "开启" : PortraitFunctionUsing.pcOptions.kefu_pic == "2" ?"关闭" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">网络回呼</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.pcOptions.call_back == "1" ? "开启" : PortraitFunctionUsing.pcOptions.call_back == "2" ? "关闭" : "-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">移动端设置</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">开启主动发起</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mobilesOptions.initiate == "3" ? "关闭" : PortraitFunctionUsing.mobilesOptions.initiate == "1" || PortraitFunctionUsing.mobilesOptions.initiate == "2" ? "开启" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">主动发起方式</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mobilesOptions.initiate_way == "1" ? "对话框" : PortraitFunctionUsing.mobilesOptions.initiate_way == "2" ? "邀请框" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">客服图标</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mobilesOptions.kefu_pic == "1" ? "开启" : PortraitFunctionUsing.mobilesOptions.kefu_pic == "2" ? "关闭" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">网络回呼</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mobilesOptions.call_back == "1" ? "开启" : PortraitFunctionUsing.mobilesOptions.call_back == "2" ? "关闭" : "-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">互动菜单</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mobilesOptions.interactive_menu == "1" ? "开启" : PortraitFunctionUsing.mobilesOptions.interactive_menu == "2" ? "关闭" : "-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">媒体对接</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">微信公众号</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.wechat_public == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">微信小程序</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.applet_of_wechat == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">百度小程序</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.applet_of_baidu == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">今日头条</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.jinri_toutiao == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">腾讯社交广告</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.tencent_social_advertising == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">360</div>
            <div class="info_bottom">{{PortraitFunctionUsing.mediaDocking._360 == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">快手</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.mediaDocking.kuaishou == "1" ? "是" :"-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">系统设置</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">客服排班</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.service_scheduling == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">常用语</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.useful_expressions == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">访客标签</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.enclave_label == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">对话主题</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.conversation_topics == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">访客表单</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.visitors_forms == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">推广链接</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.systemOptions.promote_links == "1" ? "是" :"-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">营销</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">抽奖活动</div>
            <div class="info_bottom">{{PortraitFunctionUsing.marketing.lottery == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">退出挽留</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.marketing.exit_to_retain == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">微信群发</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.marketing.short_message_send == "1" ? "是" :"-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">机器人</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">问答库</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.robot.answer_library == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">追问线索</div>
            <div class="info_bottom">{{PortraitFunctionUsing.robot.clues_to_ask == "1" ? "是" :"-"}}</div>
          </div>
        </div>
      </div>

      <div class="portray_second_item">
        <div class="second_item_title">其他</div>
        <div class="item_msg">
          <div class="item_msg_info">
            <div class="info_top">安装APP</div>
            <div class="info_bottom">{{PortraitFunctionUsing.other.app == "1" ? "是" :"-" }}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">开发者账号</div>
            <div
              class="info_bottom"
            >{{PortraitFunctionUsing.other.developerAccount == "1" ? "是" :"-"}}</div>
          </div>
          <div class="item_msg_info">
            <div class="info_top">盗号</div>
            <div class="info_bottom">{{PortraitFunctionUsing.other.handing_over == "1" ? "是" :"-"}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "companyPortrayInformarion",
  data() {
    return {
      ActiveCustomer: {},
      PortraitBasicinfo: {},
      PortraitFunctionUsing: {
        distributiveRules: {
          first_it: "0",
          customer_service: "0",
          receive_priority: "0",
          offline_priority: "0"
        },
        marketing: {
          lottery: "0",
          exit_to_retain: "0",
          short_message_send: "0"
        },
        mediaDocking: {
          wechat_public: "0",
          applet_of_wechat: "0",
          applet_of_baidu: "0",
          jinri_toutiao: "0",
          tencent_social_advertising: "0",
          _360: "0",
          kuaishou: "0"
        },
        mobilesOptions: {
          initiate: "0",
          initiate_way: "0",
          kefu_pic: "0",
          call_back: "0",
          interactive_menu: "0"
        },
        other: {
          app: "0",
          developer_account: "0",
          handing_over: "0"
        },
        pcOptions: {
          initiate: "0",
          initiate_way: "0",
          kefu_pic: "0",
          call_back: "0"
        },
        robot: {
          answer_library: "0",
          clues_to_ask: "0"
        },
        systemOptions: {
          service_scheduling: "0",
          useful_expressions: "0",
          enclave_label: "0",
          conversation_topics: "0",
          visitors_forms: "0",
          promote_links: "0"
        }
      },
      PortraitProductUsing: {}
    };
  },
  props: ["portrainformation"],
  methods: {
    transferformat(data) {
      let formatdata = {};
      data.map(function(value, index, array) {
        for (let key in value) {
          formatdata[key] = value[key];
        }
      });
      return formatdata;
    },
    portrainformation_funinit() {
      this.PortraitFunctionUsing = {
        distributiveRules: {
          first_it: "0",
          customer_service: "0",
          receive_priority: "0",
          offline_priority: "0"
        },
        marketing: {
          lottery: "0",
          exit_to_retain: "0",
          short_message_send: "0"
        },
        mediaDocking: {
          wechat_public: "0",
          applet_of_wechat: "0",
          applet_of_baidu: "0",
          jinri_toutiao: "0",
          tencent_social_advertising: "0",
          _360: "0",
          kuaishou: "0"
        },
        mobilesOptions: {
          initiate: "0",
          initiate_way: "0",
          kefu_pic: "0",
          call_back: "0",
          interactive_menu: "0"
        },
        other: {
          app: "0",
          developer_account: "0",
          handing_over: "0"
        },
        pcOptions: {
          initiate: "0",
          initiate_way: "0",
          kefu_pic: "0",
          call_back: "0"
        },
        robot: {
          answer_library: "0",
          clues_to_ask: "0"
        },
        systemOptions: {
          service_scheduling: "0",
          useful_expressions: "0",
          enclave_label: "0",
          conversation_topics: "0",
          visitors_forms: "0",
          promote_links: "0"
        }
      };
    },
    portrainformation_basicinit() {
      this.PortraitBasicinfo = {
        isMobile: 0,
        isWechat: 0,
        isQq: 0,
        isDeviceId: 0,
        isFansId: 0,
        isDeviceAuth: 0,
        isPersonalAuth: 0,
        isCompanyAuth: 0
      };
    }
  },
  watch: {
    portrainformation: {
      deep: true,
      handler(data) {
        if (Object.keys(data).length !== 0) {
          // 企业画像公司基础数据
          this.ActiveCustomer = JSON.parse(
            JSON.stringify(data.ccsActiveCustomer)
          );
          if (data.ccsPortraitBasic !== null) {
            this.PortraitBasicinfo = JSON.parse(
              JSON.stringify(data.ccsPortraitBasic)
            );
          } else {
            this.portrainformation_basicinit();
          }
          this.PortraitFunctionUsing = JSON.parse(
            JSON.stringify(data.ccsPortraitFunctionUsing)
          );
          // 企业画像购买产品/功能标签
          this.PortraitProductUsing = JSON.parse(
            JSON.stringify(data.ccsPortraitProductUsing)
          );
          this.PortraitProductUsing.productList = JSON.parse(
            this.PortraitProductUsing.productList
          );
          // 企业画像功能使用标签处理
          if (this.PortraitFunctionUsing !== null) {
            this.PortraitFunctionUsing.distributiveRules = JSON.parse(
              this.PortraitFunctionUsing.distributiveRules
            );
            this.PortraitFunctionUsing.marketing = JSON.parse(
              this.PortraitFunctionUsing.marketing
            );
            this.PortraitFunctionUsing.mediaDocking = JSON.parse(
              this.PortraitFunctionUsing.mediaDocking
            );
            this.PortraitFunctionUsing.mobilesOptions = JSON.parse(
              this.PortraitFunctionUsing.mobilesOptions
            );
            this.PortraitFunctionUsing.other = JSON.parse(
              this.PortraitFunctionUsing.other
            );
            this.PortraitFunctionUsing.pcOptions = JSON.parse(
              this.PortraitFunctionUsing.pcOptions
            );
            this.PortraitFunctionUsing.robot = JSON.parse(
              this.PortraitFunctionUsing.robot
            );
            this.PortraitFunctionUsing.systemOptions = JSON.parse(
              this.PortraitFunctionUsing.systemOptions
            );
            // 分配规则
            if (this.PortraitFunctionUsing.distributiveRules !== null) {
              this.PortraitFunctionUsing.distributiveRules = this.transferformat(
                this.PortraitFunctionUsing.distributiveRules
              );
            } else {
              this.PortraitFunctionUsing.distributiveRules = {
                first_it: "0",
                customer_service: "0",
                receive_priority: "0",
                offline_priority: "0"
              };
            }
            // 营销
            if (this.PortraitFunctionUsing.marketing !== null) {
              this.PortraitFunctionUsing.marketing = this.transferformat(
                this.PortraitFunctionUsing.marketing
              );
            } else {
              this.PortraitFunctionUsing.marketing = {
                lottery: "0",
                exit_to_retain: "0",
                short_message_send: "0"
              };
            }
            // 媒体对接
            if (this.PortraitFunctionUsing.mediaDocking !== null) {
              this.PortraitFunctionUsing.mediaDocking = this.transferformat(
                this.PortraitFunctionUsing.mediaDocking
              );
            } else {
              this.PortraitFunctionUsing.mediaDocking = {
                wechat_public: "0",
                applet_of_wechat: "0",
                applet_of_baidu: "0",
                jinri_toutiao: "0",
                tencent_social_advertising: "0",
                _360: "0",
                kuaishou: "0"
              };
            }
            // 移动端设置
            if (this.PortraitFunctionUsing.mobilesOptions !== null) {
              this.PortraitFunctionUsing.mobilesOptions = this.transferformat(
                this.PortraitFunctionUsing.mobilesOptions
              );
            } else {
              this.PortraitFunctionUsing.mobilesOptions = {
                initiate: "0",
                initiate_way: "0",
                kefu_pic: "0",
                call_back: "0",
                interactive_menu: "0"
              };
            }
            // pc端设置
            if (this.PortraitFunctionUsing.pcOptions !== null) {
              this.PortraitFunctionUsing.pcOptions = this.transferformat(
                this.PortraitFunctionUsing.pcOptions
              );
            } else {
              this.PortraitFunctionUsing.pcOptions = {
                initiate: "0",
                initiate_way: "0",
                kefu_pic: "0",
                call_back: "0"
              };
            }
            // 其他
            if (this.PortraitFunctionUsing.other !== null) {
              this.PortraitFunctionUsing.other = this.transferformat(
                this.PortraitFunctionUsing.other
              );
            } else {
              this.PortraitFunctionUsing.other = {
                app: "0",
                developer_account: "0",
                handing_over: "0"
              };
            }
            // 机器人
            if (this.PortraitFunctionUsing.robot !== null) {
              this.PortraitFunctionUsing.robot = this.transferformat(
                this.PortraitFunctionUsing.robot
              );
            } else {
              this.PortraitFunctionUsing.robot = {
                answer_library: "0",
                clues_to_ask: "0"
              };
            }
            // 系统设置
            if (this.PortraitFunctionUsing.systemOptions !== null) {
              this.PortraitFunctionUsing.systemOptions = this.transferformat(
                this.PortraitFunctionUsing.systemOptions
              );
            } else {
              this.PortraitFunctionUsing.systemOptions = {
                service_scheduling: "0",
                useful_expressions: "0",
                enclave_label: "0",
                conversation_topics: "0",
                visitors_forms: "0",
                promote_links: "0"
              };
            }
          } else {
            this.portrainformation_funinit();
          }
        } else {
          this.portrainformation_funinit();
          this.portrainformation_basicinit();
          this.PortraitProductUsing = {};
          this.ActiveCustomer = {};
        }
      }
    }
  },
  mounted() {}
};
</script>


