<template>
  <div class="PermissionSetting view">
    <div v-if="hasPermissions == true" style="width: 100%; height: 100%">
      <div>
        <div class="tab_page">
          <div class="view_title">权限设置</div>
          <div
            class="tab_item"
            :class="currentpage == 1 ? 'active' : ''"
            @click="changepage(1)"
          >
            用户列表
          </div>
          <div
            class="tab_item"
            :class="currentpage == 2 ? 'active' : ''"
            @click="changepage(2)"
          >
            身份管理
          </div>
        </div>
        <!-- 用户管理 -->
        <div class="userlist" v-show="currentpage == 1">
          <div class="table_box">
            <div class="top_box" style="padding: 5px 0; border: none">
              <a-select
                v-model="searchlist.identity"
                style="width: 200px"
                allowClear
                placeholder="全部身份"
                @change="searchidentity_change"
              >
                <a-select-option
                  v-for="item in selectidentity_list"
                  style="
                    width: 180px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                  "
                  :title="item.ccsIdentity.identityName"
                  :value="item.ccsIdentity.id"
                  :key="item.ccsIdentity.id"
                  >{{ item.ccsIdentity.identityName }}</a-select-option
                >
              </a-select>
              <a-input
                placeholder="姓名/手机号"
                v-model="searchlist.searchkey"
                style="width: 150px; margin-left: 20px"
                @change="searchkeyonchange"
              />
            </div>
            <a-table
              rowKey="id"
              :columns="usercolumns"
              :pagination="pagination"
              :dataSource="userlist"
              :scroll="{ y: tableheight }"
            >
              <span slot="account" slot-scope="text">
                <span :title="text">{{ text }}</span>
              </span>
              <span slot="userName" slot-scope="text">
                <span :title="text">{{ text }}</span>
              </span>
              <span slot="phone" slot-scope="text">
                <span :title="text">{{ text }}</span>
              </span>
              <span slot="identityName" slot-scope="text">
                <span :title="text">{{ text }}</span>
              </span>
              <span slot="operation" slot-scope="record">
                <span
                  class="text_btn"
                  :class="ifhaspermission(record.identityId) ? '' : 'unclick'"
                  @click="
                    ifhaspermission(record.identityId)
                      ? open_editidentity(record)
                      : ''
                  "
                  >修改身份</span
                >
              </span>
            </a-table>
          </div>
        </div>
      </div>

      <!-- 身份设置 -->
      <div class="permission" v-show="currentpage == 2">
        <div class="body_content clearfloat">
          <div class="identity_list">
            <div class="top_box">
              <span>身份列表</span>
              <a-button
                type="primary"
                style="float: right"
                @click="
                  identitymodal.show = true;
                  identitymodal.type = '添加身份';
                  addoredit_err = false;
                  identitymodal.name = '';
                "
                >添加身份</a-button
              >
            </div>
            <div class="list_scroll">
              <div
                class="identity_box clearfloat"
                :class="
                  current_identityid == item.ccsIdentity.id
                    ? 'identity_active'
                    : ''
                "
                v-for="item in identity_listdata"
                :key="item.ccsIdentity.id"
                @click="change_curidentity(item)"
              >
                <span
                  class="identity_title"
                  :title="item.ccsIdentity.identityName"
                  >{{ item.ccsIdentity.identityName }}</span
                >
                <div class="delete_icon" v-show="item.ccsIdentity.id > 3">
                  <i
                    style="font-size: 20px; color: #218bfc"
                    @click.stop="
                      delete_identitymodal.show = true;
                      delete_identitymodal.info = item;
                    "
                  />
                </div>
                <div class="edit_icon" v-show="item.ccsIdentity.id > 3">
                  <i
                    type="edit"
                    style="font-size: 20px; color: #218bfc"
                    @click.stop="
                      identitymodal.show = true;
                      identitymodal.type = '修改身份名称';
                      identitymodal.name = item.ccsIdentity.identityName;
                      identitymodal.info = item;
                    "
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="identity_edit">
            <div class="top_box">
              <a-checkbox
                v-model="all_check"
                :disabled="current_identityid < 3"
                @click.prevent.native="all_checkchange"
                >全选</a-checkbox
              >
            </div>
            <div class="list_scroll2">
              <a-checkbox
                :disabled="current_identityid < 3"
                class="permission_box"
                v-for="item in pagepermission_list"
                :key="item.code"
                v-model="item.value"
                @change="checkboxchange"
              >
                <span>{{ item.permission }}</span>
              </a-checkbox>
            </div>
            <div class="bottom_box" v-show="current_identityid > 2">
              <a-button
                style="float: right; margin-left: 20px"
                @click="reset_permission"
                >取消</a-button
              >
              <a-button
                type="primary"
                style="float: right"
                @click="edit_identitykeep"
                >保存</a-button
              >
            </div>
          </div>
        </div>
      </div>
      <!-- 修改身份对话框 -->
      <a-modal
        v-model="usereditModal.show"
        :width="380"
        title="修改身份"
        :footer="null"
        @on-cancel="close_editidentity"
        :styles="{ top: '200px' }"
      >
        <div class="modal_body_permission">
          <div style="padding: 10px 0">
            当前身份：【{{ usereditModal.info.identityName }}】
          </div>
          <a-select
            v-model="select_identity"
            style="width: 340px; padding: 0 0 10px 10px"
            ref="editidentity"
            allowClear
          >
            <a-select-option
              v-for="item in editidentity_list"
              style="
                width: 320px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              "
              :disabled="ifhaspermission(item.ccsIdentity.id) ? false : true"
              :value="item.ccsIdentity.id"
              :title="item.ccsIdentity.identityName"
              :key="item.ccsIdentity.id"
              >{{ item.ccsIdentity.identityName }}</a-select-option
            >
          </a-select>
          <div class="modal_footer">
            <a-button @click="close_editidentity" style="margin-right: 10px"
              >取消</a-button
            >
            <a-button
              type="primary"
              @click="editidentity"
              :loading="editidentity_botton"
              >修改</a-button
            >
          </div>
        </div>
      </a-modal>

      <!-- 添加，编辑身份名称对话框 -->
      <a-modal
        v-model="identitymodal.show"
        :width="432"
        @on-cancel="close_identitymodal"
        :footer="null"
        :title="identitymodal.type"
        :styles="{ top: '200px' }"
      >
        <div class="modal_body_permission">
          <div style="margin-bottom: 16px">
            <span>身份名称：</span>
            <a-input
              v-model="identitymodal.name"
              :class="addoredit_err ? 'inputerror' : ''"
              @on-focus="identityname_focus"
              :placeholder="
                identitymodal.type == '添加身份'
                  ? '输入要添加的身份名称'
                  : '输入要修改的身份名称'
              "
              style="width: 300px"
            />
          </div>
          <div class="modal_footer">
            <a-button @click="close_identitymodal" style="margin-right: 10px"
              >取消</a-button
            >
            <a-button
              type="primary"
              @click="edit_identityname"
              :loading="addoreditidentityname_botton"
              >{{
                identitymodal.type == "添加身份" ? "添加" : "修改"
              }}</a-button
            >
          </div>
        </div>
      </a-modal>

      <!-- 删除身份 -->
      <a-modal
        v-model="delete_identitymodal.show"
        :width="432"
        title="删除身份"
        :footer="null"
        @on-cancel="close_delete_identitymodal"
        :styles="{ top: '200px' }"
      >
        <div class="modal_body_permission">
          <p style="padding: 10px">
            确定要删除【{{
              delete_identitymodal.info.ccsIdentity.identityName
            }}】 身份吗？
          </p>
          <div class="modal_footer">
            <a-button
              @click="close_delete_identitymodal"
              style="margin-right: 10px"
              >取消</a-button
            >
            <a-button
              type="primary"
              @click="delete_identity"
              :loading="deleteidentity_botton"
              >删除</a-button
            >
          </div>
        </div>
      </a-modal>
    </div>
    <div v-else class="no-permission">
      <span>无权限，请联系管理员</span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      urlname: "",
      hasPermissions: true,
      // 当前项目
      current_project: "call",
      current_userid: "",
      currentpage: 1,
      current_userinfo: {
        identity: {},
        user: {
          account: "",
          creation_time: "",
          ccsIdentity_id: 0,
          name: "",
          phone: "",
          user_id: "",
        },
      },
      page_loading: true,
      pagepermission: true,

      // 用户列表页
      tableheight: 500,
      usercolumns: [
        {
          title: "账号",
          dataIndex: "account",
          scopedSlots: { customRender: "account" },
        },
        {
          title: "姓名",
          dataIndex: "userName",
          width: "20%",
          scopedSlots: { customRender: "userName" },
        },
        {
          title: "手机",
          dataIndex: "phone",
          width: "20%",
          scopedSlots: { customRender: "phone" },
        },
        {
          title: "身份",
          width: "20%",
          dataIndex: "identityName",
          scopedSlots: { customRender: "identityName" },
        },
        {
          title: "操作",
          width: "20%",
          key: "operation",
          scopedSlots: { customRender: "operation" },
        },
      ],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0,
        showTotal: (total) => `共 ${total} 条数据`,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "30", "40"],
        // 每页数量改变
        onShowSizeChange: (current, pageSize) => {
          this.pagination.current = 1;
          this.pagination.pageSize = pageSize;
          this.$forceUpdate();
          this.getalluserlist();
        },
        // 页码改变
        onChange: (current, size) => {
          this.pagination.current = current;
          this.getalluserlist();
        },
      },
      userlist: [],
      select_identity: null,
      searchlist: {
        identity: undefined,
        identity_name: "",
        searchkey: "",
      },
      selectidentity_list: [
        {
          ccsIdentity: {
            identityName: "",
          },
        },
      ],
      editidentity_list: [
        {
          ccsIdentity: {
            identityName: "",
          },
        },
      ],
      usereditModal: {
        show: false,
        info: {},
      },
      editidentity_botton: false,

      // 身份管理页
      identity_listdata: [
        {
          ccsIdentity: {
            identityName: "",
          },
          permissionList: {},
        },
      ],
      currentidentity: {},
      current_identityid: 1,
      all_check: false,
      pagepermission_list: [
        {
          code: "",
          permission: "",
          checked: false,
          is_own: 0,
        },
      ],
      initpage_list: [],
      addoredit_err: false,
      addoreditidentityname_botton: false,
      deleteidentity_botton: false,
      identitymodal: {
        show: false,
        type: "添加身份",
        name: "",
        info: {
          ccsIdentity: {},
        },
      },
      delete_identitymodal: {
        show: false,
        info: {
          ccsIdentity: {},
        },
      },
    };
  },
  methods: {
    // 判断字符串是否为空
    isNull(str) {
      str = str.replace(/\n/g, "");
      if (str == "") return true;
      var regu = "^[ ]+$";
      var re = new RegExp(regu);
      return re.test(str);
    },
    // 切换页面
    changepage(state) {
      if (state == 1) {
        this.currentpage = 1;
      } else if (state == 2) {
        this.currentpage = 2;
      }
    },
    // 替换字符串中的空格和回车
    replace_null(str) {
      return str
        .replace(/\ +/g, "")
        .replace(/[ ]/g, "")
        .replace(/[\r\n]/g, "");
    },
    // 用户列表页
    // 身份筛选
    searchidentity_change(value) {
      for (let item of this.selectidentity_list) {
        if (item.ccsIdentity.id == value) {
          this.searchlist.identity_name = item.ccsIdentity.identityName;
        }
      }
      this.getalluserlist();
    },
    // 姓名手机号关键词onchange
    searchkeyonchange(e) {
      this.getalluserlist();
    },
    // 输入框聚焦清除红框警告
    identityname_focus() {
      this.addoredit_err = false;
    },
    // 计算表格高度
    sumtableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      this.tableheight = height - 300;
    },
    // 判断是否有修改其他用户身份的权限
    ifhaspermission(id) {
      let curuser_identityid = this.current_userinfo.identityId;
      // console.log(this.current_userinfo.identityId);
      if (id == 1) {
        return false;
      } else if (id == 2) {
        if (curuser_identityid == 1) {
          return true;
        } else {
          return false;
        }
      } else {
        return true;
      }
    },
    // 获取用户列表
    getalluserlist() {
      let data = {
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize,
      };
      if (this.searchlist.identity) {
        data.identityId = this.searchlist.identity;
        data.identityName = this.searchlist.identity_name;
        data.pageNum = this.pagination.current;
      }
      if (this.replace_null(this.searchlist.searchkey) !== "") {
        data.nameOrPhone = this.replace_null(this.searchlist.searchkey);
        data.pageNum = this.pagination.current;
      }
      this.$per_http
        .post("/api/ccs/get/user/list", data)
        .then((res) => {
          if (res.data.code == 200) {
            this.userlist = res.data.data.list;
            // console.log(res.data.data.list);
            // console.log(this.current_userid);
            // for (let item of this.userlist) {
            //   if (this.current_userid == item.id) {
            //     this.current_userinfo = item;
            //     console.log(item);
            //   }
            // }
            this.pagination.total = res.data.data.total;
          } else {
            this.$message.error(res.data.data);
          }
        })
        .catch((err) => {
          this.$message.error("网络连接错误");
          console.log(err);
        })
        .finally(() => {});
    },
    //获取初试数据
    getuserid() {
      let data = {
        pageNum: 1,
        pageSize: 10000,
      };
      if (this.searchlist.identity) {
        data.identityId = this.searchlist.identity;
        data.identityName = this.searchlist.identity_name;
      }
      if (this.replace_null(this.searchlist.searchkey) !== "") {
        data.nameOrPhone = this.replace_null(this.searchlist.searchkey);
      }
      this.$per_http.post("/api/ccs/get/user/list", data).then((res) => {
        if (res.data.code == 200) {
          res.data.data.list.forEach((item) => {
            if (this.current_userid == item.id) {
              this.current_userinfo = item;
              //console.log(item);
            }
          });
        }
      });
    },
    // 打开修改身份对话框
    open_editidentity(row) {
      this.usereditModal.show = true;
      this.usereditModal.info = row;
      this.select_identity = row.identityId;
    },
    // 关闭修改身份对话框
    close_editidentity() {
      this.usereditModal.show = false;
      this.addoredit_err = false;
      //   this.$refs.editidentity.clearSingleSelect();
    },
    // 修改身份
    editidentity() {
      if (this.editidentity_botton == true) {
        return;
      }
      if (this.editidentity_botton) {
        return;
      }
      let setTimeoutloading;
      if (this.select_identity == undefined) {
        this.$message.error("修改的身份不能为空！");
        return;
      }
      if (this.select_identity == this.usereditModal.info.identity_id) {
        this.close_editidentity();
        return;
      }
      this.editidentity_botton = true;
      let data = {
        userId: this.usereditModal.info.usermgtUserId,
        identityId: this.select_identity,
      };
      this.$per_http
        .post("/api/ccs/user/identity/edit", data)
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success("身份修改成功！");
            this.close_editidentity();
          } else {
            this.$message.error(res.data.data);
          }
        })
        .catch((err) => {
          this.$message.error("网络连接错误");
          console.log(err);
        })
        .finally(() => {
          this.getalluserlist();
          if (setTimeoutloading) {
            clearTimeout(setTimeoutloading);
          }
          setTimeoutloading = setTimeout(() => {
            this.editidentity_botton = false;
          }, 1000);
        });
    },
    // 返回身份名称
    getidentityname(row) {
      for (let i of this.selectidentity_list) {
        if (row.ccsIdentity.id == i.ccsIdentity.id) {
          return i.identity_name;
        }
      }
    },
    // 身份管理页
    // 获取身份列表
    getallidentifylist(item) {
      this.$per_http
        .post("/api/ccs/get/identity/list", {})
        .then((res) => {
          if (res.data.code == 200) {
            this.identity_listdata = JSON.parse(JSON.stringify(res.data.data));
            this.selectidentity_list = JSON.parse(
              JSON.stringify(res.data.data)
            );
            this.editidentity_list = JSON.parse(JSON.stringify(res.data.data));
          } else {
            this.$message.error(res.data.data);
          }
        })
        .catch((err) => {
          this.$message.error("网络连接错误");
          console.log(err);
        })
        .finally(() => {
          if (item) {
            for (let key of this.identity_listdata) {
              if (item.ccsIdentity.id == key.ccsIdentity.id) {
                this.change_curidentity(key);
              }
            }
          } else {
            this.change_curidentity(this.identity_listdata[0]);
          }
        });
    },
    // 点击身份更新权限信息
    change_curidentity(item) {
      console.log(item);
      this.pagepermission_list = [];
      let list = item.permissionList;
      this.initpage_list = JSON.parse(JSON.stringify(item.permissionList));
      this.currentidentity = item;
      this.current_identityid = item.ccsIdentity.id;
      this.pagepermission_list = list;
      let checkednum = 0;
      for (let item of this.pagepermission_list) {
        if (item.is_own == 1) {
          item.value = true;
          checkednum++;
        } else {
          item.value = false;
        }
      }
      if (checkednum == this.pagepermission_list.length) {
        this.all_check = true;
      } else {
        this.all_check = false;
      }
      this.$forceUpdate();
    },
    // 权限改变onchange
    checkboxchange(e) {
      let checknum = 0;
      for (let item of this.pagepermission_list) {
        if (item.value == true) {
          checknum++;
        }
      }
      if (checknum == this.pagepermission_list.length) {
        this.all_check = true;
      } else {
        this.all_check = false;
      }
      this.$forceUpdate();
    },
    // 全选
    all_checkchange() {
      this.all_check = !this.all_check;
      if (this.all_check) {
        for (let item of this.pagepermission_list) {
          item.value = true;
        }
      } else {
        for (let item of this.pagepermission_list) {
          item.value = false;
        }
      }
    },
    // 关闭添加，修改身份名称对话框
    close_identitymodal() {
      this.identitymodal.show = false;
      this.addoredit_err = false;
    },
    // 关闭删除身份对话框
    close_delete_identitymodal() {
      this.delete_identitymodal.show = false;
    },
    // 添加，修改身份名称
    edit_identityname() {
      if (this.addoreditidentityname_botton == true) {
        return;
      }
      let setTimeoutloading;
      if (this.identitymodal.type == "添加身份") {
        if (this.isNull(this.identitymodal.name)) {
          this.$message.error("身份名称不能为空！");
          this.addoredit_err = true;
          return;
        }
        this.addoreditidentityname_botton = true;
        let data = {
          identityName: this.replace_null(this.identitymodal.name),
        };
        this.$per_http
          .post("/api/ccs/identity/add", data)
          .then((res) => {
            if (res.data.code == 200) {
              this.$message.success("添加身份成功！");
              this.close_identitymodal();
            } else {
              this.$message.error(res.data.data);
            }
          })
          .catch((err) => {
            this.$message.error("网络连接错误");
            console.log(err);
          })
          .finally(() => {
            this.getallidentifylist(this.currentidentity);
            if (setTimeoutloading) {
              clearTimeout(setTimeoutloading);
            }
            setTimeoutloading = setTimeout(() => {
              this.addoreditidentityname_botton = false;
            }, 1000);
          });
      } else if (this.identitymodal.type == "修改身份名称") {
        if (this.isNull(this.identitymodal.name)) {
          this.$message.error("身份名称不能为空！");
          this.addoredit_err = true;
          return;
        }
        if (
          this.replace_null(this.identitymodal.name) ==
          this.identitymodal.info.ccsIdentity.identityName
        ) {
          this.close_identitymodal();
          return;
        }
        this.addoreditidentityname_botton = true;
        let data = {
          id: this.identitymodal.info.ccsIdentity.id,
          identityName: this.replace_null(this.identitymodal.name),
        };
        this.$per_http
          .post("/api/ccs/identity/edit", data)
          .then((res) => {
            if (res.data.code == 200) {
              this.$message.success("身份名称修改成功！");
              // this.identitymodal.info.ccsIdentity.identityName = this.replace_null(this.identitymodal.name);
              this.close_identitymodal();
            } else {
              this.$message.error(res.data.data);
            }
          })
          .catch((err) => {
            this.$message.error("网络连接错误");
            console.log(err);
          })
          .finally(() => {
            this.getallidentifylist(this.currentidentity);
            if (setTimeoutloading) {
              clearTimeout(setTimeoutloading);
            }
            setTimeoutloading = setTimeout(() => {
              this.addoreditidentityname_botton = false;
            }, 1000);
          });
      }
    },
    // 删除身份
    delete_identity() {
      if (this.deleteidentity_botton == true) {
        return;
      }
      this.deleteidentity_botton = true;
      let data = {
        id: this.delete_identitymodal.info.ccsIdentity.id,
      };
      let setTimeoutloading;
      this.$per_http
        .post("/api/ccs/identity/delete", data)
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success("删除身份成功！");
            this.close_delete_identitymodal();
          } else {
            this.$message.error(res.data.message);
          }
        })
        .catch((err) => {
          this.$message.error("网络连接错误");
          console.log(err);
        })
        .finally(() => {
          this.getallidentifylist();
          if (setTimeoutloading) {
            clearTimeout(setTimeoutloading);
          }
          setTimeoutloading = setTimeout(() => {
            this.deleteidentity_botton = false;
          }, 1000);
        });
    },
    // 点击取消重置身份
    reset_permission() {
      let data = JSON.parse(JSON.stringify(this.currentidentity));
      data.permissionList = JSON.parse(JSON.stringify(this.initpage_list));
      this.change_curidentity(data);
      this.$forceUpdate();
    },
    // 编辑权限保存
    edit_identitykeep() {
      let data = {
        id: this.current_identityid,
        codes: "",
      };
      for (let item of this.pagepermission_list) {
        if (item.value == true) {
          if (data.codes == "") {
            data.codes = item.code;
          } else {
            data.codes = data.codes + "," + item.code;
          }
        }
      }
      this.$per_http
        .post("/api/ccs/identity/edit", data)
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success("权限保存成功！");
          } else {
            this.$message.error(res.data.message);
          }
        })
        .catch((err) => {
          this.$message.error("网络连接错误");
          console.log(err);
        })
        .finally(() => {
          this.getallidentifylist(this.currentidentity);
        });
    },
  },
  computed: {},
  watch: {
    currentpage(newval) {
      if (newval == 1) {
        this.getalluserlist();
      } else if (newval == 2) {
        this.getallidentifylist();
      }
    },
  },
  mounted() {
    document.title = "权限设置";
    this.sumtableheight();
    this.current_userid = this.$store.state.user_id;
    this.hasPermissions = this.$store.state.pagepermission;
    let userid = this.$store.getters.Userid;
    if (this.hasPermissions == true) {
      this.getallidentifylist();
      this.getalluserlist();
      this.getuserid();
    }
  },
};
</script>
<style lang="less" scopd>
.ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-left: 0;
}
.ant-table-tbody > tr > td {
  font-size: 12px;
}
.clearfloat:after {
  content: "";
  display: table;
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}

.view {
  width: 100%;
  height: 100%;
  font-size: 12px;
  overflow: hidden;
  .tab_page {
    width: 100%;
    height: 87px;
    background: #f7f8fa;
    border-bottom: 1px solid #d8dfea;
    padding-left: 20px;
    .tab_item {
      font-size: 14px;
      height: 37px;
      display: inline-block;
      line-height: 37px;
      cursor: pointer;
      text-align: center;
      margin-right: 20px;
      &:hover {
        color: #2d8cf0;
      }
    }
    .active {
      color: #2d8cf0;
      border-bottom: 2px solid #2d8cf0;
    }
    .view_title {
      width: 100%;
      height: 50px;
      font-size: 16px;
      font-weight: 400;
      padding-left: 20px;
      color: #28334b;
      line-height: 50px;
    }
  }
  .userlist {
    .table_box {
      width: 100%;
      height: 100%;
      font-size: 12px;
      padding: 10px 20px;
    }
  }
  .permission {
    .body_content {
      width: calc(100% - 40px);
      height: calc(100vh - 120px);
      margin: 10px 20px;
      border: 1px solid rgba(216, 223, 234, 1);
      .identity_list {
        width: 250px;
        height: 100%;
        float: left;
        border-right: 1px solid rgba(216, 223, 234, 1);
      }
      .identity_edit {
        width: calc(100% - 250px);
        height: 100%;
        position: relative;
        float: left;
        .bottom_box {
          width: 100%;
          height: 50px;
          padding: 10px 10px;
          line-height: 30px;
          border-top: 1px solid rgba(216, 223, 234, 1);
          position: absolute;
          bottom: 0;
          left: 0;
        }
      }
    }
  }
}

.modal_body_permission {
  padding: 16px;
}
.top_box {
  width: 100%;
  height: 50px;
  padding: 10px 10px;
  line-height: 30px;
  border-bottom: 1px solid rgba(216, 223, 234, 1);
}

.identity_box {
  width: 100%;
  height: 40px;
  padding: 5px 0 5px 10px;
  line-height: 30px;
  cursor: pointer;
}

.identity_box .identity_title {
  display: inline-block;
  width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.permission_box {
  width: 100%;
  height: 50px;
}

.identity_active {
  background-color: #f2f2f2;
}

.list_scroll {
  height: calc(100% - 50px);
  overflow-x: hidden;
  overflow-y: auto;
  .identity_box:hover {
    background-color: #ebf7ff;
  }

  .identity_box:hover .edit_icon {
    display: inline-block;
    position: relative;
    top: -7px;
  }

  .identity_box:hover .delete_icon {
    display: inline-block;
    position: relative;
    top: -7px;
  }
  .edit_icon {
    display: none;
    float: right;
    margin: 9px 10px 0 0;
    width: 16px;
    height: 16px;
    i > {
      display: inline-block;
      width: 16px;
      height: 16px;
      background-image: url("../assets/edit_icon.svg");
    }
  }

  .delete_icon {
    display: none;
    float: right;
    margin: 9px 10px 0 0;
    width: 16px;
    height: 16px;
    i > {
      display: inline-block;
      width: 16px;
      height: 16px;
      background-image: url("../assets/delete_icon_blue.svg");
    }
  }
}

.list_scroll2 {
  max-height: calc(100% - 100px);
  overflow-x: hidden;
  overflow-y: auto;
  padding: 20px;
}

.text_btn {
  color: #1890ff;
  cursor: pointer;
}

.unclick {
  color: #c5c8ce;
  cursor: not-allowed;
}

.modal_footer {
  /* border-top: 1px solid #e8eaec; */
  padding: 12px 18px 0 18px;
  text-align: right;
}

.inputerror input {
  border-color: #ed4014;
}

.inputerror input:hover {
  border-color: #ed4014;
}

.inputerror input:focus {
  border-color: #ed4014;
  box-shadow: 0px 0px 3px #ed4014;
}

.demo-spin-icon-load {
  animation: ana-demo-spin 1s linear infinite;
}

@keyframes ana-demo-spin {
  from {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  to {
    transform: rotate(360deg);
  }
}
</style>



