<template>
  <div class="Industrytemplate_QApool">
    <div class="filiter_box">
      <a-button type="primary" class="filiter_item" @click="open_addindustrymodal">新增行业</a-button>
      <a-button
        type="primary"
        class="filiter_item"
        :disabled="curtwolevel_industry == ''"
        @click="open_addclassifymodal"
      >新增问答库分类</a-button>
      <a-button
        type="primary"
        class="filiter_item"
        :disabled="curtwolevel_industry == ''"
        @click="open_addquestion_drawer"
      >新增问题</a-button>
      <a-select
        placeholder="批量转移"
        class="filiter_item"
        v-model="batch_transfer_key"
        @change="transferonchange"
        :disabled="transferdata.length == 0"
        ref="batchtransfer_ref"
        style="width: 120px"
      >
        <a-select-option
          v-for="item in batch_transfer_list"
          :key="item.type_id"
          :value="item.type_id"
        >{{item.type_name}}</a-select-option>
      </a-select>
    </div>
    <div class="content clear">
      <Industrylist
        @getcur_industry="getcur_industry"
        :industrylist="onelevelindustry_list"
        @refreshindustry="getindustrytemplate_all"
        @gettransferdata="gettransferdata"
        @getclassifylist="getclassifylist"
        ref="industrylist"
      ></Industrylist>
    </div>

    <!-- 新增行业 -->
    <a-modal
      v-model="addindustrymodal.show"
      title="新增行业"
      :footer="null"
      @cancel="close_addindustrymodal"
      :width="380"
    >
      <div class="modal_content">
        <div class="modal_body_item clear">
          <div class="item_title">行业：</div>
          <a-radio-group
            v-model="addindustrymodal.addindustrytype_radio"
            name="radioGroup"
            style="position: relative;top:6px;"
            :defaultValue="1"
          >
            <a-radio :value="1">一级行业</a-radio>
            <a-radio :value="2">二级行业</a-radio>
          </a-radio-group>
        </div>
        <div
          class="modal_body_item clear positionrelative"
          v-show="addindustrymodal.addindustrytype_radio == 1"
        >
          <div class="item_title">一级行业：</div>
          <a-input
            v-model="addindustrymodal.addindustry_onelevelname"
            placeholder="一级行业"
            style="width:270px;"
            @focus="addindustrymodal.addindustry_onelevel_nullerr = false"
            :class="addindustrymodal.addindustry_onelevel_nullerr ? 'null_err' :''"
          />
          <div
            class="qapoolTip"
            :class="[{'qapoolTip-show':addindustrymodal.addindustry_onelevel_nullerr}]"
            style="margin-left: 65px;"
          >
            <p class="qapoolTip-center" v-html="qapoolTip"></p>
          </div>
        </div>
        <div
          class="modal_body_item clear positionrelative"
          v-if="addindustrymodal.addindustrytype_radio == 2"
        >
          <div class="item_title">一级行业：</div>
          <div style="width: 270px;float:left;" class="positionrelative">
            <a-select
              v-model="addindustrymodal.onelevel_industry_selectkey"
              placeholder="一级行业"
              @focus="addindustrymodal.onelevel_industry_selectkey_nullerr = false"
              :class="addindustrymodal.onelevel_industry_selectkey_nullerr ? 'null_err' :''"
              @change="onelevel_industry_selectchange"
            >
              <a-select-option
                :value="item.type_id"
                v-for="item in onelevelindustry_list"
                :key="item.type_id"
              >{{item.type_name}}</a-select-option>
            </a-select>
            <div
              class="qapoolTip"
              :class="[{'qapoolTip-show':addindustrymodal.onelevel_industry_selectkey_nullerr}]"
            >
              <p class="qapoolTip-center" v-html="qapoolTip"></p>
            </div>
          </div>
        </div>
        <div
          class="modal_body_item clear positionrelative"
          v-if="addindustrymodal.addindustrytype_radio == 2"
        >
          <div class="item_title">二级行业：</div>
          <a-input
            v-model="addindustrymodal.addindustry_twolevelname"
            placeholder="二级行业"
            style="width:270px;"
            @focus="addindustrymodal.addindustry_twolevelname_nullerr = false"
            :class="addindustrymodal.addindustry_twolevelname_nullerr ? 'null_err' :''"
          />
          <div
            class="qapoolTip"
            :class="[{'qapoolTip-show':addindustrymodal.addindustry_twolevelname_nullerr}]"
            style="margin-left: 65px;"
          >
            <p class="qapoolTip-center" v-html="qapoolTip"></p>
          </div>
        </div>
        <div class="modal_footer">
          <a-button type="primary" @click="modal_debounce(addindustry,1000)">新增行业</a-button>
          <a-button style="margin-left:15px;" @click="close_addindustrymodal">取消</a-button>
        </div>
      </div>
    </a-modal>

    <!-- 新增问答库分类 -->
    <a-modal
      v-model="addclassifymodal.show"
      title="新增问答库分类"
      :footer="null"
      @cancel="close_addclassifymodal"
      :width="380"
    >
      <div class="modal_content">
        <div class="modal_body_item clear positionrelative">
          <div class="item_title">分类名称：</div>
          <a-input
            v-model="addclassifymodal.classify_name"
            placeholder="分类名称"
            style="width:270px;"
            @focus="addclassifymodal.classify_name_nullerr = false"
            :class="addclassifymodal.classify_name_nullerr ? 'null_err' :''"
          />
          <div
            class="qapoolTip"
            :class="[{'qapoolTip-show':addclassifymodal.classify_name_nullerr}]"
            style="margin-left: 65px;"
          >
            <p class="qapoolTip-center" v-html="qapoolTip"></p>
          </div>
        </div>

        <div class="modal_footer">
          <a-button type="primary" @click="modal_debounce(addclassify,1000)">新增分类</a-button>
          <a-button style="margin-left:15px;" @click="close_addclassifymodal">取消</a-button>
        </div>
      </div>
    </a-modal>

    <!-- 新增问题 -->
    <Questioninfo
      :show="addquestionDrawer.show"
      :type="addquestionDrawer.type"
      :curclassify_list="curclassify_list"
      @getshow="getshow"
      @refresh_classify="refreshclassify_list"
    ></Questioninfo>
  </div>
</template>
<script>
import Industrylist from "./QApool_components/Industry_list";
import Questioninfo from "./QApool_components/QuestionInfo";
export default {
  components: {
    Industrylist,
    Questioninfo
  },
  data() {
    return {
      curtwolevel_industry: "",
      batch_transfer_key: undefined,
      batch_transfer_list: [],
      curclassify_list: [],
      qapoolTip: "",
      onelevelindustry_list: [],
      transferdata: [],
      // 新增行业
      addindustrymodal: {
        addindustrytype_radio: 1,

        addindustry_onelevelname: "",
        addindustry_onelevel_nullerr: false,

        onelevel_industry_selectkey: undefined,
        onelevel_industry_selectkey_nullerr: false,
        addindustry_twolevelname: "",
        addindustry_twolevelname_nullerr: false,
        show: false,
        info: {}
      },
      // 新增问答库分类
      addclassifymodal: {
        classify_name: "",
        classify_name_nullerr: false,
        show: false,
        title: "新增问答库分类",
        info: {}
      },
      // 新增问题
      addquestionDrawer: {
        show: false,
        type: "add"
      }
    };
  },
  methods: {
    // 打开新增行业对话框
    open_addindustrymodal() {
      this.addindustrymodal.show = true;
      this.addindustrymodal.onelevel_industry_selectkey = this.onelevelindustry_list[0].type_id;
    },
    // 关闭新增行业对话框
    close_addindustrymodal() {
      this.addindustrymodal = {
        addindustrytype_radio: 1,

        addindustry_onelevelname: "",
        addindustry_onelevel_nullerr: false,

        onelevel_industry_selectkey_nullerr: false,
        addindustry_twolevelname: "",
        addindustry_twolevelname_nullerr: false,
        show: false,
        info: {}
      };
    },
    // 新增行业一级行业选择onchange
    onelevel_industry_selectchange(value) {
      console.log(value);
      this.$forceUpdate();
    },
    // 新增行业
    addindustry() {
      let data = {};
      // 新增一级行业
      if (this.addindustrymodal.addindustrytype_radio == 1) {
        if (
          this.replace_null(this.addindustrymodal.addindustry_onelevelname) ==
          ""
        ) {
          this.addindustrymodal.addindustry_onelevel_nullerr = true;
          this.qapoolTip = "请输入行业名称";
          return;
        } else {
          data = {
            type_level: 1,
            type_name: this.addindustrymodal.addindustry_onelevelname
          };
        }
      }
      // 新增二级行业
      else if (this.addindustrymodal.addindustrytype_radio == 2) {
        if (
          this.addindustrymodal.onelevel_industry_selectkey == undefined ||
          this.addindustrymodal.onelevel_industry_selectkey.length == 0
        ) {
          this.addindustrymodal.onelevel_industry_selectkey_nullerr = true;
          this.qapoolTip = "请选择一级行业";
          return;
        }
        if (
          this.replace_null(this.addindustrymodal.addindustry_twolevelname) ==
          ""
        ) {
          this.addindustrymodal.addindustry_twolevelname_nullerr = true;
          this.qapoolTip = "请输入行业名称";
          return;
        }
        data = {
          type_level: 2,
          type_name: this.addindustrymodal.addindustry_twolevelname,
          type_parent: this.addindustrymodal.onelevel_industry_selectkey
        };
      }
      this.$qapool_http
        .post("/newknowledgebase/tempBase/addIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("添加成功");
            this.close_addindustrymodal();
            this.getindustrytemplate_all();
          } else if (res.data.code == 231) {
            this.addindustrymodal.addindustry_onelevel_nullerr = true;
            this.qapoolTip = "名称已存在";
          } else if (res.data.code == 232) {
            this.addindustrymodal.addindustry_twolevelname_nullerr = true;
            this.qapoolTip = "名称已存在";
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {});
    },
    // 打开新增问答库分类对话框
    open_addclassifymodal() {
      this.addclassifymodal.show = true;
    },
    // 关闭新增问答库对话框
    close_addclassifymodal() {
      this.addclassifymodal = {
        classify_name: "",
        classify_name_nullerr: false,
        show: false,
        info: {}
      };
    },
    // 新增问答库分类
    addclassify() {
      if (this.replace_null(this.addclassifymodal.classify_name) == "") {
        this.addclassifymodal.classify_name_nullerr = true;
        this.qapoolTip = "请输入分类名称";
        return;
      }
      let data = {
        type_level: 3,
        type_name: this.addclassifymodal.classify_name,
        type_parent: this.curtwolevel_industry.type_id
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/addIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("添加分类成功");
            this.close_addclassifymodal();
            this.refreshclassify_list();
          } else if (res.data.code == 233) {
            this.addclassifymodal.classify_name_nullerr = true;
            this.qapoolTip = "名称已存在";
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 获取行业模板所有行业
    getindustrytemplate_all() {
      this.$qapool_http
        .get("/newknowledgebase/tempBase/selectIndustry")
        .then(res => {
          if (res.data.code == 101) {
            this.onelevelindustry_list = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        });
    },
    // 获取当前二级行业
    getcur_industry(curindustry) {
      this.curtwolevel_industry = curindustry;
    },
    // 获取当前问题分类列表数据
    getclassifylist(data) {
      this.batch_transfer_list = data;
      this.curclassify_list = data;
    },
    // 获取批量转移数据
    gettransferdata(data) {
      let transferdata = JSON.parse(JSON.stringify(data));
      this.transferdata = [];
      for (let item of transferdata) {
        item = "" + item;
        this.transferdata.push(item);
      }
    },
    // 批量转移下拉框onchange
    transferonchange(value) {
      let data = {
        type_id: value,
        info: JSON.stringify(this.transferdata)
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/batchTransfer", data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success("转移成功");
            this.$refs.batchtransfer_ref.blur();
            this.transferdata = [];
            this.refreshclassify_list();
          }
        })
        .catch(err => {
          this.$message.error("系统错误");
          console.log(err);
        })
        .finally(() => {
          this.batch_transfer_key = undefined;
          this.$refs.industrylist.clearquestionchecked();
        });
    },
    getshow(value) {
      this.addquestionDrawer.show = value;
    },
    // 打开新增问题编辑框
    open_addquestion_drawer() {
      this.addquestionDrawer.show = true;
    },
    // 关闭新增问题编辑框
    close_addquestion_drawer() {
      this.addquestionDrawer.show = false;
    },
    // 刷新当前分类列表
    refreshclassify_list() {
      this.$refs.industrylist.getquestionclassify_list(
        this.curtwolevel_industry
      );
    }
  },
  mounted() {
    this.getindustrytemplate_all();
  }
};
</script>
<style lang="scss">
.Industrytemplate_QApool {
  width: 100%;
  height: 100%;
  min-width: 700px;
  .filiter_box {
    height: 32px;
    margin-bottom: 10px;
    .filiter_item {
      margin-right: 10px;
    }
  }
  .content {
    width: 100%;
    height: calc(100% - 42px);
  }
}
</style>


