<template>
  <div class="Business_QApool">
    <div class="filiter_box">
      <a-select
        placeholder="一级行业"
        class="filiter_item"
        v-model="onelevel_industry"
        style="width: 150px"
        @change="onelevel_industry_onchange"
      >
        <a-select-option
          v-for="item in onelevelindustry_list"
          :key="item.id"
          :value="item.id"
          :title="item.industry_name"
        >{{item.industry_name}}</a-select-option>
      </a-select>
      <a-select
        placeholder="二级行业"
        class="filiter_item"
        v-model="twolevel_industry"
        :disabled="twolevelindustry_disabled"
        style="width: 120px"
        @change="twolevel_industry_onchange"
      >
        <a-select-option
          v-for="item in twolevelindustry_list"
          :key="item.id"
          :value="item.id"
          :disabled="item.hasCompany == '0'"
          :title="item.industry_name"
        >{{item.industry_name}}</a-select-option>
      </a-select>
      <a-select
        placeholder="企业"
        class="filiter_item"
        v-model="Business"
        :disabled="Business_disabled"
        style="width: 200px"
        @change="Business_onchange"
      >
        <a-select-option
          v-for="item in Business_list"
          :key="item.company_id"
          :value="item.company_id"
        >{{item.company_name}}</a-select-option>
      </a-select>
      <a-button
        @click="open_copyto_QApoolModal"
        :disabled="copydata.length == 0"
      >复制到行业模板问答库</a-button>
    </div>
    <div class="content">
      <QAlist
        ref="qalist_ref"
        :type="'show'"
        :question_classifylist_show="QAlist.question_classifylist"
        :cur_Businessid="Business"
        @getcopydata="getcopydata"
        @refreshallclassify="getquestionclassify(Business)"
      ></QAlist>
    </div>

    <!-- 复制到行业模板问答库 -->
    <a-modal
      v-model="copyto_QApoolModal.show"
      title="复制到行业模板问答库"
      :width="380"
      @cancel="close_copyto_QApoolModal"
      :footer="null"
    >
      <div class="modal_content">
        <div class="modal_body_item clear positionrelative">
          <div class="item_title">选择行业：</div>
          <a-cascader
            v-model="industry_cascader_key"
            :options="industry_cascaderlist"
            :field-names="{ label: 'type_name', value: 'type_id', children: 'childrenList' }"
            placeholder="选择行业..."
            expandTrigger="hover"
            :allowClear="false"
            style="width:270px"
            @focus="industry_cascader_key_nullerr = false"
            :class="industry_cascader_key_nullerr ? 'null_err' :''"
            @change="industry_cascaderlist_change"
          />
          <div
            class="qapoolTip"
            style="margin-left:65px;"
            :class="[{'qapoolTip-show':industry_cascader_key_nullerr}]"
          >
            <p
              class="qapoolTip-center"
              v-html="qapoolTip"
            ></p>
          </div>
        </div>
        <div class="modal_body_item clear">
          <div class="item_title">问答分类：</div>
          <a-radio-group
            v-model="qs_classify_radio"
            name="radioGroup"
            style="position: relative;top:6px;"
            :defaultValue="1"
          >
            <a-radio :value="1">已有分类</a-radio>
            <a-radio :value="2">新增分类</a-radio>
          </a-radio-group>
        </div>
        <div class="modal_body_item clear">
          <div
            style="width: 270px;margin-left:65px;"
            class="positionrelative"
            v-if="qs_classify_radio == '1'"
          >
            <a-select
              v-model="has_classifty_key"
              placeholder="已有分类"
              @focus="has_classifty_key_nullerr = false"
              :class="has_classifty_key_nullerr ? 'null_err' :''"
            >
              <a-select-option
                :value="item.type_id"
                v-for="item in has_classifty_list"
                :key="item.type_id"
              >{{item.type_name}}</a-select-option>
            </a-select>
            <!-- <a-select
              placeholder="已有分类"
              v-else
              :class="has_classifty_key_nullerr ? 'null_err' :''"
              @focus="has_classifty_key_nullerr = false"
            >
              <a-select-option disabled value="0">
                暂无数据，
                <span style="color:#218BFC;cursor:pointer;" @click="qs_classify_radio = 2">新增分类</span>
              </a-select-option>
            </a-select> -->
            <div
              class="qapoolTip"
              :class="[{'qapoolTip-show':has_classifty_key_nullerr}]"
            >
              <p
                class="qapoolTip-center"
                v-html="qapoolTip"
              ></p>
            </div>
          </div>
          <div
            v-if="qs_classify_radio == '2'"
            class="positionrelative"
            style="width: 270px;margin-left:65px;"
          >
            <a-input
              v-model="newaddclassify_inputkey"
              placeholder="输入新增分类名称"
              @focus="newaddclassify_inputkey_nullerr = false"
              :class="newaddclassify_inputkey_nullerr ? 'null_err' :''"
            />
            <div
              class="qapoolTip"
              :class="[{'qapoolTip-show':newaddclassify_inputkey_nullerr}]"
            >
              <p
                class="qapoolTip-center"
                v-html="qapoolTip"
              ></p>
            </div>
          </div>
        </div>
        <div class="modal_footer">
          <a-button
            type="primary"
            @click="modal_debounce(copyto_industryQApool,1000)"
          >复制</a-button>
          <a-button
            style="margin-left:15px"
            @click="close_copyto_QApoolModal"
          >取消</a-button>
        </div>
      </div>
    </a-modal>
  </div>
</template>
<script>
import QAlist from "./QApool_components/QA_list";
export default {
  components: {
    QAlist
  },
  props: ["state"],
  data() {
    return {
      onelevel_industry: undefined,
      twolevelindustry_disabled: true,
      copydata: [],
      onelevelindustry_list: [],
      twolevel_industry: undefined,
      twolevelindustry_list: [],
      Business: undefined,
      Business_disabled: true,
      Business_list: [],

      copyto_QApoolModal: {
        show: false,
        info: {}
      },
      industry_cascader_key_nullerr: false,
      industry_cascader_key: undefined,
      has_classifty_key_nullerr: false,
      industry_cascaderlist: [],
      qs_classify_radio: 1,
      has_classifty_key: undefined,
      has_classifty_list: [
      ],
      newaddclassify_inputkey: "",
      newaddclassify_inputkey_nullerr: false,

      QAlist: {
        question_classifylist: []
      },
      qapoolTip: ''
    };
  },
  methods: {
    // 初始化复制到行业问答库对话框
    init_copyto_QApoolModal() {
      this.industry_cascader_key = undefined;
      this.industry_cascader_key_nullerr = false;
      this.qs_classify_radio = 1;
      this.has_classifty_key = undefined;
      this.has_classifty_key_nullerr = false;
      this.newaddclassify_inputkey = "";
      this.newaddclassify_inputkey_nullerr = false;
    },
    // 打开复制到行业问答库对话框
    open_copyto_QApoolModal() {
      this.copyto_QApoolModal.show = true;
    },
    // 关闭复制到行业问答库对话框
    close_copyto_QApoolModal() {
      this.copyto_QApoolModal.show = false;
      this.init_copyto_QApoolModal();
    },
    // 复制到企业模板问答库
    copyto_industryQApool() {
      let data = {};
      if (this.industry_cascader_key == undefined || this.industry_cascader_key.length == 0) {
        this.industry_cascader_key_nullerr = true;
        this.qapoolTip = '请选择行业';
        this.has_classifty_key_nullerr = false;
        this.newaddclassify_inputkey_nullerr = false;
        return;
      }
      if (this.qs_classify_radio == 1) {
        if (this.has_classifty_key == undefined) {
          this.has_classifty_key_nullerr = true;
          this.qapoolTip = '请选择分类';
          return;
        } else {
          data = {
            cmd: 'have',
            template: JSON.stringify(this.copydata),
            type_id: this.has_classifty_key
          }
        }
      } else if (this.qs_classify_radio == 2) {
        if (this.replace_null(this.newaddclassify_inputkey) == "") {
          this.newaddclassify_inputkey_nullerr = true;
          this.qapoolTip = '请输入分类名称';
          return;
        } else {
          data = {
            cmd: 'new',
            template: JSON.stringify(this.copydata),
            type_name: this.newaddclassify_inputkey,
            type_parent: this.industry_cascader_key[1]
          }
        }
      }
      this.$qapool_http.post('/newknowledgebase/tempBase/copyTemplate', data)
        .then(res => {
          if (res.data.code == 101) {
            this.$message.success('复制成功');
            this.$refs.qalist_ref.questionselectedKeys = [];
            this.close_copyto_QApoolModal();
            this.getquestionclassify(this.Business);
            this.copydata = [];
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })

    },
    // 获取行业模板所有行业
    getindustrytemplate_all() {
      this.$qapool_http
        .get("/newknowledgebase/tempBase/selectIndustry")
        .then(res => {
          if (res.data.code == 101) {
            this.industry_cascaderlist = res.data.data;
            for (let item of this.industry_cascaderlist) {
              if (item.childrenList.length == 0) {
                item.disabled = true;
              }
            }
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 行业联级选择onchange
    industry_cascaderlist_change(value) {
      let data = {
        type_parent: value[1]
      };
      this.$qapool_http
        .post("/newknowledgebase/tempBase/selectClassification", data)
        .then(res => {
          if (res.data.code == 101) {
            this.has_classifty_list = res.data.data;
            this.has_classifty_key = this.has_classifty_list[0].type_id;
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 获取一级行业
    getindustry_levelone() {
      this.$qapool_http
        .get("/newknowledgebase/comBase/selectPrimaryIndustry")
        .then(res => {
          if (res.data.code == 101) {
            this.onelevelindustry_list = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 一级行业下拉框onchange
    onelevel_industry_onchange(value, option) {
      this.twolevel_industry = undefined;
      this.Business = undefined;
      this.Business_disabled = true;
      this.twolevelindustry_disabled = true;
      this.getindustry_leveltwo(value);
      this.QAlist.question_classifylist = [];
    },
    // 二级行业下拉框onchange
    twolevel_industry_onchange(value) {
      this.Business = undefined;
      this.getBusiness(value);
      this.QAlist.question_classifylist = [];
    },
    // 企业下拉框onchange
    Business_onchange(value) {
      this.getquestionclassify(value);
    },
    // 获取二级行业
    getindustry_leveltwo(id) {
      let data = {
        id: id
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/selectSecondaryIndustry", data)
        .then(res => {
          if (res.data.code == 101) {
            this.twolevelindustry_disabled = false;
            this.twolevelindustry_list = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 获取行业下对应企业
    getBusiness(id) {
      let data = {
        industry_id: id
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/selectCompanyByIndustryId", data)
        .then(res => {
          if (res.data.code == 101) {
            this.Business_disabled = false;
            this.Business_list = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 获取企业下问题分类
    getquestionclassify(id) {
      let data = {
        company_id: id
      };
      this.$qapool_http
        .post("/newknowledgebase/comBase/selectCategoryByComId", data)
        .then(res => {
          if (res.data.code == 101) {
            this.QAlist.question_classifylist = res.data.data;
          }
        })
        .catch(err => {
          this.$message.error('系统错误');
          console.log(err);
        })
    },
    // 获取列表复制勾选数据
    getcopydata(data) {
      this.copydata = data;
    }
  },
  mounted() {
    this.getindustry_levelone();
    this.getindustrytemplate_all();
  }
};
</script>
<style lang="scss">
.Business_QApool {
  width: 100%;
  height: 100%;
  min-width: 700px;
  .filiter_box {
    height: 32px;
    margin-bottom: 10px;
    .filiter_item {
      margin-right: 10px;
    }
  }
  .content {
    width: 100%;
    height: calc(100% - 42px);
  }
}
</style>


