<template>
  <div class="interfaceList">
    <div v-if="hasPermissions == true" style="width: 100%; height: 100%">
      <div class="interface-head">活跃客户（付费到停用一个月内）</div>
      <div class="active_customer_main">
        <div class="fold_box" v-show="filter_show">
          <div class="active_customer_filter">
            <a-tabs
              defaultActiveKey="1"
              v-model="activetabs_key"
              :tabBarStyle="tabsBarstyle"
            >
