                          width: 95px;
                          display: inline-block;
                          font-size: 12px;
                        "
                        @change="fifterChange"
                      >
                        <a-select-option
                          v-for="(item, index) in fifterList"
                          :key="index"
                          :value="item.value"
                          :title="item.name"
                          >{{ item.name }}</a-select-option
                        >
                      </a-select>
                      <a-input
                        style="width: 123px; margin-left: -4px; font-size: 12px"
                        placeholder="请输入"
                        v-model="searchList.companyId"
                      />
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">客服经理</div>
                    <div>
                      <a-select
                        v-model="searchList.belongSaleName"
                        style="width: 100%"
                        allowClear
                        placeholder="客户经理"
                        @change="managerChange"
                      >
                        <a-select-option
                          v-for="(item, index) in managerList"
                          :key="index"
                          :value="item.belongSaleName"
                          :title="item.belongSaleName"
                          >{{ item.belongSaleName }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">服务商</div>
                    <div>
                      <a-select
                        v-model="searchList.facilitatorName"
                        style="width: 100%"
                        allowClear
                        placeholder="服务商"
                        @change="serviceChange"
                      >
                        <a-select-option
                          v-for="(item, index) in serviceList"
                          :key="index"
                          :value="item.facilitatorName"
                          :title="item.facilitatorName"
                          >{{ item.facilitatorName }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">注册时间</div>
                    <div>
                      <a-range-picker
                        format="YYYY-MM-DD"
                        v-model="time"
                        @change="timeChange"
                      />
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">地区</div>
                    <div>
                      <a-select
                        v-model="cityList.province"
                        style="width: 102px; display: inline-block"
                        allowClear
                        placeholder="省"
                        @change="provenceChange"
                      >
                        <a-select-option
                          v-for="(item, index) in cities"
                          :key="index"
                          :value="item.value"
                          :title="item.value"
                          >{{ item.value }}</a-select-option
                        >
                      </a-select>
                      <a-select
                        v-model="cityList.city"
                        style="
                          width: 105px;
                          display: inline-block;
                          margin-left: 6px;
                        "
                        :disabled="cityType"
                        allowClear
                        placeholder="市"
                        @change="cityChange"
                      >
                        <a-select-option
                          v-for="(item, index) in chooseCities"
                          :key="index"
                          :value="item.value"
                          :title="item.value"
                          >{{ item.value }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">渠道</div>
                    <div>
                      <a-select
                        v-model="searchList.firstChannel"
                        style="width: 100%"
                        allowClear
                        placeholder="渠道"
                        @change="channelChange"
                      >
                        <a-select-option
                          v-for="(item, index) in channelList"
                          :key="index"
                          :value="item.firstChannel"
                          :title="item.firstChannel"
                          >{{ item.firstChannel }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">认证</div>
                    <div>
                      <a-select
                        v-model="searchList.authInfo"
                        style="width: 100%"
                        allowClear
                        placeholder="认证"
                        @change="certificationChange"
                      >
                        <a-select-option
                          v-for="(item, index) in certificationList"
                          :key="index"
                          :value="item.id"
                          >{{ item.name }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <!-- <div class="filter_item">
                    <div class="item_name">坐席开户数</div>
                    <div>
                      <a-input-number
                        style="width: 95px;"
                        placeholder="坐席开户数"
                        :min="0"
                        v-model="searchList.seatsAmountStart"
                        @change="accountBeforeChange"
                      />
                      <span class="account-middle-span">-</span>
                      <a-input-number
                        style="width: 95px;"
                        placeholder="坐席开户数"
                        :disabled="accountBType"
                        :min="0"
                        v-model="searchList.seatsAmountEnd"
                        @change="accountAfterChange"
                      />
                    </div>
                  </div> -->

                  <div class="filter_item">
                    <div class="item_name">坐席开户数</div>
                    <div>
                      <a-select
                        v-model="searchList.zuonum"
                        style="width: 100%"
                        allowClear
                        placeholder="坐席开户数"
                      >
                        <a-select-option key="1" value="1" title="1"
                          >1</a-select-option
                        >
                        <a-select-option key="2" value="2" title="2"
                          >2</a-select-option
                        >
                        <a-select-option key="3" value="3" title="3"
                          >3</a-select-option
                        >
                        <a-select-option key="4" value="4" title="4"
                          >4-9</a-select-option
                        >
                        <a-select-option key="5" value="5" title="5"
                          >10-19</a-select-option
                        >
                        <a-select-option key="6" value="6" title="6"
                          >20及以上</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <div class="item_name">账号类型</div>
                    <div>
                      <a-select
                        v-model="searchList.accountType"
                        style="width: 100%"
                        allowClear
                        placeholder="账号类型"
                        @change="accountTypeChange"
                      >
                        <a-select-option
                          v-for="(item, index) in accountTypeList"
                          :key="index"
                          :value="item.id"
                          :title="item.name"
                          >{{ item.name }}</a-select-option
                        >
                      </a-select>
                    </div>
                  </div>
                  <div class="filter_item">
                    <!-- <div class="item_name">
                      <span
                        style="display: inline-block;margin: 0 20px 0 0;position: relative;bottom: 10px;"
                        >行业</span
                      >
                      <a-checkbox-group
                        style="width:160px;"
                        name="checkboxgroup"
                        :options="industryTagsOptions"
                        v-model="searchList.industryTags"
                        @change="industryTagschange"
                      />
                    </div> -->
                    <div>
                      <a-select
                        v-model="searchList.firstIndustry"
                        style="width: 102px; display: inline-block"
                        allowClear
                        :dropdownMatchSelectWidth="false"
                        placeholder="一级行业"
                        @change="industryChange"
                      >
                        <a-select-option
                          v-for="(item, index) in industryList"
                          :key="index"
                          class="select_option_edit"
                          style="width: 102px"
                          :value="item.ccsIndustry.id"
                          :title="item.ccsIndustry.industryName"
                        >
                          <span class="select_option_value">{{
                            item.ccsIndustry.industryName
                          }}</span>
                          <!-- <span
                            class="text_button_hover"
                            @click.stop="open_editindustrynamemodal(item)"
                            >编辑</span
                          > -->
                        </a-select-option>
                      </a-select>
                      <a-select
                        v-model="searchList.industry"
                        style="
                          width: 105px;
                          display: inline-block;
                          margin-left: 6px;
                        "
                        :disabled="industryType"
                        allowClear
                        :dropdownMatchSelectWidth="false"
                        placeholder="二级行业"
                        @change="cityChange"
                      >
                        <a-select-option
                          v-for="(item, index) in industriessecondList"
                          :key="index"
                          class="select_option_edit"
                          style="width: 105px"
                          :value="item.id"
                          :title="item.industryName"
                        >
                          <span class="select_option_value">{{
                            item.industryName
                          }}</span>
                          <!-- <span
                            class="text_button_hover"
                            @click.stop="open_editindustrynamemodal(item)"
                            >编辑</span
                          > -->
                        </a-select-option>
                      </a-select>
                    </div>

                    <div style="margin-top: 10px">
                      <div class="item_name">质检状态</div>
                      <div>
                        <a-select
                          v-model="searchList.qualityTestState"
                          style="width: 100%"
                          allowClear
                          placeholder="质检状态"
                        >
                          <a-select-option key="0" value="0" title="未质检"
                            >未质检</a-select-option
                          >
                          <a-select-option key="1" value="1" title="已质检"
                            >已质检</a-select-option
                          >
                          <a-select-option key="2" value="2" title="暂不质检"
                            >暂不质检</a-select-option
                          >
                        </a-select>
                      </div>
                    </div>
                  </div>
                </div>
              </a-tab-pane>
              <a-tab-pane
                tab="企业画像"
                key="2"
                forceRender
                style="height: calc(100vh - 135px); overflow-y: auto"
              >
                <companyPortray
                  :industry1="industryList"
                  :industry2="industriesList"
                  :facilitatorselect="serviceList"
                  :portraitmeallist="meallist"
                  @getsearch_data="getsearch_data"
                  ref="companyPortray"
                />
              </a-tab-pane>
            </a-tabs>
            <div class="filter_buttom">
              <a-button type="primary" @click="screenBtn">开始筛选</a-button>
              <a-button @click="screenClear">清除筛选</a-button>
            </div>
          </div>
        </div>
        <div class="change_filter">
          <div class="change_filter_box">
            <img
              class="icon_img"
              @click="change_filterstate"
              v-show="filter_show == false"
              src="@/assets/right_open.png"
              alt
            />
            <img
              class="icon_img"
              @click="change_filterstate"
              v-show="filter_show == true"
              src="@/assets/left_close.png"
              alt
            />
          </div>
        </div>
        <div
          class="page-box"
          :class="
            filter_show
              ? 'active_customer_com_msg'
              : 'active_customer_com_msg_all'
          "
        >
          <div
            class="clear search-condition-box"
            style="width: 100%; overflow: hidden"
          >
            <div
              class="search-condition-box-item"
              style="width: 100%; height: 100%; margin-bottom: 0"
            >
              <div style="float: left; margin-bottom: 10px">
                <a-button @click="showfieldModal">显示设置字段</a-button>
              </div>
              <div style="float: left; margin-bottom: 10px">
                <span>续费持续时长：</span>
                <a-select
                  allowClear
                  style="width: 120px; margin-right: 10px"
                  v-model="searchList.renewal"
                  placeholder="续费时长"
                >
                  <a-select-option value="1">连续一年</a-select-option>
                  <a-select-option value="2">连续两年</a-select-option>
                  <a-select-option value="3">连续三年</a-select-option>
                  <a-select-option value="3">三年以上</a-select-option>
                </a-select>
              </div>
              <div style="float: left; margin-bottom: 10px">
                <span>去年消费总金额：</span>
                <a-select
                  allowClear
                  style="width: 120px; margin-right: 10px"
                  v-model="searchList.quxiao"
                  placeholder="去年消费总金额"
                >
                  <a-select-option value="1">0~3000</a-select-option>
                  <a-select-option value="2">3000~5000</a-select-option>
                  <a-select-option value="3">5000~10000</a-select-option>
                  <a-select-option value="4">10000以上</a-select-option>
                </a-select>
              </div>
              <div style="float: left; margin-bottom: 10px">
                <span>今年消费总金额：</span>
                <a-select
                  allowClear
                  style="width: 120px; margin-right: 10px"
                  v-model="searchList.jinxiao"
                  placeholder="今年消费总金额"
                >
                  <a-select-option value="1">0~500</a-select-option>
                  <a-select-option value="2">500~1000</a-select-option>
                  <a-select-option value="3">1000~2000</a-select-option>
                  <a-select-option value="4">2000以上</a-select-option>
                </a-select>
              </div>

              <div style="float: left; margin-bottom: 10px">
                <span>一级行业：</span>
                <a-select
                  v-model="searchList.firstIndustry"
                  style="width: 102px; display: inline-block"
                  allowClear
                  :dropdownMatchSelectWidth="false"
                  placeholder="一级行业"
                  @change="industryChange"
                >
                  <a-select-option
                    v-for="(item, index) in industryList"
                    :key="index"
                    class="select_option_edit"
                    style="width: 105px"
                    :value="item.ccsIndustry.id"
                    :title="item.ccsIndustry.industryName"
                  >
                    <span class="select_option_value">{{
                      item.ccsIndustry.industryName
                    }}</span>
                    <!-- <span
                            class="text_button_hover"
                            @click.stop="open_editindustrynamemodal(item)"
                            >编辑</span
                          > -->
                  </a-select-option>
                </a-select>
              </div>
              <div style="float: left; margin-bottom: 10px; margin-left: 10px">
                <span>二级行业：</span>
                <a-select
                  v-model="searchList.industry"
                  style="width: 105px; display: inline-block; margin-left: 6px"
                  :disabled="industryType"
                  allowClear
                  :dropdownMatchSelectWidth="false"
                  placeholder="二级行业"
                  @change="cityChange"
                >
                  <a-select-option
                    v-for="(item, index) in industriessecondList"
                    :key="index"
                    class="select_option_edit"
                    style="width: 105px"
                    :value="item.id"
                    :title="item.industryName"
                  >
                    <span class="select_option_value">{{
                      item.industryName
                    }}</span>
                    <!-- <span
                            class="text_button_hover"
                            @click.stop="open_editindustrynamemodal(item)"
                            >编辑</span
                          > -->
                  </a-select-option>
                </a-select>
              </div>
              <div style="float: left; margin-bottom: 10px; margin-left: 10px">
                <span>开通套参数：</span>
                <a-input
                  placeholder="开通套参数"
                  v-model="searchList.productCnt"
                  style="width: 100px"
                />
              </div>
              <div style="float: left; margin-bottom: 10px">
                <a-button
                  type="primary"
                  style="margin-left: 10px"
                  @click="searchmoney"
                  >筛选</a-button
                >
              </div>
              <!-- <a-button @click="batchsetting" :disabled="hasselected"
                >批量设置行业</a-button
              >
              <span>
                已勾选
                <span style="color: rgba(244, 64, 36, 1);">{{
                  selectedRowKeys.length
                }}</span>
                条数据
              </span> -->
            </div>
            <div style="margin-bottom: 10px; font-size: 13px; width: 100%">
              <!-- <div>{{activeUser_text}}</div> -->
              <div class="view_key">[访客]</div>
              <div class="view_key" style="margin-left: 10px">头条：</div>
              <div class="view_value">
                {{ activeUser_text.tt ? activeUser_text.tt : 0 }}；
              </div>
              <div class="view_key">百度：</div>
              <div class="view_value">
                {{ activeUser_text.bd ? activeUser_text.bd : 0 }}；
              </div>
              <div class="view_key">腾讯：</div>
              <div class="view_value">
                {{ activeUser_text.tx ? activeUser_text.tx : 0 }}；
              </div>
              <div class="view_key">快手：</div>
              <div class="view_value">
                {{ activeUser_text.ks ? activeUser_text.ks : 0 }}；
              </div>
              <div class="view_key">微信：</div>
              <div class="view_value">
                {{ activeUser_text.wx ? activeUser_text.wx : 0 }}；
              </div>
              <div class="view_key">sdk：</div>
              <div class="view_value">
                {{ activeUser_text.sdk ? activeUser_text.sdk : 0 }}；
              </div>
              <div class="view_key">抖音：</div>
              <div class="view_value">
                {{ activeUser_text.dy ? activeUser_text.dy : 0 }}；
              </div>
              <div class="view_key">微博：</div>
              <div class="view_value">
                {{ activeUser_text.wb ? activeUser_text.wb : 0 }}；
              </div>
              <div class="view_key">挂码移动访客：</div>
              <div class="view_value">
                {{ activeUser_text.gm_phone ? activeUser_text.gm_phone : 0 }}；
              </div>
              <div class="view_key">挂码PC访客：</div>
              <div class="view_value">
                {{ activeUser_text.gm_pc ? activeUser_text.gm_pc : 0 }}；
              </div>
              <div class="view_key">普通微信访客：</div>
              <div class="view_value">
                {{ activeUser_text.wx_nor ? activeUser_text.wx_nor : 0 }}；
              </div>
            </div>
          </div>

          <a-modal
            v-model="visible"
            title="公司主账号"
            ok-text="确认"
            cancel-text="取消"
            @ok="hideModal"
          >
            <div style="margin: 20px">
              <span>公司主账号：</span>
              <a-input
                placeholder="请输入公司主账号"
                v-model="company_num"
                style="width: 260px"
              />
            </div>
          </a-modal>

          <!-- 设置显示字段 -->
          <a-modal
            title="设置显示字段"
            width="368px"
            v-model="fieldvisible"
            @ok="fieldhandleOk"
            @cancel="fieldCancel"
          >
            <div class="fieldchoose">
              <a-checkbox-group
                @change="fieldchooseonChange"
                v-model="fieldchooseList"
              >
                <a-row>
                  <a-col :span="12">
                    <a-checkbox value="0">公司名称</a-checkbox>
                  </a-col>
                  <!-- <a-col :span="12">
                    <a-checkbox value="1">公司 ID</a-checkbox>
                  </a-col> -->
                  <a-col :span="12">
                    <a-checkbox value="1">ARG（公司唯一标识）</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="2">主账号 ID</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="3">主账号</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="4">咨询页面</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="5">着陆页面</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="6">企业认证</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="7">标题</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="8">行业</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="9">坐席数（总：开）</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="10">客服经理</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="11">服务商</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="12">注册时间</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="13">注册手机号码</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="14">注册 IP 地区</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="15">首次渠道</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="16">首次关键词</a-checkbox>
                  </a-col>
                  <a-col :span="12">
                    <a-checkbox value="17">开通套餐数</a-checkbox>
                  </a-col>
                </a-row>
              </a-checkbox-group>
            </div>
          </a-modal>

          <!-- 编辑行业或批量设置行业 -->
          <div class="edit-industry">
            <a-modal
              :title="editindustry_title"
              width="465px"
              okText="保存"
              v-model="editIndustryvisible"
              @ok="editIndustryhandleOk"
              @cancel="editIndustryCancel"
            >
              <div class="edit-industry-modal clear">
                <div
                  class="edit-industry-modal-item first-industry"
                  style="max-height: 336px; overflow: hidden; overflow-y: auto"
                >
                  <div class="edit-industry-modal-item-head">一级行业</div>
                  <div class="edit-industry-modal-item-body">
                    <div
                      class="edit-industry-modal-body-item"
                      v-for="(item, index) in industryList"
                      :key="index"
                      :title="item.ccsIndustry.industryName"
                      :class="[
                        {
                          'edit-industry-modal-body-item-active':
                            currentSort == index,
                        },
                      ]"
                      @click="industryActive(index)"
                    >
                      {{ item.ccsIndustry.industryName }}
                    </div>
                  </div>
                  <div class="edit-industry-modal-item-add">
                    <span @click="firstIndustryadd">新增一级行业</span>
                    <div
                      class="edit-industry-modal-item-add-pop"
                      :class="[
                        { 'edit-industry-modal-item-add-pop-active': popShow },
                      ]"
                    >
                      <a-input
                        style="width: 112px; height: 24px; float: left"
                        :class="[{ 'red-input': industryTipFirst }]"
