                      >
                      <a-button @click="firstIndustryaddhide">取消</a-button>
                      <div
                        class="industryTip"
                        :class="[{ 'industryTip-show': industryTipFirst }]"
                      >
                        <p class="industryTip-center" v-html="industryTip"></p>
                      </div>
