            this.getsearch(params);
            params.pageNum = current;
            params.pageSize = size;
            this.paramsCheck(params);
            this.activeUserList(params);
            // 点击页码事件
          } else if (this.activetabs_key == "2") {
            let params = JSON.parse(JSON.stringify(this.searchListbyportraits));
