  mounted() {
    document.title = "活跃客户";
    this.hasPermissions = this.$store.state.pagepermission;
    if (this.hasPermissions == true) {
      //获取Id
      getData.getId({}).then((res) => {
        this.Id = res.data;
        //获取显示列
        this.refushField();
      });
      //渲染服务商等列表
      getData.getActiveSfc({}).then((res) => {
        this.managerList = res.data.saleList;
        this.serviceList = res.data.facilitatorList;
        this.channelList = res.data.channelList;
      });
      //获取表单数据
      var params = {
        pageNum: 1,
        pageSize: 20,
      };
      this.activeUserList(params);
      //  获取行业数据
      getData.getIndustry({}).then((res) => {
        this.industryList = res.data;
        this.industriesList = this.industryList[0].ccsIndustryLevelTwoList;
      });
      // 获取企业画像产品，套餐列表
      getData.activeUsermealList({}).then((res) => {
        this.meallist = res.data;
      });
    }
  },
  computed: {
    hasselected() {
      if (this.selectedRowKeys.length > 0) {
        return false;
      } else {
        return true;
      }
    },
  },
  methods: {
    // 打开编辑行业名称对话框
    open_editindustrynamemodal(item) {
      this.editindustrynameModal.show = true;
      this.editindustrynameModal.info = JSON.parse(JSON.stringify(item));
      this.editindustrynameModal.name =
        item.industryName || item.ccsIndustry.industryName;
    },
    // 编辑行业名称
    edit_industryname() {
      if (this.loading == true) {
        return;
      }
      let curindustryName =
        this.editindustrynameModal.info.industryName ||
        this.editindustrynameModal.info.ccsIndustry.industryName;
      if (this.editindustrynameModal.name == curindustryName) {
        this.editindustrynameModal.show = false;
        return;
      }
      if (
        this.replace_null(this.editindustrynameModal.name) == "" ||
        this.editindustrynameModal.name == undefined ||
        this.editindustrynameModal.name == null
      ) {
        this.industryTip = "名称不能为空";
        this.editindustrTip = true;
        return;
      }
      this.loading = true;
      let data = {
        id:
          this.editindustrynameModal.info.id ||
          this.editindustrynameModal.info.ccsIndustry.id,
        industryName: this.replace_null(this.editindustrynameModal.name),
      };
      getData
        .editIndustry_name(data)
        .then((res) => {
          if (res.code == 200) {
            this.$message.success("修改成功！");
            this.searchList.firstIndustry = undefined;
            this.industryList = [];
            this.searchList.industry = undefined;
            this.industriessecondList = [];
          } else {
            this.$message.error(res.data);
          }
        })
        .finally(() => {
          this.editindustrynameModal.show = false;
          getData.getIndustry({}).then((res) => {
            this.industryList = res.data;
            this.industriesList = this.industryList[0].ccsIndustryLevelTwoList;
          });
          this.screenBtn();
          if (a) {
            clearTimeout(a);
          }
          let a = setTimeout(() => {
            this.loading = false;
          }, 1000);
        });
    },
    // 批量设置行业
    batchsetting() {
      this.currentSort = 0;
      this.industrychoosefirst = 0;
      this.industryActive(this.industrychoosefirst);
      this.secondcurrentSort = undefined;
      this.industrychoosesecond = undefined;
      this.editindustry_title = "批量设置行业";
      this.editIndustryvisible = true;
    },
    // 表格勾选onchange
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    // 替换字符串中的空格和回车
    replace_null(str) {
      return str
        .replace(/\ +/g, "")
        .replace(/[ ]/g, "")
        .replace(/[\r\n]/g, "");
    },
    // 计算表格宽度
    gettablewidth() {
      let w = 0;
      for (let i of this.columns) {
        if (i.show == true) {
          w += i.width;
        }
      }
      return w + 60;
    },
    gettablewidth_tui() {
      let w = 0;
      for (let i of this.tui_columns) {
        if (i.show == true) {
          w += i.width;
        }
      }
      return w + 60;
    },
    // 表格高度
    gettableheight() {
      let height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      return height - 300;
    },
    industryTagschange(data) {},
    change_filterstate() {
      this.filter_show = !this.filter_show;
    },
    // 获取企业画像筛选组件的筛选数据
    getsearch_data(portraitsearch_data) {
      let params = JSON.parse(JSON.stringify(portraitsearch_data));
      console.log(params, "==============");
      params.pageNum = 1;
      params.pageSize = this.pagination.pageSize;
      this.pagination.current = 1;
      this.searchList.pageNum = 1;
      this.searchListbyportraits = params;
    },

    // 开始筛选
    screenBtn() {
      //console.log(this.activetabs_key);
      if (this.activetabs_key == "1") {
        let params = JSON.parse(JSON.stringify(this.searchList));
        params.pageNum = 1;
        params.pageSize = this.pagination.pageSize;
        this.searchList.pageNum = 1;
        this.pagination.current = 1;
        this.paramsCheck(params);
        //this.rightAsideHide();
        this.activeUserList(params);
      } else if (this.activetabs_key == "2") {
        if (Object.keys(this.searchListbyportraits).length == 0) {
          this.searchListbyportraits = {
            pageNum: 1,
            pageSize: this.pagination.pageSize,
            portraitBasic: null,
            functionUsing: null,
            productUsing: null,
          };
          this.pagination.current = 1;
        }
        //this.rightAsideHide();
        this.activeUserListbyporttaits(this.searchListbyportraits);
      }
    },
    // 清除筛选
    screenClear() {
      if (this.activetabs_key == "1") {
        this.time = undefined;
        this.cityType = true;
        this.accountBType = true;
        this.industryType = true;
        this.searchList = {
          belongSaleName: undefined,
          facilitatorName: undefined,
          firstChannel: undefined,
          authInfo: undefined,
          seatsAmountStart: undefined,
          seatsAmountEnd: undefined,
          accountType: undefined,
          firstIndustry: undefined,
          industry: undefined,
          industryList: [],
          companyId: "",
          companyName: "",
          account: "",
          companyArg: "",

          zuonum: "",
          qualityTestState: "",
          registerTimeEnd: undefined,
          registerTimeStart: undefined,
          pageNum: this.pagination.current,
          pageSize: this.pagination.pageSize,
        };
        this.cityList = {
          province: undefined,
          city: undefined,
        };
        this.screenBtn();
      } else if (this.activetabs_key == "2") {
        this.searchListbyportraits = {};
        this.$refs.companyPortray.clearsearchdata();
        this.screenBtn();
      }
    },
    paramsCheck(params) {
      //console.log(params);
      if (this.fifter == "companyId") {
        params.companyId = this.replace_null(this.searchList.companyId);
      } else if (this.fifter == "companyName") {
        params.companyName = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      } else if (this.fifter == "account") {
        params.account = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      } else if (this.fifter == "companyArg") {
        params.companyArg = this.replace_null(this.searchList.companyId);
        params.companyId = null;
      }
      if (params.belongSaleName == undefined) {
        params.belongSaleName = null;
      }
      if (params.facilitatorName == undefined) {
        params.facilitatorName = null;
      }
      if (params.firstChannel == undefined) {
        params.firstChannel = null;
      }
      if (params.authInfo == undefined) {
        params.authInfo = null;
      }
      if (params.seatsAmountStart == undefined) {
        params.seatsAmountStart = null;
      }
      // if (params.seatsAmountEnd == undefined || params.seatsAmountEnd == "") {
      //   params.seatsAmountEnd = null;
      //
      // }

      if (params.zuonum == 1) {
        params.seatsAmountStart = 1;
        params.seatsAmountEnd = null;
      } else if (params.zuonum == 2) {
        params.seatsAmountStart = 2;
        params.seatsAmountEnd = null;
      } else if (params.zuonum == 3) {
        params.seatsAmountStart = 3;
        params.seatsAmountEnd = null;
      } else if (params.zuonum == 4) {
        params.seatsAmountStart = 4;
        params.seatsAmountEnd = 9;
      } else if (params.zuonum == 5) {
        params.seatsAmountStart = 10;
        params.seatsAmountEnd = 19;
      } else if (params.zuonum == 6) {
        params.seatsAmountStart = 20;
        params.seatsAmountEnd = null;
      } else {
        params.seatsAmountStart = null;
        params.seatsAmountEnd = null;
      }

      if (params.accountType == undefined) {
        params.accountType = null;
      }
      if (params.firstIndustry == undefined) {
        params.firstIndustry = null;
        params.industry = null;
      }
      if (params.industry == undefined) {
        params.industry = null;
        if (
          params.firstIndustry !== undefined &&
          params.firstIndustry !== null
        ) {
          params.industryList = [];
          for (let item of this.industriessecondList) {
            //params.industryList.push(item.industryName);
            //ccsIndustry.id
            params.industryList.push(item.id);
          }
        } else {
          params.industryList = undefined;
        }
      } else {
        params.industryList.push(params.industry);
      }
      if (params.companyId == "") {
        params.companyId = null;
      }
      if (params.companyName == "") {
        params.companyName = null;
      }
      if (params.companyArg == "") {
        params.companyArg = null;
      }
      if (params.account == "") {
        params.account = null;
      }
      if (params.registerTimeEnd == "") {
        params.registerTimeEnd = null;
      }
      if (params.registerTimeStart == "") {
        params.registerTimeStart = null;
      }
      params.province = this.cityList.province;
      params.city = this.cityList.city;
      if (params.city == undefined) {
        params.city = null;
      }
      if (params.province == undefined) {
        params.province = null;
      }
    },
    //到处风格
    import_style(text) {
      //console.log(text);
      this.visible = true;
      this.main_taxt = text;
    },
    hideModal() {
      var data = {
        companyId: this.main_taxt.companyId,
        account: this.company_num,
      };
      this.table_spinning = true;
      this.$http
        .post(
          "http://cloudservice.71baomu.com/ccs/api/ccs/active/customer/sync/style",
          this.qs.stringify(data)
        )
        .then((res) => {
          //console.log(res);
          if (res.data.code == 0) {
            this.$message.success("导出成功！");
            this.table_spinning = false;
            this.visible = false;
          } else {
            this.$message.error(res.data.msg);
            this.table_spinning = false;
            this.visible = false;
          }
          this.company_num = "";
