    //获取行业数据
    refushIndustry() {
      getData.getIndustry({}).then((res) => {
        this.industryList = res.data;
        this.industriesList = this.industryList[0].ccsIndustryLevelTwoList;
        this.industryActive(this.industrychoosefirst);
        this.industriesActive(this.industrychoosesecond);
      });
    },
    //设置后刷新显示字段
    refushField() {
      var params = { userId: this.Id };
      getData.getField(params).then((res) => {
        this.fieldchooseUpList = res.data;
        this.unchooseListdraw();
        for (var i = 0; i < this.fieldchooseList.length; i++) {
          for (var j = 0; j < 18; j++) {
            if (j == this.fieldchooseList[i]) {
              this.columns[j + 1].show = true;
            }
          }
        }
        for (let item of this.columns) {
          if (true == item.show) {
            this.columnsChange.push(item);
          }
        }
        this.columns = this.columnsChange;
      });
