    // 企业画像筛选获取活跃用户列表
    activeUserListbyporttaits(params) {
      this.table_spinning = true;
      getData
        .activeUserListbyportrait(params)
        .then((res) => {
          //console.log(res);
          this.table_spinning = false;
          if (res.code == "200") {
            this.data = res.data.list;
            if (res.data.list) {
              this.pagination.total = res.data.total;
            }
            for (var i = 0; i < this.data.length; i++) {
              this.data[i].num =
                i +
                1 +
                (this.pagination.current - 1) * this.pagination.pageSize;
            }
            if (res.data.fkNum) {
              this.activeUser_text = res.data.fkNum;
            } else {
              this.activeUser_text = {};
            }
          } else if (res.code == "900") {
            this.data = [];
            this.pagination.total = 0;
            this.$message.error("获取数据失败");
          } else {
            this.$message.error(res.data);
          }
        })
        .catch((err) => {
          this.$message.error("系统错误");
        });
    },
    // 获取活跃用户列表
    activeUserList(params) {
      this.table_spinning = true;
      getData
        .activeUserList(params)
        .then((res) => {
          this.table_spinning = false;
          if (res.code == "200") {
            this.activeUser_text = res.data.fkNum;
            this.data = res.data.list;
            this.sumseats = res.data.sumSeats;
            if (res.data.list.length == 0 && this.pagination.current !== 1) {
              let paramsdata = JSON.parse(JSON.stringify(params));
              this.pagination.current = this.pagination.current - 1;
              paramsdata.pageNum = paramsdata.pageNum - 1;
              this.activeUserList(paramsdata);
            }
            if (res.data.list) {
              this.pagination.total = res.data.total;
            }
            for (var i = 0; i < this.data.length; i++) {
              this.data[i].num =
                i +
                1 +
                (this.pagination.current - 1) * this.pagination.pageSize;
            }
            if (this.searchList.companyId == "") {
              return;
            }
            if (this.fifter == "companyId") {
              this.data.map((item) => {
                //遍历
                item.companyId = this.brightKeyword(item.companyId);
              }); //到这里search方法结束
            } else if (this.fifter == "companyName") {
              this.data.map((item) => {
                //遍历
                item.companyName = this.brightKeyword(item.companyName);
              }); //到这里search方法结束
            } else if (this.fifter == "account") {
              this.data.map((item) => {
                //遍历
                item.account = this.brightKeyword(item.account);
              }); //到这里search方法结束
            } else if (this.fifter == "companyArg") {
              this.data.map((item) => {
                //遍历
                item.companyArg = this.brightKeyword(item.companyArg);
              }); //到这里search方法结束
            }
          } else if (res.code == "900") {
            this.data = [];
            this.pagination.total = 0;
            this.$message.error("获取数据失败");
          } else {
            this.$message.error(res.data);
          }
        })
        .catch((err) => {
          this.$message.error("系统错误");
        });
    },
    getsearch(params) {
      if (this.searchList.quxiao == 1) {
        params.lastYearTotalB = 0;
        params.lastYearTotalE = 500;
      } else if (this.searchList.quxiao == 2) {
        params.lastYearTotalB = 500;
        params.lastYearTotalE = 1000;
      } else if (this.searchList.quxiao == 3) {
        params.lastYearTotalB = 1000;
        params.lastYearTotalE = 2000;
      } else if (this.searchList.quxiao == 4) {
        params.lastYearTotalB = 2000;
      }

      if (this.searchList.jinxiao == 1) {
        params.yearTotalB = 0;
        params.yearTotalE = 500;
      }
      if (this.searchList.jinxiao == 2) {
        params.yearTotalB = 500;
        params.yearTotalE = 1000;
      }
      if (this.searchList.jinxiao == 3) {
        params.yearTotalB = 1000;
        params.yearTotalE = 2000;
      }
      if (this.searchList.jinxiao == 4) {
        params.yearTotalB = 2000;
      }
    },
    searchmoney() {
      var params = {};
      params.pageNum = 1;
      params.pageSize = this.pagination.pageSize;

      params.realActive = this.searchList.renewal;
      params.firstIndustry = this.searchList.firstIndustry;
      params.industry = this.searchList.industry;
      params.productCnt = this.searchList.productCnt;
      params.industryList = [];
      if (this.searchList.firstIndustry) {
        if (this.searchList.industry) {
          this.industriessecondList.forEach((item) => {
            if (item.id == this.searchList.industry) {
              params.industryList.push(item.id);
            }
          });
        } else {
          for (let item of this.industriessecondList) {
            params.industryList.push(item.id);
          }
        }
      }
      this.getsearch(params);
      this.activeUserList(params);
    },
    brightKeyword(val) {
      let keyword = this.replace_null(this.searchList.companyId); //获取输入框输入的内容
      if (val.indexOf(keyword) !== -1) {
        //判断这个字段中是否包含keyword
        //如果包含的话，就把这个字段中的那一部分进行替换成html字符
        return val.replace(keyword, `<font color='#42cccc'>${keyword}</font>`);
      } else {
        return val;
      }
    },
    managerChange(v) {},
    serviceChange(v) {},
    channelChange(v) {},
    certificationChange(v) {},
    accountTypeChange(v) {},
    accountType(v) {},
    accountBeforeChange(v) {
      var v = this.searchList.seatsAmountStart;
      if (v == undefined || v == "" || v == null) {
        this.searchList.seatsAmountEnd = "";
        this.accountBType = true;
