    // 注册时间onchange
    timeChange(date, dateString) {
      if (dateString[0] == "") {
        this.searchList.registerTimeStart = "";
      } else {
        this.searchList.registerTimeStart = dateString[0] + "00:00:00";
      }
      if (dateString[1] == "") {
        this.searchList.registerTimeEnd = "";
      } else {
        this.searchList.registerTimeEnd = dateString[1] + " 23:59:59";
      }
