    // 地区省份选择onchange
    provenceChange(v) {
      if (v == undefined) {
        this.cityList.city = undefined;
        this.cityType = true;
      }
      for (let item of this.cities) {
        if (v == item.value) {
          this.cityList.city = undefined;
          this.chooseCities = item.children;
          if (!item.children) {
            this.cityType = true;
          } else {
            this.cityType = false;
          }
        }
      }
    },
    cityChange(v) {},

