    // 点击编辑行业选中已指定的行业
    toEditor(text, record, index) {
      //获取指定行业列表
      this.editindustry_title = "编辑行业";
      this.activeId = record.id;
      this.currcompanyId = record.companyId;
      this.workingId = record.id;
      this.currentSort = 0;
      this.secondcurrentSort = 0;
      var params = { industryName: record.industry };
      getData.getSpecifiedIndustry(params).then((res) => {
        if (res.data == null) {
          this.currentSort = 0;
          this.industrychoosefirst = 0;
          this.industryActive(this.industrychoosefirst);
          this.secondcurrentSort = undefined;
          this.industrychoosesecond = undefined;
        } else {
          if (res.data.industryName == " " || res.data.industryName == "") {
            // for (var j = 0; j < this.industryList.length; j++) {
            //   if (res.data.id == this.industryList[j].ccsIndustry.id) {
            //     this.currentSort = j;
            //     this.industrychoosefirst = j;
            //     this.industryActive(this.industrychoosefirst);
            //   }
            // }
            this.currentSort = 0;
            this.industrychoosefirst = 0;
            this.industryActive(this.industrychoosefirst);
            this.secondcurrentSort = undefined;
            this.industrychoosesecond = undefined;
          } else {
            for (var j = 0; j < this.industryList.length; j++) {
              if (
                res.data.industryParent == this.industryList[j].ccsIndustry.id
              ) {
                this.currentSort = j;
                this.industrychoosefirst = j;
                this.industryActive(this.industrychoosefirst);
              }
            }
            for (var i = 0; i < this.industriesList.length; i++) {
              if (
                res.data.industryName == this.industriesList[i].industryName
              ) {
                this.industrychoosesecond = i;
                this.industriesActive(this.industrychoosesecond);
              }
            }
          }
        }
      });
      this.editIndustryvisible = true;
    },
    // 编辑行业保存
    editIndustryhandleOk(e) {
      var a = this.industryList[this.currentSort].ccsIndustryLevelTwoList;
      var b = "";
      if (
        this.secondcurrentSort == null ||
        this.secondcurrentSort == undefined
      ) {
        // b = this.industryList[this.currentSort].ccsIndustry.industryName;
        this.$message.error("请选择二级行业");
        return;
      } else {
        b = a[this.secondcurrentSort].industryName;
      }
      let params = {};
      if (this.editindustry_title == "批量设置行业") {
        params = {
          ids: this.selectedRowKeys,
          industry: b,
          industryTag: 1,
        };
        getData
          .batcheditIndustry(params)
          .then((res) => {
            if (res.code == 200) {
              this.$message.success("批量设置行业成功！");
              let params = JSON.parse(JSON.stringify(this.searchList));
              this.pagination.current = 1;
              params.pageNum = 1;
              this.paramsCheck(params);
              this.activeUserList(params);
              this.selectedRowKeys = [];
            } else {
              this.$message.errror(res.message);
            }
          })
          .catch((errr) => {
            //console.log(err);
          });
      } else {
        params = {
          id: this.activeId,
          industry: b,
          companyId: this.currcompanyId,
          industryTag: 1,
        };
        getData.editIndustry(params).then((res) => {
          let params = JSON.parse(JSON.stringify(this.searchList));
          params.pageNum = this.pagination.current;
          this.paramsCheck(params);
          this.activeUserList(params);
        });
      }
      this.editIndustryCancel();
    },
