        companyId: this.activeId,
      };
      getData
        .getcertificationInfo(params)
        .then((res) => {
          if (res.code == 200) {
            this.authenticationInformationList = res.data;
            this.rightAside = true;
            this.asidecurrentSort = a;
          } else if (res.code == 900) {
            // this.$message.error("获取企业认证信息失败！");
            this.authenticationInformationList = {};
          } else {
            this.$message.error(res.data);
          }
        })
        .catch((err) => {
          this.$message.error("系统错误");
        });
      var params = {
        companyId: this.activeId,
        pageNum: 1,
        pageSize: 20,
      };
      getData
        .getworkInfo(params)
        .then((res) => {
          if (res.code == 200) {
            this.workingInformationList = res.data;
            for (var i = 0; i < this.workingInformationList.list.length; i++) {
              this.workingInformationList.list[i].num = i + 1;
            }
            this.rightAside = true;
            this.asidecurrentSort = a;
          } else if (res.code == 900) {
            this.$message.error("获取企业工号信息失败！");
          } else {
            this.$message.error(res.data);
          }
        })
        .catch((err) => {
          this.$message.error("系统错误");
        });
