        .then((res) => {
          if (res.code == 200) {
            this.portrainformation = res.data;
            this.rightAside = true;
            this.asidecurrentSort = a;
          } else if (res.code == 900) {
            this.$message.error("获取企业画像失败！");
            this.portrainformation = {};
          } else {
            this.$message.error(res.data);
          }
        })
        .catch((err) => {
          this.$message.error("系统错误");
        });

      getData.packagesList(data).then((res) => {
        //console.log(res);
        if (res.code == 200) {
          this.packagesList = res.data;
        } else {
          this.packagesList = [];
        }
      });
      if (a == 2) {
        this.bgcolor = "rgba(247,248,250,1)";
      } else {
        this.bgcolor = "#fff";
      }
    },
    getgetinterfaceList() {
      let data = {
        companyId: this.sider_data.companyId,
        // companyId: "72000813",
      };
      getData.getinterface(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          this.shouquanList = [];
          res.data.forEach((item, index) => {
            console.log(item);
            this.shouquanList.push({ num: index + 1, name: item, isoff: 1 });
          });
          // this.shouquanList = res.data;
        } else {
          this.shouquanList = [];
        }
      });
    },
    getgetwhitelistList() {
      let data = {
        companyId: this.sider_data.companyId,
      };
      getData.getwhitelist(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          this.ipList = res.data;
        } else {
          this.ipList = [];
        }
      });
    },
    // 切换右边弹窗分页
    asideActive(index) {
      // this.rightAside = true;
      this.asidecurrentSort = index;
      if (index == 2) {
        this.bgcolor = "rgba(247,248,250,1)";
      } else if (index == 5) {
        console.log(this.sider_data, "5555555555555555");
        this.getgetinterfaceList();
        this.getgetwhitelistList();
      } else {
        this.bgcolor = "#fff";
      }
    },
    //取消授权
    calcashou(text) {
      console.log(text);
      var data = {
        companyId: "72000813",
        jk: text.name,
      };
      getData.authorized(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          text.isoff = 0;
          this.$message.success("取消授权成功");
          this.getgetinterfaceList();
        } else {
          this.$message.error("取消授权失败");
        }
      });
    },
    //添加IP
    addipcommit() {
      var data = {
        companyId: this.sider_data.companyId,
        ipSegment: this.add_ip_name,
      };
      getData.getaddwhitelist(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          this.addipmodal = false;
          this.$message.success("添加IP成功");
          this.getgetwhitelistList();
        } else {
          this.$message.error("添加IP失败");
        }
      });
    },
    //修改IP
    editipcommit() {
      var data = {
        id: this.edit_id,
        ipSegment: this.edit_ip_name,
      };
      getData.getmodwhitelist(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          this.editipmodal = false;
          this.$message.success("修改IP成功");
          this.getgetwhitelistList();
        } else {
          this.$message.error("修改IP失败");
        }
      });
    },
    //删除IP
    delipcommit() {
      var data = {
        id: this.edit_id,
      };
      getData.getdelwhitelist(data).then((res) => {
        console.log(res);
        if (res.code == 200) {
          this.delipmodal = false;
          this.$message.success("删除IP成功");
          this.getgetwhitelistList();
        } else {
          this.$message.error("删除IP失败");
        }
      });
    },
    // 隐藏右边分页弹窗
    rightAsideHide() {
      document.getElementById("right_aside").scrollTop = 0;
      this.rightAside = false;
      this.asidecurrentSort = 0;
    },
  },
};
</script>
<style scoped="" lang="scss">
/deep/ .ant-select-dropdown-menu {
  // max-width: 102px !important;
}
.active_customer_main {
  display: flex;
  position: relative;
}
.active_customer_filter {
  width: 270px;
  height: calc(100vh - 50px);
  background: rgba(247, 248, 250, 1);
  box-shadow: -1px 0px 0px 0px rgba(206, 213, 224, 1);
}
.active_customer_com_msg {
  width: calc(100% - 270px);
}
.active_customer_com_msg_all {
  width: calc(100% - 20px);
}
.ant-tabs-nav .ant-tabs-tab {
  font-size: 12px;
}
.change_filter_box {
  width: 20px;
  height: 100%;
}
.icon_img {
  width: 20px;
  height: 100px;
  padding: 40px 0;
  position: relative;
  top: 43%; /*偏移*/
  transform: translateY(-50%);
  border-top: 2px solid rgba(247, 248, 250, 1);
  border-right: 2px solid rgba(247, 248, 250, 1);
  border-bottom: 2px solid rgba(247, 248, 250, 1);
  border-radius: 8%;
  cursor: pointer;
}
.icon_img:hover {
  box-shadow: 1px 0px 2px rgba(33, 139, 252, 1);
}
.filter_buttom {
  position: absolute;
  bottom: 0;
  width: 270px;
  height: 44px;
  background: rgba(247, 248, 250, 1);
  border-top: solid 1px rgba(206, 213, 224, 1);
  line-height: 44px;
}
.filter_buttom button:nth-child(1) {
  margin-left: 74px;
}
.company_msg_filter {
  width: 230px;
  /* height:600px; */
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(206, 213, 224, 1);
  margin: 0 15px;
  padding: 10px 5px;
}
.filter_item:not(:last-child) {
  margin-bottom: 15px;
}
.filter_item:last-child {
  margin-bottom: 10px;
}
.item_name {
  color: rgba(40, 51, 75, 1);
  line-height: 18px;
  font-size: 14px;
  margin-bottom: 6px;
}
.company_msg_filter .ant-tabs-tab:first {
  margin-left: 20px;
}
.sumseatsbox {
  position: absolute;
  bottom: 37px;
}
.view_key {
  float: left;
  font-size: 13px;
}
.view_value {
  float: left;
  color: rgb(244, 64, 36);
}
.main_switch.is_off {
  background: #99abb6;
  border-color: #83929c;
}
.main_switch {
  background: #52a362;
  border: 1px solid #52a362;
  border-radius: 2px;
  width: 56px;
  height: 26px;
  position: relative;
  display: inline-block;
  transition: 0.2s;
  cursor: pointer;
}
.main_switch:before {
  content: "开";
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  color: #ffffff;
  line-height: 24px;
  text-align: center;
}
.main_switch b {
  position: absolute;
  left: 32px;
  top: 2px;
  background: #ffffff;
  border-radius: 2px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: 0.2s;
}
.main_switch:after {
  content: "关";
  position: absolute;
  right: 0;
  top: 0;
  width: 50%;
  height: 100%;
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  color: #ffffff;
  line-height: 24px;
  text-align: center;
}
.main_switch.is_off b {
  left: 4px;
}
</style>



